<template>
    <div class="relative p-3 bg-white dark:bg-gray-700 dark:text-dark-color focus:outline-none focus:ring-0">
        <div
            class="w-full h-24 mb-4 rounded shadow-gradient-preview"
            :class="{ 'bg-white dark:bg-gray-600 dark:text-dark-color': !positionsValidSorted.length }"
            :style="preview('')"
        />

        <div class="relative mb-4" style="z-index:11">
            <div class="relative" @click="onAddNewColor"  data-tooltip="Click to add color stop">
                <div
                    class="relative h-5 rounded-full range shadow-range"
                    :class="[
                        { 'bg-gray-50 dark:bg-gray-600': !positionsValidSorted.length },
                        hasAdd ? 'cursor-crosshair' : 'cursor-default'
                    ]"
                    :style="preview()"
                >
                    <div ref="range" class="relative mx-2.5">
                        <div
                            v-for="pos in value.positions"
                            :key="pos.index"
                            :style="{ left: pos.index + '%' }"
                            class="
                                item
                                absolute
                                top-0.5
                                transform
                                -translate-x-1/2
                                block
                                w-4
                                h-4
                                border-2 border-white
                                bg-transparent
                                flex
                                items-center
                                justify-center
                                rounded-full
                            "
                            :class="[
                                selected == pos
                                    ? (isMove ? 'cursor-grabbing ' : 'cursor-pointer ') +
                                      'ring-2 ring-primary-500 z-1'
                                    : 'cursor-pointer',
                                { 'bg-empty': !pos.color }
                            ]"
                            data-tooltip="Click to select color"
                            @mousedown="mouseDownItem(pos, $event)"
                        >
                            <span
                                v-if="selected == pos"
                                class="
                                    absolute
                                    -bottom-3
                                    left-1/2
                                    transform
                                    -translate-x-1/2
                                    p-0.5
                                    rounded-full
                                    bg-primary-500
                                "
                            /><template v-else />
                        </div>
                    </div>
                </div>
            </div>
            <ColorPicker
                v-if="selected && showPicker"
                v-clickOutside="closePopup"
                class="absolute z-10 w-full color-picker top-8"
                :pos-arrow="posArrow"
                :has-delete="true"
                :value="color(selected?.color)"
                :global="{ type: 'colors' }"
                :global-selected="selected?.color?.['global-colors']"
                @refeshColor="updateColor('')"
                @update:value="updateColor($event)"
                @update:global-selected="updateColor({ 'global-colors': $event })"
                @update:delete="deleteColor"
            /><template v-else />
        </div>
        <div class="flex items-center justify-between mb-4">
            <span class="text-xs font-semibold text-form-title dark:text-dark-color">COLOR STOP</span>
            <span class="flex items-center">
                <ecom-icon :icon="icons.faArrowsRotate"
                    class="w-5 h-5 text-gray-400 transition"
                    :class="
                        value.positions?.length
                            ? 'text-primary-600 hover:text-primary-700 cursor-pointer'
                            : 'text-gray-400'
                    "
                    @click="hasRefresh && (value.positions = [])"
                />
            </span>
        </div>
        <Fields
            v-if="value?.positions?.length"
            :data="value"
            :fields="fields"
            :freeform="true"
        /><template v-else />
        <div
            class="flex justify-center my-2"
        >
            <a
                href="https://help.ecomposer.io/docs/faqs/how-to-create-gradient-backgrounds-for-buttons-or-section/"
                class="flex items-center text-primary-600 text-sm hover:underline"
                target="_blank"
            >
                <span>See guide</span>
                <ecom-icon :icon="icons.faQuestionSquare" class="h-4 w-4 ml-1" />
            </a>
        </div>
    </div>
</template>

<script lang="ts">
import {faArrowsRotate, faQuestionSquare} from '@fortawesome/pro-light-svg-icons';
import ColorPicker from "../../components/molecules/ColorPicker/ColorPanel.vue";

import { convertGradient } from "@/components/Builder/Helpers/Css";
import { clickOutside } from "../../directives";


export default {
    name: "Gradient",
    components: {
        ColorPicker,
    },
    directives: {
        clickOutside
    },
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    emits: ["update:data"],
    data() {
        return {
            icons: {
                faArrowsRotate, faQuestionSquare
            },
            selected: null,
            value: {
                positions: [{ index: 0, color: "" }]
            },
            inputRange: null,
            isMove: false,
            x: 0,
            max: 10,
            showPicker: false
        };
    },
    computed: {
        fields() {
            return [
                {
                    name: "type",
                    label: "Type",
                    type: "popup",
                    value: "linear",
                    options: {
                        type: "dropdown",
                        default: false,
                        values: {
                            linear: "Linear",
                            radial: "Radial"
                        },
                        oneline: true
                    }
                },
                {
                    name: "angle",
                    label: "Angle",
                    type: "number",
                    value: 180,
                    options: {
                        min: 0,
                        max: 360,
                        visible: {
                            keep_data: true,
                            condition: (data) => {
                                return data["type"] == "linear";
                            }
                        }
                    }
                },
                {
                    name: "position",
                    label: "Position",
                    type: "popup",
                    value: "center center",
                    options: {
                        type: "dropdown",
                        default: false,
                        values: {
                            "center center": "Center center",
                            "center right": "Center right",
                            "center left": "Center left",
                            "top left": "Top left",
                            "top center": "Top center",
                            "top right": "Top right",
                            "bottom right": "Bottom right",
                            "bottom left": "Bottom left",
                            "bottom center": "Bottom center"
                        },
                        oneline: true,
                        visible: {
                            keep_data: true,
                            condition: (data) => {
                                return data["type"] == "radial";
                            }
                        }
                    }
                }
            ];
        },
        positionsSorted() {
            let poss = this.$helpers.copy(this.value.positions);
            poss.sort((a, b) => a.index - b.index);
            return poss;
        },
        positionsValidSorted() {
            let poss = this.$helpers.copy(this.positionsSorted);
            return poss.filter((pos) => pos.color);
        },
        hasRefresh() {
            return this.positionsSorted.length;
        },
        hasAdd() {
            return this.positionsSorted.length < 10;
        },
        output() {
            if (this.positionsValidSorted?.length > 0) {
                // eslint-disable-next-line no-unused-vars
                let { positions, ...agrs } = this.value;
                return {
                    positions: this.positionsValidSorted,
                    ...agrs
                };
            }
            return {};
        },
        globalColors() {
            return this.$store.getters["settings/globalDatas"];
        },
        posArrow() {
            if (this.selected) {
                if(!this.inputRange)
                {
                    this.inputRange = this.$refs["range"];
                }
                if(!this.inputRange) {
                    return '0px';
                }
                let w = this.inputRange.offsetWidth;
                return this.intBetween((this.selected.index * w) / 100 + 10, 10, w + 10) + "px";
            }
            return "";
        }
    },
    watch: {
        data: {
            deep: true,
            immediate: true,
            handler(val) {
                if (val && JSON.stringify(val) !== JSON.stringify(this.output)) {
                    this.value = this.validValue(val);
                    this.selected = this.value.positions?.[0];
                } else if (!this.selected) {
                    this.selected = this.value.positions?.[0];
                }
            }
        },
        output: {
            deep: true,
            handler(val) {
                this.$emit("update:data", val);
            }
        }
    },
    mounted() {
        this.inputRange = this.$refs["range"];
        window.EComposerBuilder.contentDocument.onclick = () => {
            this.showPicker = false;
        };
    },
    methods: {
        preview(type = "linear") {
            let output = this.$helpers.copy(this.output);
            if (type === "linear") {
                output.type = "linear";
                output.angle = 90;
            }
            return convertGradient(output);
        },
        color(v) {
            if (typeof v === "string") {
                return v;
            } else if (v?.["global-colors"]) {
                return this.globalColors?.colors?.[v["global-colors"]]?.value ?? "";
            }
            return "";
        },
        validValue(value) {
            let v = { positions: [] };
            ["first", "second"].forEach((t) => {
                if (value[t + "-color"]) {
                    v.positions.push({
                        index: value[t + "-location"] ?? 0,
                        color: value[t + "-color"]
                    });
                }
            });
            ["type", "angle", "position", "positions"].forEach((t) => {
                if (value[t]) {
                    v[t] = value[t];
                }
            });
            return v;
        },
        intBetween(num, min, max) {
            if (num === undefined) {
                return num;
            }
            return Math.max(min, Math.min(max, num));
        },
        updateColor(color) {
            if (this.selected) {
                this.selected.color = color;
            } else {
                let newItem = {
                    index: 0,
                    color
                };
                this.selected = newItem;
                this.value.positions.push(newItem);
            }
        },
        onAddNewColor(e) {
            // console.log('e',e);
            if (e.target.closest(".item:not(.ecom-group-item)")) {
                return;
            }

            let { x } = e,
                target = e.target,
                targetRect = target.getBoundingClientRect(),
                targetWidth = target.offsetWidth,
                percent = Math.round(((x - targetRect.x) / targetWidth) * 100);
            this.addNewColor(percent);
        },
        addNewColor(index, color = "") {
            if (!this.hasAdd) {
                return;
            }
            if (this.value.positions.length < 10) {
                let newItem = {
                    index,
                    color
                };
                this.selected = newItem;
                this.value.positions = this.value.positions.filter(pos => pos.color != '');
                this.value.positions.push(newItem);
                this.showPicker = true;
            }
        },
        mouseDownItem(pos, e) {
            this.isMove = false;
            this.selected = pos;
            this.x = e.x;
            document.onmousemove = this.mouseMove;
            document.onmouseup = this.mouseUp;
        },
        mouseMove(e) {
            if (Math.abs(this.x - e.x) > 3) {
                this.showPicker = false;
                this.isMove = true;
                let { x } = e;
                let rangeWidth = this.inputRange.offsetWidth,
                    rangeRect = this.inputRange.getBoundingClientRect();
                let per = Math.round(((x - rangeRect.x) / rangeWidth) * 100);
                if (this.selected) {
                    this.selected.index = this.intBetween(per, 0, 100);
                }
            }
        },
        mouseUp() {
            if (!this.isMove) {
                this.showPicker = true;
            }
            document.onmousemove = null;
            document.onmouseup = null;
            this.isMove = false;
            (this.x = null), (this.y = null);
        },
        deleteColor() {
            let i = this.value.positions.findIndex((pos) => pos === this.selected);
            this.value.positions.splice(i, 1);
            this.showPicker = false;
        },
        closePopup(e) {
            if (!e.target.closest(".item") && !e.target.closest(".range")) {
                this.showPicker = false;
            }
        }
    }
};
</script>

<style>
.bg-size-2 {
    background-size: 8px;
}
.bg-size-3 {
    background-size: 12px;
}
.bg-size-5 {
    background-size: 20px;
}
.shadow-range {
    box-shadow: inset 0 0 0 1px rgb(0 0 0 / 7%), inset 0 1px 3px 0 rgb(0 0 0 / 15%);
}
.shadow-gradient-preview {
    box-shadow: inset 0 0 0 1px rgb(0 0 0 / 20%);
}
.bg-empty {
    background: linear-gradient(
        to top left,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0) calc(50% - 0.8px),
        red 50%,
        rgba(0, 0, 0, 0) calc(50% + 0.8px),
        rgba(0, 0, 0, 0) 100%
    );
}
</style>