                    return [];
                } else if (this.type == 1){
                    toolbarTemp = toolbarTemp.filter(
                        x => x && ['strikeThrough', 'insertImage', 'insertVideo'].indexOf(x?.action) === -1
                    );
                }
                return toolbarTemp;
            },
            CMDs(){
                return this.toolbarFilterd.map(x => x.action);
            },
        },
        methods: {
            styleButton(cmd){
                let val = this.state[cmd];
                switch(cmd){
                    case 'foreColor':
                    case 'codeFormat':
                        return {'--ta-fill' : val};
                    case 'backColor':{
                        let c = '';
                        if(!val) {
                            c = 'rgb(0, 0, 0)';
                        }
                        else{
                            c = (val == 'rgb(255, 255, 255)') ?
                                'rgb(0, 0, 0)' : val;
                        }
                        return {'--ta-fill' : c};
                    }
                    default:
                        return {};
                }
            },
            classToolbar(cmd){
                let val = this.state[cmd];
                switch(cmd){
                    case 'foreColor':
                    case 'backColor':
                        return 'hover:bg-gray-200 rounded dark:hover:bg-dark dark:hover:text-primary-600';
                    default:
                        return val ?
                                'text-primary-600 bg-gray-100' :
                                'text-gray-500 dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-600 hover:bg-gray-200';
                }
            },
            clickToolbar($event,action){
                this.showing = action === this.showing ? null: action;
                this.$emit('select',action)
            },
            changeColor(val, action){
                this.colors[action] = val;
                let itv = null;
                clearTimeout(itv);

                itv = setTimeout(() => {
                    this.$emit('updateColor',{val, action})
                }, 100)
            },
        }
    };
</script>
<style>
    .flex-break{
        flex-basis: 100%;
        max-height: 0;
        overflow: hidden;
    }
</style>