            {{ valueFormat }}
        </button>
        <transition name="slide-up-tiny">
            <div v-show="isopen" class="absolute z-10 w-full mt-2 transition duration-300">
                <DatePicker
                    :field="field"
                    :data="value"
                />
            </div>
        </transition>
  </div>
</template>

<script lang="ts">
    import { Popmenu } from '@/mixins';

    import { EComDate } from '../../utils';
    import DatePicker from './Picker.vue';
    import mixin from './mixin';
    export default {
        name: 'DateField',
        components: {
            DatePicker
        },
        mixins: [ Popmenu, mixin ],
        props: {
            field: {
                type: Object,
                default() {
                    return {};
                }
            },
            // eslint-disable-next-line vue/require-default-prop
            data: [Object, String],
        },
        emits: ['update:data'],
        data() {
            return {
                isWatch: true,
                isEmit: false,
                value: null
            };
        },
        computed: {
            months() {
                return EComDate.names.month;
            },

            valueFormat() {
                this.validDate();
                return this.getValueFormat(this.value) ;
            }
        },
        watch: {
            data: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.value = this.exportData(val);
                    }
                    this.isWatch = true;
                }
            },
            valueFormat: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            },
            range: {
                deep: true,
                handler() {
                    this.validDate();
                }
            }
        },
        methods: {
            getTimeZone() {
                var offset = new Date().getTimezoneOffset(), o = Math.abs(offset);
                return (offset < 0 ? '+' : '-') + ('00' + Math.floor(o / 60)).slice(-2) + ('00' + (o % 60)).slice(-2);
            },
            exportData(data) {
                let rs = {};
                let date = new Date(data);
                if(date != 'Invalid Date') {
                    let properties = {
                        year: 'getFullYear',
                        month: 'getMonth',
                        day: 'getDate',
                        hour: 'getHours',
                        minute: 'getMinutes',
                        second: 'getSeconds',
                    };
                    for(let prop in properties) {
                        rs[prop] = date[properties[prop]]();
                        if(prop === 'month') {
                            rs[prop]++;
                        }
                    }
                    rs['gmt'] = this.getTimeZone();
                }
                return rs;
            },
            validDate() {
                let currentTime = new Date(this.getValueFormat(this.value));

                if(currentTime instanceof Date && !isNaN(currentTime)) {
                    if(currentTime < this.minDate) {
                        this.isEmit = true;
                        this.value = this.exportData(this.minDate);
                    }
                    else if(currentTime > this.maxDate) {
                        this.isEmit = true;
                        this.value = this.exportData(this.maxDate);
                    }
                }
            }
        }
    };
</script>

<style>
    .slide-up-tiny-enter-from,
    .slide-up-tiny-leave-to {
        transform: translateY(10px);
        opacity: 0;
    }
</style>
