<template>
    <div class="relative flex items-center divide-x divide-gray-300 border-form-border rounded border">
        <input
            v-model="values['href']"
            type="text"
            placeholder="https://your-link.com"
            class="block w-full pr-10 truncate transition border-none rounded-l shadow-sm text-form-input dark:text-dark-color  dark:bg-dark placeholder-form-input sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
            :class="field.options?.oneline ? 'h-7-5' : 'h-9'"
        >
        <ecom-icon :icon="icons.faGear"
            class="p-2 text-gray-500 dark:text-dark-color transition cursor-pointer w-4 hover:text-gray-600 dark:hover:text-primary-600"
            :class="field.options?.oneline ? 'h-7-5' : 'h-4'"
            @click="showInsertLink"
        />
        <InsertLink
            ref="insert-link"
            :data="values"
            :position="field.options?.position || 'absolute'"
            type="field"
            @update:link="updateLink"
        />
    </div>
</template>

<script lang="ts">
    import InsertLink from '../../components/editor/InsertLink/InsertLink.vue';
    import {faGear} from '@fortawesome/pro-light-svg-icons';
     export default {
        name: 'LinkField',
        components: {
            InsertLink
        },
        props: {
            data: {
                type: Object,
                default(){
                    return {};
                }
            },
            field: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data(){
            return {
                values: {},
                isWatch: true,
                icons: {faGear},
                isEmit: false
            };
        },
        watch: {
            data: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.values = this.$helpers.copy(val);
                    }
                    this.isWatch = true;
                }
            },
            values: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }
        },
        methods: {
            showInsertLink(){
                let $inserLink = this.$refs['insert-link'];
                if($inserLink){
                    $inserLink.show = true;
                }
            },
            updateLink(values){
                this.values = values;
            }
        }
    };
</script>
