<template>
    <PickerFrame
    :show="show"
    :header="headerFrame"
    :position="position"
    @close="show = false"
  >
    <template #content>
      <Loading
          v-if="loading"
          type="spin"
          :size="8"
          class="text-gray-800 dark:text-dark-color transition mt-10"
      />
      <div v-else>
        <div class="p-2 flex gap-2 flex-col">
            <label class="text-sm text-form-title dark:text-dark-color font-semibold">
                Describe your request:
            </label>
            <textarea
                v-model="content"
                placeholder="Clearly describe your content ideas or requests, our AI will generate the suggested result."
                class="block w-full transition dark:text-dark-color dark:border-gray-600 dark:bg-dark rounded shadow-sm text-form-input border-form-border placeholder-form-input h-9 sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300 min-h-40"
            />
            <Button
                text="Generate"
                type="primary"
                @click="generate"
            />
            <template v-if="result">
                <hr class="h-0 border-b border-gray-300 dark:border-gray-600" /> 
                <label class="text-sm font-semibold text-form-title dark:text-dark-color">
                    The result
                </label>
                <textarea
                    v-model="result"
                    class="block w-full transition dark:text-dark-color dark:border-gray-600 dark:bg-dark rounded shadow-sm text-form-input border-form-border placeholder-form-input h-9 sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300 min-h-48"
                />
            </template>
            <p v-if=" count >=0 " class="text-sm">
                <span class="text-gray-500">Used: </span>
                <span class="text-gray-700 dark:text-dark-color">{{ count }}/ 50 requests/ day</span>
            </p>
            <p class="w-full p-3 mb-3 text-xs font-light text-gray-600 border-l-4 border-yellow-500 shadow-sm bg-yellow-50">
                <ul class="list-disc pl-1">
                    <li>
                        Our AI uses Chat GPT to generate content.
                    </li>
                    <li>
                        This tool can be used in EComposer text element, to create content for your website, blog or social media 
                    </li>
                    <li>
                        We don't store or share any data with anyone and we're not responsible for the content generated by AI. Please use it as a recommended tool. 
                    </li>
                </ul>
                
                 
                
            </p>
            
        </div>
        
      </div>
  
    </template>
    <template #footer>
            <PickerFooter
              :is-change="!!result"
              @clickCancel="show = false"
              @clickSelect="eSelect"
              textSelect="Insert"
            />
          </template>
  </PickerFrame>
  </template>
  
  <script lang="ts">
    import PickerFrame from '../picker/PickerFrame.vue';
    import PickerFooter from '../picker/PickerFooter.vue';
    import { faCheck,faXmark} from '@fortawesome/pro-light-svg-icons';
    import { clickOutside } from '../../directives';
  
    export default {
        name: 'InsertLink',
        directives: {
            clickOutside
        },
        components: {
            PickerFrame,
            PickerFooter
  
        },
        props: {
            data: {
                type: Object,
                default(){
                    return  {};
                }
            },
            type: {
                type: String,
                default: 'editor'
            },
            position: {
                type: String,
                default: 'absolute'
            }
        },
        emits: ['close_popup','update:content'],
        data(){
            return {
                show: false,
                value: {},
                attributes: '',
                styleGroupTyles: {},
                icons: { faCheck,faXmark},
                loading: false,
                content:'',
                result: '',
                count: -1
            };
        },
        computed: {
          headerFrame(){
              return {
                  title: 'AI Content Generator',
                  search: {
                      show: false,
                      value: ''
                  }
              };
          }, 
        },
        watch: {
             
        },
        methods: {
            generate(){
                this.loading = true;
                this.$worker.fetch(window.route('builder.generator'), {
                    prompt: this.content
                }, 'POST').then((response) => {
                    this.loading = false;
                     if(response.data.status === 'success')
                     {
                        this.result = response.data.data.trim();
                        this.count = response.data.count;
                        // this.$emit('update:content', this.result);
                        // this.show = false;
                     }
                     else if(response.data.status === 'error')
                     {
                        throw new Error(response.data.message);
                        
                     }
                     else 
                        throw new Error('Invalid response');
                })
                .catch((error) => {
                    this.loading = false;
                    this.$store.dispatch('global/flash',{
                        type: 'error',
                        message:error.message,
                        open: true
                    });
                });
            },
            eSelect(){
                this.$emit('update:content', this.result);
               this.eClose();
            },
            eClose(){
                this.show = false;
                this.content = '';
                this.result = '';
            }
          
          
  
        }
  
    };
  </script>
  