<template>
    <div class="inline-block w-full">
        <template v-if="showToolbar">
            <Toolbar
                v-if="toolbar.show || alwayToolbar"
                ref="toolbar"
                :type="shortToolbar ? 1 : 2"
                :state="selection.state"
                :field="field"
                @select="eventAction($event)"
                @updateColor="updateColor($event)"
            />
            <ImportLink
                ref="insert-link"
                :data="ObjLink"
                @update:link="updateLink"
                @update:remove-link="removeFromMenu"
            />
            <DynamicSources
                v-if="dynamicSouce"
                ref="dynamic-sources"
                @update:metafield="updateMetafield"
            />
            <Generator 
                v-if="generator"
                ref="generator"
                @update:content="updateHTML"
            />
            
            <!-- <template !shortToolbar">
                <ImportVideo ref="insert-video" @update:video="updateHTML" />
                <ImportImage
                    ref="insert-image"
                    :data="objImage"
                    @update:image="updateHTML"
                    @edit:image="editImage"
                />
            </template><template v-else /> -->
        </template><template v-else />

        <div class="relative w-full">
            <iframe
                class="editor w-full text-sm transition transform -translate-y-px bg-white dark:bg-gray-100 dark:text-dark-color border shadow-sm ecom-textarea-focus text-form-input border-form-border placeholder-form-input editor h-9 focus:outline-none focus:ring-0"
                :class="[toolbar?.show ? 'rounded-b' : 'rounded']"
                :style="'min-height:' + minHeight + 'px'"
                :data-type="type"
                :srcdoc="srcdoc"
                ref="editor"
                @load="onload"
            />
            <div
                class="absolute top-0 rounded-b pointer-events-none bottom-px left-px right-px z-1"
            />
            <span
                v-if="field.placeholder && !html"
                class="absolute text-sm text-gray-500 dark:text-dark-color pointer-events-none top-2 left-3 right-3 truncate"
                v-html="field.placeholder"
            /><template v-else />
            <button
                v-if="showToolbar && !alwayToolbar"
                type="button"
                class="
                    absolute
                    transform
                    text-gray-600
                    cursor-pointer
                    transition
                    rounded-full
                    bg-gray-100
                    hover:bg-gray-200
                    dark:bg-dark
                    dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark
                    top-0.5
                    right-1
                    p-1.5
                    w-7
                    h-7
                    flex items-center justify-center
                "
                :class="[toolbar.show ? 'text-red-600' : '', isfocus ? ' hidden' : '']"
                :data-tooltip="(toolbar.show ? 'Hide' : 'Show') + ' toolbar'"
                @click="toolbar.show = !toolbar.show"
            >
                <ecom-icon class="h-3 w-3" :icon="icons[toolbar.show ? 'faChevronDown' : 'faPencil']" />
            </button><template v-else />
            <ModifyMenu
                v-if="showToolbar"
                ref="modify"
                :url="metadata.menu"
                :info="metadata.posMenu"
                :max-width="metadata.maxWidthMenu"
                @update:delete="removeFromMenu"
                @update:edit="editFromMenu"
            /><template v-else />
        </div>
        <div
            v-if="editSource"
            class="flex text-sm items-center justify-center mt-2 gap-2"
        >
            <button
                class="inline-flex items-center px-2 py-2 border border-transparent shadow-sm text-xs font-medium rounded-md text-white bg-gray-600 dark:bg-dark dark:border-transparent dark:hover:bg-gray-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500 group gap-1"
                @click="eventAction('dynamicSources')"
            >
                <ecom-icon class="h-3 w-3" :icon="icons.faArrowsRotate" />
                Change source
            </button>
            <button
                class="inline-flex items-center px-2 py-2 border border-transparent shadow-sm text-xs font-medium rounded-md bg-white dark:bg-dark dark:border-transparent dark:hover:bg-gray-600 dark:text-dark-color border-gray-300 focus:outline-none focus:ring-2 focus:ring-offset-2 group gap-1"
                @click="deleteSource"
            >
                <ecom-icon class="h-3 w-3" :icon="icons.faTrash" />
                Delete source
            </button>
        </div>
        <!-- <div
            v-if="wasPaste"
            class="inline-block w-full p-3 mt-2 text-sm text-gray-500 dark:text-dark-color whitespace-normal border-l-4 border-yellow-200 bg-yellow-50 i-full"
        >
            <span class="py-0.5 px-1.5 bg-gray-200 rounded-sm"> + shift + v</span> to paste text
            only
        </div>
        <template v-else /> -->
    </div>
  </template>

  <script lang="ts">
  import Toolbar from './Toolbar.vue';
  import ImportLink from './InsertLink/InsertLink.vue';
  import DynamicSources from './InsertDynamicSources/DynamicSources.vue';
  import Generator from './Generator.vue';
  import ModifyMenu from './Menu.vue';
  import { node2Obj } from '../../utils';
  import {faChevronDown,faPencil, faArrowsRotate, faTrash} from '@fortawesome/pro-light-svg-icons';
  import {textHelper} from '@/utils';

  export default {
    name: 'Editor',
    components: {
        Toolbar,
        ImportLink,
        ModifyMenu,
        DynamicSources,
        Generator
    },
    props: {
        data: {
            type: String,
            default: ''
        },
        type: {
            type: Number,
            default: 2
        },
        field: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    emits: ['update:data'],
    data() {
        return {
            icons: {faChevronDown,faPencil, faArrowsRotate, faTrash} ,
            value: '',
            html: '',
            editor: null,
            editorDoc: null,
            isfocus: false,
            editSource: false,
            itv: null,
            toolbar: {
                show: false
            },
            selection: {
                selection: undefined,
                action: undefined,
                range: undefined,
                state: {},
                image: {
                    node: undefined
                }
            },
            metadata: {
                images: [],
                menu: '',
                posMenu: {},
                maxWidthMenu: 0
            },
            wasPaste: false,
            srcdoc: `<html><head><meta http-equiv=Content-Type content="text/html; charset=UTF-8"><link rel="stylesheet" href="https://cdn.ecomposer.app/vendors/css/ecom-base.css"/><style>html{height:100%;}body{box-sizing: border-box;margin: 0;padding: 8px;height:100%;font-size:14px;line-height:20px;color:#111827;font-family:system-ui,-apple-system,'Segoe UI',Roboto,Helvetica,Arial,sans-serif,'Apple Color Emoji','Segoe UI Emoji'}iframe,img{max-width:100%}ol,ul{margin:0;margin-block-start:0;margin-block-end:0;margin-inline-start:0;margin-inline-end:0;padding-inline-start:20px}
                .ecom-dynamic-meta {
                    display: inline-block;
                    position: relative;
                    background: #f0f0f0;
                    padding-right: 2px;
                    border: 1px solid #c0b7b7;
                }
                .ecom-dynamic-meta:after {
                    content: attr(data-title);
                    padding-left: 20px;
                }
                .ecom-dynamic-meta::before {
                    content: '';
                    position: absolute;
                    left: 5px;
                    top: 2px;
                    width: 12px;
                    height: 14px;
                    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path d="M224 512C100.3 512 0 476.2 0 432V80C0 35.82 100.3 0 224 0C347.7 0 448 35.82 448 80V432C448 476.2 347.7 512 224 512zM416 80.45C415.7 79.69 414.4 77.27 409.8 73.31C402.4 67.11 389.9 60.09 371.6 53.57C335.4 40.62 283.2 32 224 32C164.8 32 112.6 40.62 76.37 53.57C58.1 60.09 45.59 67.11 38.25 73.31C33.55 77.27 32.29 79.69 32 80.45V182.1C46.47 192.7 69.9 202.8 100.9 210.4C135.5 218.9 177.1 224 224 224C270 224 312.5 218.9 347.1 210.4C378.1 202.8 401.5 192.7 416 182.1V80.45zM416 219.5C398.8 228.4 377.9 235.8 354.8 241.5C317.3 250.7 272.2 256 224 256C175.8 256 130.7 250.7 93.22 241.5C70.11 235.8 49.18 228.4 32 219.5V310.1C46.47 320.7 69.9 330.8 100.9 338.4C135.5 346.9 177.1 352 224 352C270 352 312.5 346.9 347.1 338.4C378.1 330.8 401.5 320.7 416 310.1V219.5zM38.25 438.7C45.59 444.9 58.1 451.9 76.37 458.4C112.6 471.4 164.8 480 224 480C283.2 480 335.4 471.4 371.6 458.4C389.9 451.9 402.4 444.9 409.8 438.7C414.4 434.7 415.7 432.3 416 431.6V347.5C398.8 356.4 377.9 363.8 354.8 369.5C317.3 378.7 272.2 384 224 384C175.8 384 130.7 378.7 93.22 369.5C70.11 363.8 49.18 356.4 32 347.5V431.6C32.29 432.3 33.55 434.7 38.25 438.7zM416 431.4C416.1 431.3 416.1 431.3 416.1 431.3L416 431.4zM31.96 431.4C31.94 431.3 31.93 431.3 31.92 431.3L31.96 431.4zM31.96 80.56C31.93 80.65 31.92 80.7 31.92 80.7L31.96 80.56zM416.1 80.7C416.1 80.7 416.1 80.65 416 80.56z"/></svg>');}</style> </head> <body class="text-content ecom-html"></body></html>`
        };
    },
    computed: {
        showToolbar() {
            return (this.type > 0 && this.field?.options?.toolbar !== false) || this.alwayToolbar;
        },
        alwayToolbar() {
            return ['short', 'full'].indexOf(this.field?.options?.toolbar) > -1;
        },
        shortToolbar() {
            return this.field?.options?.toolbar == 'short' || this.type === 1;
        },
        minHeight() {
            return this.field?.options?.height ?? '';
        },
        dynamicSouce() {
            return this.field?.options?.dynamic ?? false;
        },
        generator(){
            return this.field?.options?.generator ?? false;
        },
        ObjLink() {
            if (!this.selection.range) {
                return {};
            }
            let link = this.selectionIsLink();
            if (!link) {
                return { text: this.selection.range.toString() };
            }
            return node2Obj(link);
        },
        objImage() {
            if (!this.selection.image.node) {
                return {};
            }
            return node2Obj(this.selection.image.node);
        }
    },
    watch: {
        data: {
            immediate: true,
            handler: function (val) {
                if (!this.isEmit) {
                    this.value = val ?? '';
                    this.html = val ?? '';
                    this.editorDoc && (this.editorDoc.body.innerHTML = val ?? '');
                    this.clearSelection();
                    //this.getStateSelection();
                }
                this.isEmit = false;
            }
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.metadata.maxWidthMenu = this.$refs.editor?.clientWidth;
        });
    },
    methods: {
        onload() {
            if (!this.$refs['editor']) {
                return;
            }
            this.editor = this.$refs['editor'];
            this.editorDoc = this.editor.contentDocument;
            this.editorDoc.body.innerHTML = this.value;
            this.editorDoc.designMode = 'on';
            this.editorDoc.body.oninput = this.inputEditor;
            this.editorDoc.body.onselectstart = this.selectstart;
            this.editorDoc.body.onclick = this.clickEditor;
            this.editorDoc.body.onblur = this.selectend;
            this.editorDoc.body.onkeydown = this.keyDown;
            this.editorDoc.body.onpaste = this.pasteContent;

        },
        inputEditor(e) {
            if (this.type === 1 && e.keyCode === 13) {
                e.preventDefault();
                return false;
            }

            let html = this.selection.state['codeFormat'] ? e.target.querySelector('xmp')?.innerHTML : e.target.innerHTML;
            this.html = html;
            this.$emit('update:data', html);
            this.isEmit = true;
            //this.onSelectionChange();
            if (this.type === 2) {
                if (
                    e.target.scrollHeight > 64 &&
                    e.target.scrollHeight < 250 &&
                    this.minHeight === ''
                ) {
                    e.target.style.minHeight = e.target.scrollHeight + 'px';
                } else if (e.target.scrollHeight < 64 && this.minHeight === '') {
                    e.target.style.minHeight = '';
                }
            }
        },
        keyDown(e) {
            // if (/*e.which === 13 &&  */this.type == 1) {
            //     // e.preventDefault();
            // }
            if ((e.ctrlKey || e.metaKey) && !e.shiftKey && e.keyCode == 86) {
                this.wasPaste = true;
                setTimeout(() => {
                    this.wasPaste = false;
                }, 5000);
            }
        },
        pasteContent(e) {
            if (this.type == 1 && textHelper && this.editorDoc) {
                let clipboardData = e.clipboardData || window.clipboardData;
                let pastedData = clipboardData.getData('text/plain') || '';
                if (pastedData !== '') {
                    pastedData = pastedData.replace(/<[^>]*>/g, '');

                    textHelper.insertText(this.selection, pastedData)
                    this.editorDoc.body.dispatchEvent(new Event('input'));
                    e.preventDefault();
                }
            }
        },
        restoreRange(selectLink = true) {
            let rangeData = this.selection.range;
            if (!rangeData) {
                return false;
            }
            let $link = this.selectionIsLink();
            let range = rangeData;
            if ($link && selectLink) {
                range = this.editorDoc.createRange();
                range.selectNodeContents($link);
            }
            this.editorDoc.getSelection().removeAllRanges();
            this.editorDoc.getSelection().addRange(range);
        },
        updateColor(color) {
            clearTimeout(this.itv);
            this.itv = setTimeout(()=> {
                color.action === 'foreColor'
                    ? textHelper?.inserColor(this.selection, color.val)
                    : textHelper?.inserBackground(this.selection, color.val);
                if(this.editorDoc)
                    this.editorDoc.body.dispatchEvent(new Event('input'));
            },100)
        },
        eventAction(action) {
            this.selection.action = action;

            switch (action) {
                case 'insertLink':
                    this.$refs['insert-link'].show = true;
                    break;
                case 'dynamicSources':
                    this.$refs['dynamic-sources'].show = true;
                    this.$store.dispatch('dynamicSources/loadDynamicSources');
                    break;
                case 'contentGenerator':
                    this.$refs['generator'].show = true;
                    // this.$store.dispatch('dynamicSources/loadDynamicSources');
                    break;
                case 'insertunorderedlist':
                case 'insertorderedlist':
                    textHelper.insertOrderedlist(this.selection, this.value, action);
                    break;
                case 'removeFormat':
                    textHelper.removeFormat(this.selection);
                    if(this.editorDoc)
                        this.editorDoc.body.dispatchEvent(new Event('input'));
                    break;
                case 'codeFormat':
                    this.switchFormatCode();
                    this.selection.state[action] = !this.selection.state[action];
                    if(this.editorDoc)
                        this.editorDoc.body.dispatchEvent(new Event('input'));
                    break;
                default:
                    if (this.restoreRange() === false) {
                        return;
                    }

                    textHelper.insertHtmlTag(this.selection);
                    if(this.editorDoc)
                        this.editorDoc.body.dispatchEvent(new Event('input'));
                    this.selection.state[action] = !this.selection.state[action];

                    break;
            }
        },
        clearSelection() {
            if (window.getSelection) {
                if (window.getSelection().empty) {
                    window.getSelection().empty();
                } else if (window.getSelection().removeAllRanges) {
                    window.getSelection().removeAllRanges();
                }
            } else if (this.editorDoc.selection) {
                this.editorDoc.selection.empty();
            }
        },
        getStateSelection() {
            const cmds = [
                {
                    tag: 'b',
                    action: 'bold'
                },
                {
                    tag: 'i',
                    action: 'italic'
                },
                {
                    tag: 'u',
                    action: 'underline'
                },
                {
                    tag: 'ol',
                    action: 'insertorderedlist'
                },
                {
                    tag: 'ul',
                    action: 'insertunorderedlist'
                }
            ];

            cmds.forEach((cmd) => {
                this.selection.state[cmd.action] = this.isSelectionInTag(cmd.tag);
            });

        },
        selectionIsLink() {
            var container = this.selection.range.commonAncestorContainer;
            if (container.nodeType == 3) {
                container = container.parentNode;
            }
            return container.closest('a') ?? false;
        },
        isSelectionInTag(tag) {
            // Get the current node
            let selection = this.editorDoc.getSelection();
            let currentNode = selection.focusNode;
            // While the node is not the editor division

            while (currentNode && currentNode !== 'BODY')
            {
                if(currentNode?.tagName === tag.toUpperCase())
                    return true;
                // Move up in the tree
                currentNode = currentNode?.parentNode;
            }
            return false;
        },
        setDataMenu(el) {
            let $menu = this.$refs['modify'];
            let $editor = this.$refs['editor'],
                $editor_h = $editor.offsetHeight,
                $editor_st = $editor.scrollTop;
            let top = el.offsetTop + el.offsetHeight / (el.tagName == 'IMG' ? 2 : 1) - $editor_st;
            top = top > $editor_h ? $editor_h : top;
            this.metadata.posMenu = {
                top: top,
                left: el.offsetLeft,
                width: el.offsetWidth
            };
            $menu.show = true;
        },
        onSelectionChange() {
            if (this.type == 0) {
                return;
            }
            this.selection.selection = this.editorDoc.getSelection();

            //console.log(this.editorDoc.getSelection())
            this.selection.range = this.editorDoc.getSelection().getRangeAt(0);
            this.getStateSelection();
            let isLink = this.selectionIsLink();
            if (isLink) {
                this.metadata.menu = isLink.getAttribute('href');
                this.setDataMenu(isLink);
                this.selection.action = 'insertLink';
            } else {
                let $menu = this.$refs['modify'];
                $menu.show = false;
            }
        },
        selectstart() {
            if (this.type == 0) {
                return;
            }
            this.editorDoc.addEventListener('selectionchange', this.onSelectionChange);
        },
        selectend() {
            if (this.type == 0) {
                return;
            }
            this.editorDoc.removeEventListener('selectionchange', this.onSelectionChange);
            this.isfocus = false;
            setTimeout(() => {
                let $modify = this.$refs['modify'];
                if ($modify) {
                    $modify.show = false;
                }
            }, 200);
        },
        removeFromMenu() {
            if (this.selection.action == 'insertLink') {
                textHelper.removeLink(this.selection);
                if(this.editorDoc)
                    this.editorDoc.body.dispatchEvent(new Event('input'));
            } else if (this.selection.action == 'insertImage') {
                this.selection.image.node.remove();
                if(this.editorDoc)
                    this.editorDoc.body.dispatchEvent(new Event('input'));
            }
        },
        editFromMenu() {
            if (this.selection.action == 'insertLink') {
                this.eventAction('insertLink');
            } else if (this.selection.action == 'insertImage') {
                this.$refs['insert-image'].show = true;
            }
        },
        editImage(val) {
            if (val && this.selection.image.node) {
                var element = this.editorDoc.createElement('div');
                element.innerHTML = val;
                element = element.firstChild;
                this.selection.image.node.replaceWith(element);
                if(this.editorDoc)
                    this.editorDoc.body.dispatchEvent(new Event('input'));
            }
        },
        updateLink({ data }) {
            this.editorDoc.body.focus();
            if (this.selection.range) {
                this.selection.range.deleteContents();
            }
            this.restoreRange();
            textHelper.insertLink(this.selection, data);
            if(this.editorDoc)
                this.editorDoc.body.dispatchEvent(new Event('input'));

        },
        updateMetafield(data) {
            this.editorDoc.body.focus();
            if (this.selection.range) {
                this.selection.range.deleteContents();
                this.deleteSource();
            }
            this.restoreRange();
            textHelper.insertHTML(this.selection, data);
            if(this.editorDoc)
                this.editorDoc.body.dispatchEvent(new Event('input'));
        },
        updateHTML(html){
            this.editorDoc.body.innerHTML = html;
            if(this.editorDoc)
                this.editorDoc.body.dispatchEvent(new Event('input'));
        },
        clickEditor(e) {
            this.isfocus = true;
            let target = e.target;
            if(e.target && e.target.classList.contains('ecom-dynamic-meta')) {
                this.editSource = true;
            } else {
                this.editSource = false;
            }
            if (this.type < 2) {
                return;
            }

            let elImg = target.closest('img');
            if (elImg) {
                this.selection.image.node = elImg;
                this.metadata.menu = '';
                this.setDataMenu(elImg);
                this.selection.action = 'insertImage';
            }
        },
        switchFormatCode() {
            if(!this.editorDoc?.body) return false;
            if(this.selection.state['codeFormat']) {
                const xmp = this.editorDoc.body.querySelector('xmp');
                if(xmp) {
                    this.editorDoc.body.innerHTML = xmp.innerHTML;
                }
            } else {
                const xmp = document.createElement('xmp');
                xmp.style.display = 'contents';
                xmp.style.whiteSpace = 'inherit';
                xmp.innerHTML = this.editorDoc.body.innerHTML;
                this.editorDoc.body.innerHTML = '';
                this.editorDoc.body.appendChild(xmp);
            }
        },

        deleteSource() {
            if (this.selection.range) {
                const range = this.selection.range; 
                if(!range) return;
                const domFragement = range.cloneContents();

                let node = domFragement.querySelector('.ecom-meta-editor');
                let currentNode = this.selection.selection.focusNode;
                if(node) {

                } else {
                //Find node of tag
                while (currentNode && currentNode !== 'BODY')
                {
                    if(currentNode?.classList?.contains('ecom-meta-editor')) {
                        node = currentNode;
                        break;
                    }

                    currentNode = currentNode?.parentNode;
                }
                if(currentNode) {
                    const parent = currentNode.parentNode
                    parent.removeChild(currentNode);
                    range.deleteContents();
                }
                }
            }
        }
    }
  };
  </script>
  <style>
  .editor {
    min-height: 2rem;
    max-height: 15rem;
  }
  .editor[data-type='2'] {
    min-height: 4rem;
  }
  </style>