<template>
  <div class="tab">
      <div class="flex items-center justify-between px-4 py-2 mb-2 border-b dark:border-gray-600">
          <template v-if="!showInput">
            <span class="text-sm font-medium text-form-title capitalize dark:text-dark-color">{{ title }}</span>
                <span class="flex items-center">
                    <button
                        v-if="!hasDelete"
                        type="button"
                        class="p-2 transition rounded text-form-secondary hover:bg-gray-200 hover:text-form-secondary-hover dark:text-dark-color dark:hover:bg-dark dark:hover:text-white"
                        data-tooltip="Reset to default"
                        data-tooltip-location="top-right"
                        @click="$emit('refesh')"
                    >
                        <ecom-icon :icon="icons.faArrowsRotate" class="w-5 h-5 p-px block" />
                    </button>

                    <button
                        v-if="tabSelected !== 'global'"
                        type="button"
                        data-tooltip="Add new global"
                        data-tooltip-location="top-right"
                        class="p-2 transition rounded text-form-secondary hover:bg-gray-200 hover:text-form-secondary-hover dark:text-dark-color dark:hover:bg-dark dark:hover:text-white"
                        :class="{'opacity-50 pointer-events-none': check || selected}"
                        @click="showInput = true"
                    >
                        <ecom-icon :icon="icons.faPlus" class="w-5 h-5 p-px block" />
                    </button><template v-else />
                    <button
                        v-if="hasDelete && tabSelected !== 'global'"
                        type="button"
                        data-tooltip="Delete item"
                        data-tooltip-location="top-right"
                        class="p-2 transition rounded text-form-secondary hover:bg-gray-200 hover:text-form-secondary-hover dark:text-dark-color dark:hover:bg-dark dark:hover:text-white"
                        @click="$emit('update:delete')"
                    >
                        <ecom-icon :icon="icons.faTrash" class="w-5 h-5 p-px block" />
                    </button><template v-else />
                    <button
                        v-if="tabSelected == 'global'"
                        type="button"
                        class="flex p-2 transition rounded  text-form-secondary hover:bg-gray-200 dark:hover:bg-dark dark:hover:text-white hover:text-form-secondary-hover"
                        data-tooltip="Manage global settings"
                        data-tooltip-location="top-right"
                        @click="openSiteSettings"
                    >
                        <ecom-icon :icon="icons.faGear" class="w-5 h-5 block" />
                    </button>
                </span>
                </template>
                <div
                        v-else
                        class="flex w-full h-13"
                    >
                            <input
                                v-model="valueInput"
                                type="text"
                                class="w-full pl-2 transition border-gray-300 dark:border-gray-600 rounded rounded-r-none shadow-sm h-9 sm:text-sm focus:ring-transparent dark:bg-dark dark:focus:ring-transparent focus:border-gray-300 text-black dark:text-dark-color"
                                placeholder="New Item"
                                @keypress.enter="updateValue"
                                @keypress.esc="showInput = false"
                            >
                            <button type="button" class="inline-flex h-9 items-center px-2 text-gray-600 transition border border-l-0 border-gray-300 dark:border-gray-600 rounded-r cursor-pointer w-9 hover:bg-gray-200 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark">
                                <ecom-icon
                                    :icon="icons[(valueInput.trim() != '') ? 'faCheck' : 'faXmark']"
                                    class="w-5 h-5"
                                    @click="updateValue"
                                />
                            </button>
                    </div>
            </div>
        <div class="tab__heading border-form-border-input">
            <ul class="flex">
                <li
                    v-for="(tab, i) in tabs"
                    :key="i"
                    class="flex-1 ecom-item-tab-hover"
                >
                    <button
                        type="button"
                        class="flex items-center justify-center w-full px-3 py-2 text-sm font-normal text-center capitalize align-middle transition rounded h-7-5 whitespace-nowrap"
                        :class="tabSelected === tab ? 'text-primary-600': 'text-form-secondary dark:text-dark-color'"
                        @click="tabSelected = tab"
                    >
                        {{ tab }}
                    </button>
                    <span :class="tabSelected === tab ? ' bg-primary-600' : 'bg-gray-200 dark:bg-gray-600'" class="flex w-full" style="height:3px" />
                </li>
            </ul>
        </div>
        <div
            class="mt-3"
            >
            <div
                v-for="(tab, i) in tabs"
                :key="i"
            >
                <slot v-if="tabSelected === tab" :name="tab" />
            </div>
        </div>
    </div>
</template>

<script lang="ts">


import {faPlus,faGear,faArrowsRotate,faTrash,faCheck,faXmark} from '@fortawesome/pro-light-svg-icons';
export default {
    name:'SubcomponentTabs',

    props:{
        tabs:{
            type:Array,
            default() {
                return [];
            }
        },
        title:{
            type:String,
            default:''
        },
        type:{
            type:String,
            default:''
        },
        // eslint-disable-next-line vue/require-default-prop
        value:[String,Object],
        selected:{
            type:String,
            default:''
        },
        hasDelete: {
            type: Boolean,
            default: false
        }
    },
    emits:['refesh','addglobal','activecolor', 'update:delete'],
    data(){
        return{
            showInput: false,
            valueInput: '',
            tabSelected:this.selected ? this.tabs[1] : this.tabs[0],
            icons: {faPlus,faGear,faArrowsRotate,faTrash,faCheck,faXmark}
        };
    },
    computed:{
        check(){
            if(this.value){
                return typeof(this.value) == 'string' ? !this.value : !Object.keys(this.value).length;
            }else{
                return true;
            }
        }
    },
    methods:{
        openSiteSettings() {
            this.$store.dispatch('settings/open', {
                category: 'theme_settings',
                tab: 'global-' + this.type
            });
        },

        updateValue() {
            if(this.valueInput.trim() != '') {
                this.$emit('addglobal', this.valueInput);
            }
            this.showInput = false;
        }
    }
};
</script>

<style>
.ecom-item-tab-hover:hover span{
    opacity: 1;
}
</style>
