<template>
    <div v-show="show" class="w-full h-full">
        <template v-if="loading">
            <div class="relative flex items-center justify-center h-10">
                <Loading type="spin" />
            </div>
        </template>
        <template v-else>
            <div class="flex items-center justify-between py-1">
                <button
                    type="button"
                    class="
                        p-2
                        text-gray-600 text-sm
                        border-0
                        transition
                        hover:text-gray-800
                        dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark
                        focus:outline-none
                    "
                    @click="show = false"
                >
                    <span class="flex items-center">
                        <ecom-icon :icon="icons.faChevronLeft" class="w-5 h-5" />
                    </span>

                    <!-- <span>{{strResult(count)}}</span> -->
                </button>
                <input
                    v-if="type != 'email' && type != 'phone'"
                    v-model="search"
                    type="text"
                    class="flex-1 h-8 mr-2 text-sm transition border-gray-300 rounded shadow-sm  sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 focus:border-gray-300 dark:focus:ring-transparent"
                    placeholder="Search"
                >
                <input v-else
                    type="text"
                    ref="email_or_phone"
                    v-on:keydown.enter="$emit('update:select', this.$refs.email_or_phone.value)"
                    class="flex-1 h-8 text-sm transition border-gray-300 rounded shadow-sm  sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 focus:border-gray-300 dark:focus:ring-transparent"
                    :placeholder="type === 'email' ? 'Email' : 'Phone number'"
                >
                <button
                    v-if="type == 'email' || type == 'phone'"
                    type="button"
                    class="
                        p-2
                        text-gray-600 text-sm
                        border-0
                        transition
                        hover:text-gray-800
                        dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark
                        focus:outline-none
                    "
                    @click="$emit('update:select', this.$refs.email_or_phone.value)"
                >
                <span class="flex items-center">
                    <ecom-icon
                        :icon="icons.faCheck"
                        class="w-5 h-5 fill-current"
                    />
                </span>
                    <!-- <span>{{strResult(count)}}</span> -->
                </button>
            </div>

            <ul v-if="listData.length" ref="list" class="w-full">
                <ItemContent
                    v-for="item in listData"
                    :key="item.id"
                    :item="item"
                    :src="src"
                    :offset-list="offsetList"
                    @click="$emit('update:select', item.value)"
                />
                <li
                    v-if="isNext && loadmore.loading"
                    class="relative flex items-center justify-center h-10 text-center"
                >
                    <Loading type="spin" />
                </li>
            </ul>
        </template>
    </div>
</template>

<script>
import ItemContent from './ItemContent.vue';
import { mapGetters } from 'vuex';
import { faChevronLeft, faCheck } from '@fortawesome/pro-light-svg-icons'
export default {
    name: 'ContentLink',
    components: {
        ItemContent,
    },
    props: {
        type: {
            type: String,
            default: 'collection'
        },
        src: {
            type: String,
            default: ''
        },
        scrolledBottom: {
            type: Boolean,
            default: false
        },
        offsetList: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    emits: ['update:select', 'hide', 'dispatchScroll'],
    data() {
        return {
            init: false,
            show: false,
            search: '',
            listData: [],
            count: 10,
            loading: true,
            loadmore: {
                loading: false
            },
            icons:{faChevronLeft, faCheck}
        };
    },
    computed: {
        ...mapGetters('picker', { pickerData: 'data' }),
        ...mapGetters('picker', ['isLoadmore']),
        isNext() {
            return this.isLoadmore(this.type);
        }
    },
    watch: {
        type: function () {
            if (this.show) {
                this.listData = [];
                this.search = '';
                this.getData();
            }
        },
        show: function (val) {
            if (!val) {
                this.$emit('hide');
            }
        },
        search: function () {
            let itv = null;
            clearTimeout(itv);

            itv = setTimeout(() => {
                this.getData();
            }, 500)
        },
        scrolledBottom: function (val) {
            if (val && this.show && this.isNext && !this.loadmore.loading) {
                this.getData(true);
            }
        },
        loading: function (val) {
            !val && this.$emit('dispatchScroll');
        },
        'loadmore.loading': function (val) {
            !val && this.$emit('dispatchScroll');
        }
    },
    methods: {
        copy(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        strResult(count) {
            return count + ' result' + (count == 0 || count > 1 ? 's' : '');
        },
        getData(loadmore = false) {
            if (loadmore) {
                this.loadmore.loading = true;
            } else {
                this.loading = true;
            }
            if(this.type == 'email' || this.type == 'phone'){
                this.loading = false;
                return;
            }
            this.pickerData(this.type, '', this.search, loadmore).then((res) => {
                if (loadmore) {
                    this.listData = this.copy([...this.listData, ...res]);
                } else {
                    this.listData = this.copy(res);
                }
                this.loading = false;
                this.loadmore.loading = false;
                loadmore && (this.$parent.scroll.bottom = false);
            });
        }
    }
};
</script>

<style>
.min-w-5 {
    min-width: 1.25rem;
}
.focus\:border-b:focus {
    border-bottom-width: 1px;
    border-color: rgba(209, 213, 219, 1);
}
</style>
