<template>
    <div :class="id">
        <button
            :class="
                [isopen ? 'bg-gray-200 z-10' : 'opacity-0 transition-all',
                noHover ? '' : 'group-hover:opacity-100', cl]
            "
            class="p-2.5 text-gray-600 transition border-0 rounded hover:bg-gray-200 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark focus:outline-none focus:ring-0"
            @click="open"
        >
            <ecom-icon :icon="icons.faPencil" class="w-4 h-4" />
        </button>

        <transition
            enter-active-class="transition duration-200 ease-out"
            enter-from-class="transform scale-90 opacity-0"
            enter-to-class="transform scale-100 opacity-100"
            leave-active-class="transition duration-150 ease-in"
            leave-from-class="transform scale-100 opacity-100"
            leave-to-class="transform scale-90 opacity-0"
        >
            <div v-if="isopen" class="absolute z-10 mt-2 bg-white dark:bg-dark dark:text-dark-color top-full -right-1 text-left ecom-picker--modify">
                <div class="inline-block w-64 bg-white dark:bg-dark dark:text-dark-color rounded shadow-popup">
                    <svg
                        viewBox="0 0 213.3 213.3"
                        class="absolute top-0 w-4 h-4 mr-10 -mt-3 transform rotate-180 right-10 fill-current"
                    >
                        <polygon
                            class="shadow-xxl"
                            points="0,53.333 106.667,160 213.333,53.333"
                        />
                    </svg>
                    <Fields
                        class="py-4 p-3"
                        :deep="1"
                        :data="data"
                        :fields="fields"
                    />
                </div>
            </div>
        </transition>
     </div>
</template>

<script lang="ts">
    import { Popmenu } from '@/mixins';
    import { faPencil} from '@fortawesome/pro-light-svg-icons'
    export default {
        name: 'PickerModify',
        components: {
        },
        provide: {
            teleport_id: ".ecom-picker--modify"
        },
        mixins: [ Popmenu ],
        props: {
            fields: {
                type: Array,
                default() {
                    return [];
                }
            },
            data: {
                type: Object,
                default() {
                    return {};
                }
            },
            cl: {
                type: String,
                default: ''
            },
            noHover: {
                type: Boolean,
                default: false
            }
        },
        data(){
            return {
                icons: {faPencil}
            }
        },
        emits: ['update:modify'],
        watch: {
            data: {
                deep: true,
                handler() {
                    this.$emit('update:modify');
                }
            }
        }
    };
</script>
