<template>
    <div class="flex justify-end" :class="id">
        <div class="relative inline-flex justify-end">
            <button
                type="button"
                class="relative flex items-center popup__button"
                :class="size === 'normal' ? 'w-7 h-7 rounded border border-gray-300' : 'w-3 h-3 rounded-xs'"
                :style="bg"
                :data-tooltip="globalTitle ?? value ?? null"
                data-tooltip-location="top-left"
                @click="open"
            />
            <transition
                enter-active-class="transition duration-200 ease-out"
                enter-from-class="translate-y-1 opacity-0"
                enter-to-class="translate-y-0 opacity-100"
                leave-active-class="transition duration-150 ease-in"
                leave-from-class="translate-y-0 opacity-100"
                leave-to-class="translate-y-1 opacity-0"
            >
                <div v-if="isopen" style="z-index:22" class="absolute mt-3 top-full -left-4 ecom-fields__popup dark:bg-gray-700">
                    <ColorComponent
                        :global-selected="dGlobal.selected"
                        :global="global"
                        :value="value"
                        :field="field"
                        @refeshColor="refeshColor"
                        @update:value="value = $event"
                        @update:global-selected="dGlobal.selected = $event"
                    />
                </div>
            </transition>
        </div>
    </div>
</template>

<script lang="ts">
import { Popmenu } from '@/mixins';
import ColorComponent from '../../components/molecules/ColorPicker/ColorPanel.vue';

export default {
    name: 'Color',
    components: {
        ColorComponent,
    },
    mixins: [ Popmenu ],
    props: {
        field: {
            type: Object,
            default() {
                return {};
            }
        },
        data: [Object, String],
        size: {
            type: String,
            default: 'normal'
        }
    },
    emits: ['update:data'],
    data() {
        return {
            value: this.data ?? this.field.value ?? '',
            dGlobal: {
                selected: ''
            }
        };
    },
    computed: {
        bg() {
            return {
                background: this.value
                    ? this.value
                    : `linear-gradient(to top left,
                        rgba(0,0,0,0) 0%,
                        rgba(0,0,0,0) calc(50% - 0.8px),
                        red 50%,
                        rgba(0,0,0,0) calc(50% + 0.8px),
                        rgba(0,0,0,0) 100%)`
            };
        },
        globalTitle() {
            if (this.dGlobal) {
                return this.$store.getters['settings/globalTitle']('colors', this.dGlobal.selected);
            }
            return '';
        },
        global() {
            return this.field?.options?.global;
        }
    },
    watch: {
        'dGlobal.selected'(val) {
            if (val && this.global) {
                this.$emit('update:data', {
                    ['global-' + this.global.type]: val
                });
            }
        },
        data: {
            immediate: true,
            handler: function (val) {
                if (this.isWatch) {
                    this.isEmit = false;
                    this.value = val;
                }
                this.isWatch = true;
                this.dGlobal.selected = this.global?.value ?? '';
            }
        },
        value: {
            immediate: true,
            handler: function (val) {
                if (this.isEmit) {
                    this.isWatch = false;
                    this.$emit('update:data', val);
                }
                this.isEmit = true;
            }
        }
    },
    methods: {
        update(e) {
        },
        refeshColor() {
            this.value = '';
        }
    }
};
</script>

<style>
.ecom-status-dot {
    width: 6px;
    height: 6px;
    display: block;
    border-radius: 50%;
    bottom: 4px;
    left: 4px;
}
</style>
