<template>
        <div
            class="relative p-4 bg-white dark:bg-dark dark:text-dark-color border border-gray-300 rounded shadow-popup"
            :class="type !== 'time' ? 'text-primary-600' : 'text-primary-600'"
        >
            <svg
                viewBox="0 0 213.3 213.3"
                class="absolute top-0 w-4 h-4 mr-1 -mt-3 transform rotate-180 left-4 fill-current"
            >
                <polygon
                    class="shadow-xxl"
                    points="0,53.333 106.667,160 213.333,53.333"
                />
            </svg>
            <ContentDate
                v-show="type !== 'time'"
                :time="time"
                :time-temp="timeTemp"
                :tab="selected.tab"
                :field="field"
            />
            <ContentTime
                v-show="type !== 'date'"
                v-model:data="time"
                :type="options.type"
                :field="field"
            />
        </div>
</template>

<script lang="ts">
    import ContentDate from './Content/Date.vue';
    import ContentTime from './Content/Time.vue';

    import { EComDate } from '../../utils';

    export default {
        name: 'DateTimePicker',
        components: {
            ContentDate,
            ContentTime,
        },
        props: {
            field: {
                type: Object,
                default() {
                    return {};
                }
            },
            data: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        data() {
            return {
                timeTemp : {},
                selected: {
                    tab: 'date',
                    time: 'hour'
                }
            };
        },
        computed: {
            time() {
                return this.data;
            },
            options() {
                return this.field?.options;
            },
            type() {
                return this.options?.type ?? 'date';
            },
            min() {
                return this.options.min || '';
            },
            max() {
                return this.options.max || '';
            },
            names() {
                return EComDate.names;
            },
            now() {
                let now = new Date();
                return {
                    day: this.names.day[now.getDay()],
                    date: now.getDate(),
                    month: now.getMonth() + 1,
                    year: now.getFullYear()
                };
            },
            nameDay() {
                let now = this.now;
                let { day = now.date, month = now.month, year = now.year } = this.time;
                let i = EComDate.startIndex(day, month, year);
                return this.names.day[i];
            }
        },
        watch: {
            // 'selected.tab':{
            //     immediate: true,
            //     handler: function(val){
            //         if(val === 'time') {
            //             this.time.hour = this.time.hour ?? 12;
            //             this.time.minute = this.time.minute ?? 0;
            //         }
            //     }
            // },
            data: {
                deep: true,
                immediate: true,
                handler: function(value) {
                    let { month = this.now.month, year = this.now.year } = value;
                    this.timeTemp = {
                        month, year
                    };
                }
            }
        },
        created () {
            if(this.type === 'time') {
                this.selected.tab = 'time';
            }
        },
        methods: {
            ordinalSuffix(n) {
                let s = ['th', 'st', 'nd', 'rd'],
                    v = n % 100;
                return n + (s[(v - 20) % 10] || s[v] || s[0]);
            },
            num2Didits(num) {
                return (num < 10 ? '0' : '') + num;
            }
        }
    };
</script>
