<template>
<transition
        enter-active-class="transition duration-300 ease-in-out transform"
        enter-from-class="opacity-0"
        enter-to-class="opacity-100"
        leave-active-class="transition duration-100 ease-in-out transform"
        leave-from-class="opacity-100"
        leave-to-class="opacity-0"
        >
    <div
        v-if="show"
        ref="wrapper"
        class="absolute inline-flex items-center h-10 px-1 mt-1 text-sm bg-white dark:bg-dark dark:text-dark-color rounded-lg max-w-62 text-primary-500 shadow-popup z-10"
        :style="css"
    >
        <a
            v-if="url"
            :href="urlFull(url)"
            class="relative px-2 py-1 pr-5 truncate transition hover:text-blue-700"
            target="_blank"
        >
            {{ url }}
            <ecom-icon :icon="icons.faArrowUpRightFromSquare" class="absolute right-0 w-4 h-4 transform -translate-y-1/2 top-1/2" />
        </a>
        <a
            href="#"
            class="px-2 py-1 ml-1 text-sm transition rounded hover:text-blue-700"
            @click.prevent="emit('edit')"
        >
            Edit
        </a>
        <a
            href="#"
            class="px-2 py-1 transition rounded hover:text-blue-700"
            @click.prevent="emit('delete')"
        >
            <ecom-icon :icon="icons.faTrash" class="w-4 h-4" />
        </a>
    </div>
</transition>
</template>

<script lang="ts">
    import { faTrash, faArrowUpRightFromSquare} from '@fortawesome/pro-light-svg-icons';
    import { mapGetters } from 'vuex';
    export default {
        name: 'Visit',

        props: {
            url: {
                type: String,
                default: ''
            },
            maxWidth: {
                type: Number,
                default: 210
            },
            info: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        data(){
            return {
                show: false,
                width: -1,
                infoData: this.copy(this.info),
                icons: { faTrash, faArrowUpRightFromSquare}
            };
        },
        computed: {
            ...mapGetters('global', ['user']),
            css(){
                let rs = {};
                Object.keys(this.infoData)
                    .filter(x  => x != 'width')
                    .forEach(x => {
                    rs[x] = this.infoData[x] + 'px';
                });
                rs['max-width'] = this.maxWidth + 'px';
                return rs;
            }
        },
        watch: {
            info: {
                deep: true,
                handler: function(val){
                    this.$nextTick(() => {
                        this.infoData = this.copy(val);
                        let wrapper = this.$refs['wrapper'];
                        if(this.show){

                                this.width = wrapper.clientWidth;
                                this.setPos();
                        }
                    });
                }
            }
        },
        methods: {
            copy(obj){
                return JSON.parse(JSON.stringify(obj));
            },
            setPos(){
                let {left, width} = this.infoData;
                let leftNew = left - (this.width - width) / 2;
                if(leftNew <= 0) {
                    leftNew = 0;
                }
                else if(leftNew + this.width > this.maxWidth){
                    let than = leftNew + this.width - this.maxWidth;
                    leftNew = (than > leftNew) ? 0 : than - leftNew;
                }
                this.infoData.left = leftNew;
            },
            urlFull(url){
                return url.startsWith('/') ? ('https://' + this.user?.name + url) :
                        url;
            },
            emit(action){
                this.$emit('update:' + action);
                this.show = false;
            }
        }
    };
</script>

<style>
    .max-w-62{
        max-width: 15.5rem;
    }
</style>
