<template>
    <div class="relative flex flex-col items-start">
        <div
        v-for="(item,index) in Object.keys(list)"
        :key="index"
        class="flex items-center w-full mb-2"
        >
            <input
                :id="item"
                v-model="value"
                type="checkbox"
                :value="item"
                class="w-5 h-5 p-px mr-2 border-2 border-gray-400 rounded cursor-pointer focus:ring-0 focus:ring-transparent text-primary-600"
                >
            <label
                class="text-sm font-normal leading-5 cursor-pointer text-form-title dark:text-dark-color"
                :for="item"
            >
            {{ list[item] }}
            </label>
        </div>
    </div>
</template>
<script lang="ts">
export default {
    props: {
        field: {
            type: Object,
            default(){
                return {};
            }
        },
        // eslint-disable-next-line vue/require-default-prop
        data: [Array]
    },
    emits: ['update:data'],
    data () {
        return {
            value: this.data ?? this.field?.value ?? [],
            list: this.field.options?.values,
            status: true,
            emit: false
        };
    },
    watch: {
        data:{
            immediate: true,
            handler:function(val){
                if(this.status){
                    this.emit = false;
                    if (typeof val != 'undefined') {
                        this.value = val;
                    }
                }
                this.status = true;
            }
        },
        value: {
            immediate: true,
            handler: function (val) {
                if (this.emit) {
                    this.status = false;
                    this.$emit('update:data', val);
                }
                this.emit = true;
            },
        }
    },
};
</script>
