export default {
    data() {
        return {
            x: 0,
            y: 0,
            draggingClone: null,
            draggingEle: null,
            enterEl: null,
            isDraggingStarted: false,
            hasSorted: false,
            above: false,
            needCheck: false,
            changedPos: {
                before: null,
                after: null
            },
        };
    },
    methods: {
        dragDir(type, dir) {
            let c = {
                padding: {
                    top: {
                        c: 's-resize',
                        d: 1
                    },
                    right: {
                        c: 'w-resize',
                        d: 0
                    },
                    bottom: {
                        c: 'n-resize',
                        d: 0
                    },
                    left: {
                        c: 'e-resize',
                        d: 1
                    }
                },
                margin: {
                    top: {
                        c: 'n-resize',
                        d: 0
                    },
                    right: {
                        c: 'e-resize',
                        d: 1
                    },
                    bottom: {
                        c: 's-resize',
                        d: 1
                    },
                    left: {
                        c: 'w-resize',
                        d: 0
                    }
                },
                'border-radius': {
                    top: {
                        c: 'n-resize',
                        d: 0
                    },
                    right: {
                        c: 'n-resize',
                        d: 0
                    },
                    bottom: {
                        c: 's-resize',
                        d: 1
                    },
                    left: {
                        c: 's-resize',
                        d: 1
                    }
                }
            };
            return c[type]?.[dir] ?? c['margin'][dir] ?? {
                c: 's-resize',
                d: 1
            };
        },

        getPos(el) {
            const index = (el) => {
                let i = 0;
                while( (el = el.previousElementSibling) != null && el.classList.contains('item') ) {
                    i++;
                }
                return i;
            };
            if(el) {
                let i, pos = [], p;
                do {
                    i = index(el);
                    p = el.dataset.id ? {index: i, id: el.dataset.id} : i;
                    pos.unshift(p);
                }
                while((el = el.parentNode.closest('.item')) && this.$el.contains(el));
                return pos;
            }
            return [];
        },

        insert(nodeA, nodeB, before = true) {

            if(
                (this.canDrop && !this.canDrop(nodeA, nodeB)) ||
                !this.needCheck ||
                nodeA === nodeB ||
                (nodeA.dataset.id !== undefined &&
                nodeA.dataset.id === nodeB.dataset.id)
            )
            {
                return;
            }
            if(nodeB.classList.contains('item-empty')) {
                nodeB.style.display = 'none';
            }
            let parentB = nodeB.parentNode;
            nodeB = before ? nodeB : nodeB.nextElementSibling;
            parentB.insertBefore(nodeA, nodeB);
            this.hasSorted = true;
        },

        isAbove(nodeA, nodeB) {
            if(!nodeB) {
                return undefined;
            }
            // Get the bounding rectangle of nodes
            const rectA = nodeA.getBoundingClientRect();
            const rectB = nodeB.getBoundingClientRect();
            return (rectA.top + rectA.height / 2 < rectB.top + rectB.height / 2);
        },

        mouseDownHandler(e) {

            if (
                e.target.tagName == 'INPUT' ||
                e.target.tagName == 'TEXTAREA' ||
                document.activeElement.isContentEditable
            ) {
                return;
            } 
           
            let items = this.$el.querySelectorAll('.item');
            items.forEach(li => {
                li.onmouseenter = (e) => {
                    let target = e.target.closest('.item');
                    if(
                        target !== this.enterEl &&
                        !target.classList.contains('ecom-dragging--placeholder'))
                    {
                        this.needCheck = true;
                        this.enterEl = target;
                    }
                    items.forEach(li => {
                        if( (this.canDrop && this.canDrop(this.draggingEle, this.enterEl) || !this.canDrop)) {
                            li.style.removeProperty('display');
                        }
                    });
                };
            });
            this.hasSorted = false;
            this.draggingEle = e.target.closest('.item');

            const rect = this.draggingEle.getBoundingClientRect();
            this.x = e.pageX - rect.left ;
            this.y = e.pageY - rect.top ;
            this.changedPos.before = this.getPos(this.draggingEle);
            this.cbStartDrag && this.cbStartDrag();
            document.addEventListener('mousemove', this.mouseMoveHandler);
            document.addEventListener('mouseup', this.mouseUpHandler);
        },

        mouseMoveHandler(e) {
            const draggingRect = this. draggingEle.getBoundingClientRect();

            if (!this.isDraggingStarted) {
                this.isDraggingStarted = true;
                this.draggingClone = this.draggingEle.cloneNode(true);
                this.draggingClone.onmouseenter = null;
                document.body.append(this.draggingClone);
                this.draggingEle.classList.add('ecom-dragging--placeholder');
                this.draggingEle.onmouseenter = null;
            }

            this.draggingClone.classList.add('ecom-dragging');
            draggingRect.width && (this.draggingClone.style.width = `${draggingRect.width}px`);
            this.draggingClone.style.top = `${e.pageY - this.y}px`;
            if(!this?.drag?.axis ||
                (this?.drag?.axis && this.drag.axis.indexOf('y') === -1)) {
                this.draggingClone.style.left = `${'left',e.pageX - this.x}px`;
            }
            else {
                this.draggingClone.style.left = `calc(${draggingRect.x }px - ` +window.getComputedStyle(this.draggingClone).marginLeft+ `)`;
            }

            let above = this.isAbove(this.draggingClone, this.enterEl);
            if(above !== this.above) {
                this.needCheck = true;
                this.above = above;
            }
            if(this.enterEl && this.needCheck && above !== undefined) {
                this.insert(this.draggingEle, this.enterEl, above);
            }
            this.needCheck = false;

        },

        mouseUpHandler() {
            document.querySelectorAll('li').forEach(li => {
                li.onmouseenter = null;
            });
            this.draggingClone && this.draggingClone.remove();
            this.draggingEle.classList.remove('ecom-dragging--placeholder');
            this.changedPos.after = this.getPos(this.draggingEle);
            if (typeof this.cbEndDrag == 'function') {
                this.cbEndDrag(this.changedPos);
            }
            this.enterEl = null,
            this.needCheck = false;
            this.x = null;
            this.y = null;
            this.isDraggingStarted = false;
            document.removeEventListener('mousemove', this.mouseMoveHandler);
            document.removeEventListener('mouseup', this.mouseUpHandler);
        },
    },
};
