export const blankObj = (obj) => {
    Object.keys(obj).forEach(field => {
        if (!obj[field]) {
            delete obj[field];
        }
        else if (typeof obj[field] == 'object') {
            blankObj(obj[field]);
            if (!Object.keys(obj[field]).length){
                delete obj[field];
            }
        }
    });
}

export const node2Obj = (node) => {
    let rs = {};
    Array.from(node.attributes).forEach(attr => {
        rs[attr.nodeName] = attr.nodeValue;
    });
    let innerText = node.innerText;
    if (innerText) {
        rs.text = innerText;
    }
    return rs;
};

export const formatOptions = (field) => {
    let { options } = field;
    options = options ?? {};
    let { button_text, title, type,
        search, multiple, useDelete,
        drag, layout, tabs } = options;
    return {
        button_text: button_text ?? 'Select image',
        title: title ?? 'Select image',
        type: type ?? 'image',
        search: search ?? true,
        multiple: multiple ?? false,
        delete: useDelete ?? true,
        drag: drag ?? true,
        layout: layout ?? 'grid',
        tabs: tabs ?? {}
    };
};


