<template>
    <div class="relative flex switchdevice" :class="id">
        <button
            class="text-gray-600 px-1.5 hover:text-primary-600 transition dark:text-dark-color"
            :data-tooltip="screens[current].title"
            data-tooltip-location="right"
            @click="doOpen"
        >
            <ecom-icon :icon="icons[screens[current]?.icon]" class="w-4 h-4 relative z-0" />
        </button>
        <div
            v-if="open"
            class="switch__content shadow-lg ring-1 ring-black ring-opacity-10 p-1.5 absolute -top-2 right-1/2 transform translate-x-1/2 bg-white dark:bg-dark dark:text-dark-color rounded transition z-20"
            :class="classContent"
        >
            <div
                v-for="(value, name) in screens"
                :key="name"
                class="flex"
            >
                <button
                    type="button"
                    class="w-full px-1 py-1 text-gray-600 transition hover:text-primary-500 dark:text-dark-color"
                    :data-tooltip="value.title"
                    data-tooltip-location="right"
                    :class="{ 'text-primary-600': current === value.name }"
                    @click="choose(name)"
                >
                    <ecom-icon :icon="icons[value.icon]" class="w-4 h-4" />
                </button>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
// Reduce time render by remove listOptions
import { faDesktop, faMobile, faTablet } from '@fortawesome/pro-light-svg-icons';
export default {
    data() {
        return {
            open: false,
            id: this.$helpers.randid(),
            icons: {
                faDesktop, faMobile, faTablet
            }
        }
    },
    computed: {
        current() {
            return this.screen;
        },
        classContent() {
            let index = Object.keys(this.screens).indexOf(this.current);
            if (index == 1) {
                return '-mt-6';
            } else if (index == 2) {
                return '-mt-12';
            }
            return '';
        },
        screens() {
            return this.$store.getters['builder/screens'];
        },
        screen() {
            return this.$store.getters['builder/screen'];
        }
    },
    methods: {
        choose(val) {
            this.$store.commit('builder/setScreen', val);
            this.doClose();
        },
        doOpen(e) {
            this.open = true;
            document.addEventListener('click', this.doClose);
        },

        doClose(e) {
            if (
                e === undefined ||
                !e.target.closest('.'+this.id)
            ) {
                this.open = false;
                document.removeEventListener('click', this.doClose);
            }
        }
    }
};
</script>
