<template>
    <div
        class="relative"
    >
        <div class="flex items-center justify-between pb-2">
            <button
                type="button"
                class="text-gray-600 transition border-0 focus:outline-0 focus:ring-0 hover:text-gray-700 dark:text-dark-color dark:hover:text-primary-600"
                :class="{'opacity-50 pointer-events-none': !isPrev}"
                @click="changePreview(-1)"
            >
                <ecom-icon :icon="icons.faArrowLeft" class="w-5 h-5" />
            </button>
            <div class="flex items-center pl-3">
                <Dropdown
                    v-model:data.number="timeTempComp.month"
                    :field="{options:{values: months, default: false}}"
                    :cl="'relative flex-1 max-h-full pr-6 focus:z-10 rounded-r-none'"
                />
                <Dropdown
                    v-model:data.number="timeTempComp.year"
                    :field="{options:{values: years, default: false}}"
                    :cl="'relative flex-1 max-h-full pr-6 w-auto focus:z-10 -ml-0.5 rounded-l-none'"
                />
            </div>
            <button
                type="button"
                class="ml-4 text-gray-600 transition border-0 focus:outline-0 focus:ring-0 hover:text-gray-700 dark:text-dark-color dark:hover:text-primary-600"
                :class="{'opacity-50 pointer-events-none': !isNext}"
                @click="changePreview(1)"
            >
                <ecom-icon :icon="icons.faArrowLeft"  class="w-5 h-5 transform rotate-180" />
            </button>
        </div>
        <ul class="flex items-center">
            <li
                v-for="day in names.day"
                :key="day"
                class="flex-1 py-2 text-xs text-center w-1/7 text-form-secondary"
            >
                {{ day.slice(0, 3) }}
            </li>
        </ul>

        <ul
            class="flex flex-wrap px-1 text-sm text-gray-600 dark:text-dark-color"
        >
            <li
                v-for="(d, index) in days"
                :key="index"
                class="mb-1 text-center cursor-pointer select-none w-1/7"
                :class="[state(d.day, index) !== 0 ? 'opacity-0 pointer-events-none' : (d.disabled ? 'pointer-events-none opacity-50' :'')]"
                @click="selectDay(d.day, index)"
                :data-tooltip="(now.date === d.day &&
                        now.month === timeTemp.month &&
                        now.year === timeTemp.year) ? 'Today' :null"
                data-tooltip-location="bottom"
            >
            <span
                class="relative block transition rounded pb-100p ring-2 ring-transparent"

                :class="[
                    (((Object.keys(time).length &&
                        (time.day === d.day &&
                        time.month === timeTemp.month &&
                        time.year === timeTemp.year)) ||
                    (!Object.keys(time).length && (now.date === d.day &&
                        now.month === timeTemp.month &&
                        now.year === timeTemp.year))) &&
                    state(d.day, index) === 0) ?
                        'bg-primary-600 text-white' :
                        'text-form-input hover:ring-primary-600',
                        (now.date === d.day &&
                        now.month === timeTemp.month &&
                        now.year === timeTemp.year)? 'bg-primary-100 font-bold':''
                ]"
            >
                <span class="absolute transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2 dark:text-dark-color">{{ d.day }}</span>
            </span>
            </li>
        </ul>
         <div class="relative flex align-middle justify-center">
            <button type="button" class="text-primary-500 font-light hover:font-bold hover:text-primary-700" @click="setToday">Today</button>
        </div>
    </div>
</template>

<script lang="ts">
    import { EComDate } from '../../../utils';

    import { faArrowLeft} from '@fortawesome/pro-light-svg-icons';
    import Dropdown from '../../dropdown/Dropdown.vue';
    import mixin from '../mixin';
    export default {
        name: 'ContentDate',
        components: {
            Dropdown
        },
        mixins: [mixin],
        props: {
            field: {
                type: Object,
                default() {
                    return {};
                }
            },
            time: {
                type: Object,
                default() {
                    return {};
                }
            },
            timeTemp: {
                type: Object,
                default() {
                    return {};
                }
            },
            tab: {
                type: String,
                default: ''
            },
        },
        computed: {
            icons(){
                return { faArrowLeft};
            },
            timeComp() {
                return this.time;
            },
            timeTempComp() {
                return this.timeTemp;
            },
            names() {
                return EComDate.names;
            },
            now() {
                let now = new Date();
                return {
                    date: now.getDate(),
                    month: now.getMonth() + 1,
                    year: now.getFullYear()
                };
            },
            isPrev() {
                let { month, year } = this.timeTempComp;
                return !(month <= this.minDate.getMonth() + 1 && year <= this.minDate.getFullYear());
            },
            isNext() {
                let { month, year } = this.timeTempComp;
                return !(month >= this.maxDate.getMonth() + 1 && year >= this.maxDate.getFullYear());
            },
            months() {
                let months = this.$helpers.copy(this.names.month);
                let { year } = this.timeTempComp;
                let minMonth = this.minDate.getMonth() + 1,
                    maxMonth = this.maxDate.getMonth() + 1,
                    minYear  = this.minDate.getFullYear(),
                    maxYear  = this.maxDate.getFullYear();
                months.forEach(m => {
                    if((year <= minYear && m.name < minMonth) ||
                        (year >= maxYear && m.name > maxMonth)) {
                        m.disabled = true;
                    }
                });
                return months;
            },
            years() {
                let min = this.minDate?.getFullYear(),
                    max = this.maxDate?.getFullYear();
                let years = {};
                for(let i = min; i <= max; i++) {
                    years[i] = i;
                }
                return years;
            },
            days() {
                let days = EComDate.days(this.timeTemp.month ?? this.now.month, this.timeTemp.year ?? this.now.year);
                let { month, year } = this.timeTempComp;
                let isTimeMin = year <= this.minDate.getFullYear() &&
                        month <= this.minDate.getMonth() + 1,
                    isTimeMax = year >= this.maxDate.getFullYear() &&
                        month >= this.maxDate.getMonth() + 1;
                let minDay = this.minDate.getDate(),
                    maxDay = this.maxDate.getDate();

                days = days.map(day => {
                    return {
                        day: day,
                        disabled: (isTimeMin && minDay > day) ||
                                  (isTimeMax && maxDay < day)
                    };
                });
                return days;
            },
        },
        methods: {
            setToday(){
                let now = new Date();
                this.time.day = now.getDate();
                this.time.month = now.getMonth()+1;
                this.time.year = now.getFullYear();
            },
            state(day, index) {
                if(index < 14) {
                    if(day > 14) {
                        return -1;
                    }
                }
                else if(index > 21 && day < this.days?.[index - 14]){
                    return 1;
                }
                return 0;
            },

            updateObject(obj, payload ) {
                for(let k in payload) {
                    obj[k] = payload[k];
                }
            },
            changePreview(state) {
                let { month, year } = this.timeTempComp,
                    newMonth = month
                if(state == -1) {
                    this.updateObject(this.timeTempComp, {
                        month: month === 1 ? 12 : (month - 1),
                        year: month === 1 ? (year - 1) : year
                    });
                }
                else if(state == 1) {
                    this.updateObject(this.timeTempComp, {
                        month: month === 12 ? 1 : (month + 1),
                        year: month === 12 ? (year + 1) : year
                    });
                }
            },
            getTimeZone() {
                var offset = new Date().getTimezoneOffset(), o = Math.abs(offset);
                return (offset < 0 ? '+' : '-') + ('00' + Math.floor(o / 60)).slice(-2) + ('00' + (o % 60)).slice(-2);
            },
            selectDay(day, index) {
                let state = this.state(day, index);

                this.timeComp.day = day;
                this.timeComp.hour = this.timeComp.hour ?? 12;
                this.timeComp.minute = this.timeComp.minute ?? 0;
                this.timeComp.gmt = this.timeComp.gmt ?? this.getTimeZone();
                if(state !== 0) {
                    this.changePreview(state, true);
                }
                this.updateObject(this.timeComp, this.timeTempComp);

            },
        }
    };
</script>
<style>
    .w-1\/7 {
        width: 14.285714%;
    }
</style>
