<template>
  <PickerFrame
  :show="show"
  :header="headerFrame"
  :position="position"
  @close="show = false"
>
  <template #content>
    <Loading
        v-if="loading"
        type="spin"
        :size="8"
        class="text-gray-800 dark:text-dark-color transition mt-10"
    />
    <div v-else>
      <h3
        v-html="shopifyType"
        class="p-2 text-center uppercase"
      />
      <ul
        v-if="dynamicSource?.length"
        class="px-2"
      >
        <li
          v-for="(item, index) in dynamicSource"
          :key="index"
          class="py-2 cursor-pointer leading-4 text-sm line-clamp-2 hover:underline"
          @click="insertSource(item)"
        >
          {{item?.title}}
        </li>
      </ul>
      <ul
        v-if="metafileds?.length"
        class="px-2"
      >
        <li
          v-for="(item, index) in metafileds"
          :key="index"
          class="py-2 cursor-pointer leading-4 text-sm line-clamp-2 hover:underline"
          @click="insertMeta(item)"
        >
          {{item?.node?.name}}
        </li>
      </ul>
    </div>

  </template>
</PickerFrame>
</template>

<script lang="ts">
  import { mapGetters } from 'vuex';
  import PickerFrame from '../../picker/PickerFrame.vue';
  import PickerFooter from '../../picker/PickerFooter.vue';
  import { faCheck,faXmark} from '@fortawesome/pro-light-svg-icons';
  import { clickOutside } from '../../../directives';

  export default {
      name: 'InsertLink',
      directives: {
          clickOutside
      },
      components: {
          PickerFrame,
          PickerFooter

      },
      props: {
          data: {
              type: Object,
              default(){
                  return  {};
              }
          },
          type: {
              type: String,
              default: 'editor'
          },
          position: {
              type: String,
              default: 'absolute'
          }
      },
      emits: ['close_popup', 'update:metafield'],
      data(){
          return {
              show: false,
              value: {},
              attributes: '',
              styleGroupTyles: {},
              icons: { faCheck,faXmark}
          };
      },
      computed: {
        headerFrame(){
            return {
                title: 'Insert Dynamic Sources',
                search: {
                    show: false,
                    value: ''
                }
            };
        },
        ...mapGetters('dynamicSources', ['metafileds', 'dynamicSource', 'loading', 'shopifyType']) ,
      },
      watch: {
          show: function(val){
          },
      },
      methods: {
        updateSources() {

        },
        truncateTitle(title) {
          return title.length <= 10 ? title : title.slice(0,10) + '...'
        },
        insertMeta(meta) {
          const m = `<span data-meta-key="${meta?.node.key}" data-meta="product.metafields.${meta?.node?.namespace}.${meta?.node.key}.value" data-title="${this.truncateTitle(meta?.node?.key)}" class="ecom-dynamic-meta"></span>`;
          const span = document.createElement('span');
          span.classList.add('ecom-meta-editor')
          span.innerHTML = ` ${m} `;
          this.$emit('update:metafield', span);
          this.show = false;
        },
        insertSource(source) {
          const m = `<span data-meta-key="${source.title}" data-meta="${source.key}" data-title="${this.truncateTitle(source.title)}" class="ecom-dynamic-meta"></span>`
          const span = document.createElement('span');
          span.classList.add('ecom-meta-editor')
          span.innerHTML = ` ${m} `;
          this.$emit('update:metafield', span);
          this.show = false;
        }

      }

  };
</script>
