<template>
    <div class="relative">
        <div
            class="absolute right-0 flex items-center justify-center mb-1 heading-number bottom-full"
        >
            <div>
                <ul v-if="Object.keys(units).length > 1" class="flex justify-end w-full">
                    <li v-for="(item, index) in Object.keys(units)" :key="index">
                        <button
                            type="button"
                            class="
                                border-0
                                focus:outline-none focus:ring-0
                                px-1
                                min-w-5
                                py-0.5
                                text-tiny
                                uppercase
                            "
                            :class="
                                unit == item
                                    ? 'text-form-input dark:text-primary-600 underline'
                                    : 'text-form-secondary dark:text-dark-color dark:hover:text-primary-600 hover:text-form-secondary-hover'
                            "
                            @click="chooseType(item)"
                        >
                            {{ item }}
                        </button>
                    </li>
                </ul>
            </div>
        </div>
        <div class="flex items-center w-full slider-number-wrp">
            <template v-if="useSlide">
                <div class="relative flex flex-1">
                    <input
                        :value="(value && value != 'none') ? (value == 'auto' ? min : value ?? defaultNum) : min"
                        type="range"
                        :min="min"
                        :max="max"
                        :step="step"
                        class="relative w-full h-5 ecom-slider-number focus:outline-none input-slider text-primary-600 dark:bg-gray-700"
                        style="cursor: grab"
                        :placeholder="field?.placeholder"
                        @input="value = $event.target.value"
                    >
                    <div
                        class="absolute h-0.5 transform rounded pointer-events-none number-slide-per top-2/4 -translate-y-2/4 bg-primary-600"
                        :style="percentProgressbar"
                        style="z-index: 0"
                    />
                </div>
                <span
                    v-show="!showinput"
                    class="ml-2 px-2 text-sm text-right cursor-pointer text-form-secondary dark:text-dark-color dark:hover:text-primary-600 focus:outline-none uppfirst sitooltip relative"
                    data-tooltip="Click to edit"
                    @click="showInput"
                >{{ data ?? (dt ?? 'none') }}</span>
                <input
                    v-if="showinput"
                    v-focus="focus"
                    class="relative min-w-10 pl-2 pr-1 ml-2 text-center transition border-gray-300 rounded shadow-sm h-7-5 sm:text-sm focus:ring-inset focus:ring-1 focus:ring-primary-400 dark:focus:ring-transparent focus:border-primary-400 dark:bg-dark dark:text-dark-color"
                    type="number"
                    :value="data !== undefined ? value : ''"
                    :min="min"
                    :max="max"
                    :step="step"
                    :placeholder="field?.placeholder"
                    @blur="debounceInputSlider"
                    @keydown.enter.esc="debounceInputSlider"
                    ref="input_slider"
                >
                <template v-else />
            </template>
            <div
                v-else
                class="flex justify-center"
                :class="{
                    'pl-2 ml-2': useSlide,
                    'w-20 number-input-container': !(
                        (field.options?.half && field.options?.half == true) ||
                        field.options?.control_width
                    ),
                    'w-full':
                        (field.options?.half && field.options?.half == true) ||
                        field.options?.control_width
                }"
                :style="
                    field.options?.control_width
                        ? 'width:' + field.options?.control_width
                        : field.options?.oneline
                        ? 'max-width:60%'
                        : ''
                "
            >
                <input
                    :value="data !== undefined ? value : ''"
                    class="relative w-full pl-2 pr-1 transition border-gray-300 rounded shadow-sm h-7-5 sm:text-sm focus:ring-inset focus:ring-1 focus:ring-primary-400 focus:border-primary-400 text-black dark:text-dark-color dark:focus:ring-transparent dark:bg-dark dark:border-gray-600"
                    type="number"
                    :min="min"
                    :max="max"
                    :step="step"
                    :placeholder="field?.placeholder"
                    @input="debounceInput"
                    ref="input_default"
                >
            </div>
        </div>
    </div>
</template>

<script lang="ts">
export default {
    name: 'Number',
    props: {
        field: {
            type: Object,
            default() {
                return {};
            }
        },
        // eslint-disable-next-line vue/require-default-prop
        data: [String, Number]
    },
    emits: ['update:data'],
    data() {
        return {
            value: this.data ?? this.field?.value ?? this.field.options?.units?.[this.unit]?.min ?? 'none',
            units: this.field.options?.units ?? {},
            focus: this.field?.focus ? true : false,
            useSlide: this.field.options?.slider ?? true,
            status: true,
            emit: false,
            unit: '',
            showinput: this.field?.focus ? true : false,
            time: null
        };
    },
    computed: {
        step() {
            return Object.keys(this.units).length > 0
                ? this.units[this.unit]?.step ?? 1
                : this.field.options?.step ?? 1;
        },
        dt(){
            if(this.value == '') {
                return 'none';
            }
            if (this.value == 'auto' || this.value == 'none') {
                return this.value;
            } else {
                if (this.unit) {
                    if (this.max !== '') {
                        if (this.value > this.max) {
                            return this.max + this.unit;
                        } else {
                            return this.value + this.unit;
                        }
                    } else {
                        return this.value + this.unit;
                    }
                } else {
                    return parseFloat(this.value);
                }
            }
        },
        defaultNum() {
            return this.field.options?.default;
        },
        min(){
            return Object.keys(this.units).length > 0
                ? this.units[this.unit]?.min ?? (this.useSlide ? 1 : '')
                : this.field.options?.min ?? (this.useSlide ? 1 : '');
        },
        max(){
            return Object.keys(this.units).length > 0
                ? this.units[this.unit]?.max ?? (this.useSlide ? 100 : '')
                : this.field.options?.max ?? (this.useSlide ? 100 : '');
        },
        percentProgressbar() {
            let per = Math.abs(this.value - this.min);
            return {
                width:
                    this.value == '' || this.value == 'auto' || this.value == 'none'
                        ? '0%'
                        : Math.round((per / (this.max - this.min)) * 100) + '%'
            };
        }
    },
    watch: {
        unit: function () {
            if (this.value !== 'auto') {
                if (this.min) {
                    if (this.value < this.min) {
                        this.value = this.min;
                    }
                }
                if (this.max) {
                    if (this.value > this.max) {
                        this.value = this.max;
                    }
                }
            }
        },
        units: {
            immediate: true,
            handler: function (val) {
                if (val) {
                    this.unit = Object.keys(val)[0];
                }
            }
        },
        data: {
            immediate: true,
            handler: function (val) {
                if (this.status) {
                    this.emit = false;
                    if (typeof val !== 'undefined') {
                        if (val.length > 0 && typeof val === 'string' && val !== 'auto') {
                            let v = val.match(/[.-\d]/g) ?? [];
                            this.value = parseFloat(v.join('')) ?? 0;
                            for (var i = 0; i < v.length; i++) {
                                val = val.replace(v[i], '');
                            }

                            let unit = val.replace(/\s/g, '');
                            if(this.units[unit]) {
                                this.unit = unit;
                            }
                            else
                            {
                                this.unit = Object.keys(this.units)[0];
                            }

                        } else {
                            if (val !== 'auto') {
                                this.unit = '';
                            }
                            this.value = val;
                        }
                    } else {
                        this.value = this.defaultNum ?? 'none';
                    }
                }
                this.status = true;
            }
        },
        dt: {
            immediate: true,
            handler: function (val) {
                if (this.emit) {
                    this.status = false;
                    this.$emit('update:data', val);
                }
                this.emit = true;
            }
        }
    },
    mounted() {
        if(this.$refs.input_slider) {
            this.$refs.input_slider.select();
        }
    },
    methods: {
        showInput() {
            this.showinput = true;
            this.focus = true;

            this.$nextTick(
                function () {
                    this.$refs.input_slider.select();
                }.bind(this)
            );
        },
        debounceInput(e) {

            let value = e.target.value.trim();
            const setValue = () => {
                if (this.min !== '' && this.max !== '' && value.length > 0) {
                    this.value = this.minMax(value, this.min, this.max);
                } else {
                    this.value = value;
                }
            }
            let numValue = parseFloat(value);
            if(!isNaN(numValue) && (numValue < this.min || numValue > this.max)) {
                this.time = setTimeout(() => {
                    setValue();
                    if(this.$refs?.input_default) this.$refs.input_default.value = this.value
                }, 500);

            }
            else {
                if(this.time) {
                    clearTimeout(this.time);
                }
                setValue();
            }

        },
        debounceInputSlider(e) {

            let value = e.target.value.trim();
            const setValue = () => {
                if (this.min !== '' && this.max !== '' && value.length > 0) {
                    this.value = this.minMax(value, this.min, this.max);
                } else {
                    this.value = value;
                }
            }
            setValue();
            this.showinput = false;

        },
        minMax(value, min, max) {
            if (parseFloat(value) < min || isNaN(parseFloat(value))) {
                {
                    return min;
                }
            } else if (parseFloat(value) > max) {
                {
                    return max;
                }
            } else {
                return value;
            }
        },
        chooseType(type) {
            this.unit = type;
        }
    }
};
</script>

<style>
.number-slide-per {
    max-width: calc(100% - 4px);
    z-index: 1;
}
.number-input-container {
    max-width: 75px;
}
input.ecom-slider-number[type='range']::-webkit-slider-runnable-track {
    height: 2px;
    width: calc(100% - 4px);
    background: #e5e7eb;
    background-size: 4px 4px;
}

input.ecom-slider-number[type='range']::-moz-range-track {
    height: 2px;
    width: calc(100% - 4px);
    background: #e5e7eb;
    background-size: 4px 4px;
}

html.dark input.ecom-slider-number[type='range']::-webkit-slider-runnable-track {
    background: #1f2937;
}

html.dark input.ecom-slider-number[type='range']::-moz-range-track {
    background: #1f2937;
}
input.ecom-slider-number[type='range'] {
    -webkit-appearance: none;
}
input.ecom-slider-number[type='range']::before {
    content: '';
    position: absolute;
    border-right: 4px solid #e5e7eb;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    height: 2px;
    border-radius: 2px;
}
html.dark input.ecom-slider-number[type='range']::before {
    border-right: 4px solid #1f2937;
}
input.ecom-slider-number[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 10px;
    width: 10px;
    backface-visibility: hidden;
    border-radius: 50%;
    background: currentColor;
    background-clip: padding-box;
    position: relative;
    z-index: 9;
    top: -4px;
    transition: all 0.25s ease;
}
input.ecom-slider-number[type='range']::-moz-range-thumb {
    -webkit-appearance: none;
    border: none;
    height: 14px;
    width: 14px;
    backface-visibility: hidden;
    border-radius: 50%;
    background: currentColor;
    background-clip: padding-box;
    position: relative;
    transition: all 0.25s ease;
}
input.ecom-slider-number[type='range']:focus::-webkit-slider-thumb {
    box-shadow: 0 0 0 1px rgba(69, 143, 255, 1);
    border: 2px solid transparent;
    transform: scale(1.2);
}
input.ecom-slider-number[type='range']:focus::-moz-range-thumb {
    box-shadow: 0 0 0 1px rgba(69, 143, 255, 1);
    border: 2px solid transparent;
    transform: scale(1.2);
}
input.ecom-slider-number[type='range']:active::-webkit-slider-thumb {
    transform: scale(1.8);
    cursor: grabbing;
}
input.ecom-slider-number[type='range']:active::-moz-range-thumb {
    transform: scale(1.8);
    cursor: grabbing;
}
.slider-number-wrp:hover .sitooltip{
    color: #000;
    text-decoration: underline;
}
html.dark .slider-number-wrp:hover .sitooltip{
    color: #d1d5da;
}
</style>