<template>
    <PickerFrame
    :show="show"
    :header="headerFrame"
    :position="position"
    @close="show = false"
>
    <template #content>
        <div class="absolute top-0 left-0 w-full h-full bg-white dark:bg-gray-700 dark:text-dark-color overflow-y-auto overflow-x-hidden pb-0.5">
            <div class="p-4">
                <label
                    v-if="isEditor"
                    class="block mb-4 text-sm text-gray-700 dark:text-dark-color"
                >
                    <span class="block mb-0.5">Text</span>
                    <input
                        v-model="value.text"
                        type="text"
                        class="w-full h-8 px-3 py-2 border-gray-300 rounded shadow-sm rtransition sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
                        tabindex="0"
                    >
                </label>
                <label class="relative block mb-4 text-sm text-gray-700 dark:text-dark-color">
                    <span class="block mb-0.5">Link</span>
                    <span class="flex items-center">
                        <span
                            v-clickOutside="blurGroupTypes"
                            class="w-full"
                        >
                            <input
                                v-model="value.href"
                                type="text"
                                class="w-full h-8 px-3 py-2 dark:bg-dark dark:text-dark-color border-gray-300 rounded shadow-sm rtransition sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
                                placeholder="Paste a link"
                                tabindex="1"
                                @focus="focusInputLink"
                            >
                            <GroupTypes
                                ref="group-type"
                                :style="styleGroupTyles"
                                :src="value?.href ?? ''"
                                @update:select="updateSelect"
                            />
                        </span>
                        <button
                            type="button"
                            class="ml-1 h-8 bg-white dark:bg-dark dark:text-dark-color border-md border p-2.5 rounded-md border-gray-300 flex items-center justify-center hover:bg-gray-100 transition"
                            :class="{'bg-gray-100 opacity-50' : disabled}"
                            :disabled="disabled"
                            tabindex="2"
                            @click="value.href = ''"
                        >
                            <ecom-icon :icon="icons.faXmark"
                                class="w-4 h-4 p-px text-white bg-gray-600 rounded-full"
                            />
                        </button>
                    </span>
                    <!-- <p class="mt-1 text-gray-400 text-xss">https:// is required for external links</p> -->
                </label>
                <label class="relative block mb-4 text-sm text-gray-700 dark:text-dark-color">
                    <span class="block mb-0.5">Link title</span>
                    <input
                        v-model="value.title"
                        type="text"
                        class="w-full h-8 px-3 py-2 border-gray-300 dark:bg-dark dark:text-dark-color rounded shadow-sm rtransition sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
                        tabindex="3"
                    >
                    <p class="mt-1 text-gray-400 text-xss">Used for accessibility and SEO</p>
                </label>
                <label class="inline-flex items-center py-1 mb-1 cursor-pointer select-none">
                    <span class="relative mr-1.5 min-w-5 h-5 border border-transparent">
                        <input
                            v-model="value.target"
                            class="absolute top-0 left-0 w-full h-full dark:bg-dark dark:text-dark-color opacity-0 z-1"
                            type="checkbox"
                            true-value="_blank"
                            false-value=""
                            tabindex="4"
                        >
                        <span
                            class="block w-full h-full border border-gray-300 rounded"
                        >
                            <ecom-icon :icon="icons.faCheck"
                                class="absolute top-0 left-0 w-full h-full text-white transition duration-75 transform rounded bg-primary-600"
                                :class="newTab ? 'scale-100' : 'scale-0'"
                            />
                        </span>
                    </span>

                    <span class="text-sm text-gray-700 dark:text-dark-color">Open this link in a new window</span>
                </label>
                <label class="inline-flex items-center py-1 mb-4 cursor-pointer select-none">
                    <span class="relative mr-1.5 min-w-5 h-5 border border-transparent">
                        <input
                            v-model="value.rel"
                            class="absolute dark:bg-dark dark:text-dark-color top-0 left-0 w-full h-full opacity-0 z-1"
                            type="checkbox"
                            true-value="nofollow"
                            false-value=""
                            tabindex="4"
                        >
                        <span
                            class="block w-full h-full border border-gray-300 rounded"
                        >
                           <ecom-icon :icon="icons.faCheck"
                                class="absolute top-0 left-0 w-full h-full text-white transition duration-75 transform rounded bg-primary-600"
                                :class="nofollow ? 'scale-100' : 'scale-0'"
                            />
                        </span>
                    </span>

                    <span class="text-sm text-gray-700 dark:text-dark-color">Add nofollow</span>
                </label>
                <label class="relative block mb-4 text-sm text-gray-700 dark:text-dark-color">
                    <span class="block mb-0.5">Custom attributes</span>
                    <input
                        v-model="attributes"
                        type="text"
                        placeholder="key|value, key2|value2"
                        class="dark:bg-dark dark:text-dark-color w-full px-3 py-2 border-gray-300 rounded shadow-sm h-9 rtransition sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
                        tabindex="3"
                    >
                </label>
            </div>
            <div
                v-if="isShowRemoveLink"
                class="mx-4 my-5"
            >
                <Button
                    :text="'Remove link'"
                    @click="removeLink"
                />
            </div>
        </div>
    </template>
    <template #footer>
      <PickerFooter
        :is-change="isChange"
        :text-select="textSelect"
        @clickCancel="show = false"
        @clickSelect="updateLink"
      />
    </template>
  </PickerFrame>
</template>

<script lang="ts">

    import PickerFrame from '../../picker/PickerFrame.vue';
    import PickerFooter from '../../picker/PickerFooter.vue';
    import GroupTypes from './Types.vue';
    import { faCheck,faXmark} from '@fortawesome/pro-light-svg-icons';
    import { clickOutside } from '../../../directives';

    export default {
        name: 'InsertLink',
        directives: {
            clickOutside
        },
        components: {
            PickerFrame,
            PickerFooter,
            GroupTypes,

        },
        props: {
            data: {
                type: Object,
                default(){
                    return  {};
                }
            },
            type: {
                type: String,
                default: 'editor'
            },
            position: {
                type: String,
                default: 'absolute'
            }
        },
        emits: ['close_popup', 'update:link', 'update:remove-link'],
        data(){
            return {
                show: false,
                value: {},
                attributes: '',
                styleGroupTyles: {},
                icons: { faCheck,faXmark}
            };
        },
        computed: {
            isEditor(){
                return this.type === 'editor';
            },
            headerFrame(){
                return {
                    title: this.isEditor ? 'Insert link' : 'Link settings',
                    search: {
                        show: false,
                        value: ''
                    }
                };
            },
            textSelect(){
                if(this.isEditor ){
                    if(this.data.href){
                        return 'Update';
                    }
                    return 'Insert';
                }
                return 'Done';
            },
            disabled(){
                return !this.value?.href?.trim();
            },
            newTab(){
                return this.value?.target == '_blank';
            },
            nofollow(){
                return this.value?.rel == 'nofollow';
            },
            objAttributes(){
                let result = {};
                if(this.attributes){
                    let p = this.attributes.split(',');
                    p.forEach(item => {
                        let [key, value] = item.split('|');
                        if(key && value){
                            result[key.trim()] = value.trim();
                        }
                    });
                }
                return result;
            },
            valueFinal(){
                let v = {...this.value, ...this.objAttributes};
                let igroneKey = ['href','title','rel','target','text', ...Object.keys(this.objAttributes)];
                let deleteKey = Object.keys(v).filter(k => igroneKey.indexOf(k) === -1);
                deleteKey.forEach(k => delete v[k]);
                return v;
            },
            html(){
                let node = document.createElement('a');
                let value = this.valueFinal;
                let key;
                for(key in value){
                    if(value[key] != '' && key != 'text') {
                        node.setAttribute(key, value[key]);
                    }
                }
                node.innerText = this.value['text'];
                return node;
            },
            isShowRemoveLink(){
                return this.data?.href && this.isEditor;
            },
            isChange(){
                let isChange = ('href' in this.value &&
                        this.value.href.trim() != '');
                if(this.isEditor){
                    isChange &= ('text' in this.value &&
                        this.value.text.trim() != '');
                }

                return !!isChange;
            }
        },
        watch: {
            show: function(val){
                if(val){
                    this.value = this.copy(this.data);
                }else{
                    this.$emit('close_popup');
                }
            },
            data: {
                deep: true,
                immediate: true,
                handler: function(val){
                    this.attributes = this.getAttrCustom(val);
                }
            }
        },
        methods: {
            copy(obj){
                return JSON.parse(JSON.stringify(obj));
            },
            getAttrCustom(data){
                let keysIgrone = ['title', 'href', 'rel', 'target', 'text'],
                    keys = Object.keys(data),
                    keyAccept = keys.filter(key => keysIgrone.indexOf(key) === -1);
                return keyAccept.map(key => key + '|' + data[key]).join(',');
            },
            blurGroupTypes(){
                this.$refs['group-type'].showFull = false;
            },
            focusInputLink(e){
                let $groupType = this.$refs['group-type'];
                if(!$groupType.showFull){
                    let target = e.target;
                    let { top, height } = target.getBoundingClientRect(),
                        sh = window.innerHeight,
                        t = sh - top - height - 10,
                        mh;
                    if(top < t) {
                        mh = sh - (top + height ) - 90;
                        mh = mh < 0 ? 0 : (mh > 350 ? 350 : mh);
                        this.styleGroupTyles = {
                            top: `calc(${ top + height - 52 }px)`,
                            maxHeight: `${ mh }px`
                        };
                    }
                    else {
                        mh = top - 82;
                        mh = mh < 0 ? 0 : (mh > 350 ? 350 : mh);
                        this.styleGroupTyles = {
                            bottom: `calc(100vh - ${ top - 10 }px)`,
                            maxHeight: `${ mh }px`
                        };
                    }
                    $groupType.show = true;
                    $groupType.showFull = true;
                }

            },
            updateSelect(value, type){
                if(type == 'email'){
                    this.value.href = 'mailto:' + value;
                    this.$refs['group-type'].showFull = false;
                    return;
                }
                if(type == 'phone'){
                    this.value.href = 'tel:' + value;
                    this.$refs['group-type'].showFull = false;
                    return;
                }
                this.value.href = '/' + value;
                this.$refs['group-type'].showFull = false;
            },
            updateLink(){
                if(this.isEditor){
                    this.$emit('update:link', {
                        data: this.html,
                        update: 'href' in this.data
                    });
                }
                else {
                    this.$emit('update:link', this.valueFinal);
                }

                this.show = false;
            },
            removeLink(){
                this.$emit('update:remove-link');
                this.show = false;
            }
        }

    };
</script>
