export default {
    methods: {
        dragDir(type, dir) {
            let c = {
                padding: {
                    top: {
                        c: 's-resize',
                        d: 1
                    },
                    right: {
                        c: 'w-resize',
                        d: 0
                    },
                    bottom: {
                        c: 'n-resize',
                        d: 0
                    },
                    left: {
                        c: 'e-resize',
                        d: 1
                    }
                },
                margin: {
                    top: {
                        c: 'n-resize',
                        d: 0
                    },
                    right: {
                        c: 'e-resize',
                        d: 1
                    },
                    bottom: {
                        c: 's-resize',
                        d: 1
                    },
                    left: {
                        c: 'w-resize',
                        d: 0
                    }
                },
                'border-radius': {
                    top: {
                        c: 'n-resize',
                        d: 0
                    },
                    right: {
                        c: 'n-resize',
                        d: 0
                    },
                    bottom: {
                        c: 's-resize',
                        d: 1
                    },
                    left: {
                        c: 's-resize',
                        d: 1
                    }
                }
            };
            return c[type]?.[dir] ?? c['margin'][dir] ?? {
                c: 's-resize',
                d: 1
            };
        }
    }
};