<template>
    <div class="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 bg-white dark:bg-dark dark:text-dark-color diagonal rounded h-45 text-gray-100 select-none"
            :class="size === 'large' ? 'h-40 w-full' : 'h-22'"
        >
        <div v-if="size === 'small' && !options?.simple && options.type !== 'radius'" class="absolute inset-1 border border-primary-600 -m-1 rounded" />
        <div v-if="size === 'small' && !options?.simple && options.type !== 'radius'" class="absolute border border-primary-600 transform top-1/2 -translate-y-1/2 w-3 h-3 z-10 bg-white dark:bg-dark dark:text-dark-color -left-1.5" />
        <div v-if="size === 'small' && !options?.simple && options.type !== 'radius'" class="absolute border border-primary-600 transform top-1/2 -translate-y-1/2 w-3 h-3 z-10 bg-white dark:bg-dark dark:text-dark-color -right-1.5" />
        <div v-if="size === 'small' && !options?.simple && options.type !== 'radius'" class="absolute border border-primary-600 transform -top-1.5 -translate-x-1/2 w-3 h-3 z-10 bg-white dark:bg-dark dark:text-dark-color left-1/2" />
        <div v-if="size === 'small' && !options?.simple && options.type !== 'radius'" class="absolute border border-primary-600 transform -bottom-1.5 -translate-x-1/2 w-3 h-3 z-10 bg-white dark:bg-dark dark:text-dark-color left-1/2" />

        <span
            v-if="type"
            class="absolute text-xxs text-gray-500 dark:text-dark-color z-2 uppercase"
            :class="type == 'padding' ? 'bottom-1 right-3' : 'top-3 left-4'"
            v-html="type"
        />
        <div
            v-if="!options?.simple && options.type !== 'radius'"
        >
            <div
                v-for="name in names" :key="name"
                class="absolute rounded border inset-0 transition z-1"
                :class="[size === 'small' ? 'border-primary-600' : 'border-dashed border-gray-400', classDirSpacing(name)]"
                :style="styleBgInput(name)"
                @mousedown.stop="mouseDown($event, name)"
            >
                <span
                    data-tooltip="Click to enter number"
                    data-tooltip-location="top"
                    class="input-number absolute transform dark:text-white  z-5 p-2 cursor-pointer text-center focus:outline-none bg-transparent focus:ring-0 text-xs leading-none text-form-secondary"
                    :class="[classInput(name), options?.simple ? 'w-8' : '']"
                    v-html="values[name] == undefined || values[name] == 'none' ? '-' : values[name]"
                />
            </div>
        </div>
        <div v-else>
            <div
                v-for="name in names" :key="name"
                class="absolute rounded border border-gray-300 dark:border-gray-600 inset-0 transition z-1"
                :class="classDir(name)"
                :style="styleBgInput(name)"
                @mousedown.stop="mouseDown($event, name)"
            >
                <span
                    data-tooltip="Click to enter number"
                    data-tooltip-location="top"
                    class="input-number absolute transform  dark:text-white z-5 p-2 cursor-pointer text-center focus:outline-none bg-transparent focus:ring-0 text-xs leading-none text-form-secondary"
                    :class="[classInput(name), options?.simple ? 'w-8' : '']"
                    v-html="values[name] == undefined || values[name] == 'none' ? '-' : values[name]"
                />
            </div>
        </div>

    </div>
</template>

<script>
    import mixin from './mixin.js';
    export default {
        name: 'Box',
        mixins: [mixin],
        props: {
            type: {
                type: String,
                default: ''
            },
            current: {
                type: Object,
                default() {
                    return {};
                }
            },
            unit: {
                type: String,
                default: ''
            },
            size: {
                type: String,
                default: 'large'
            },
            data: {
                type: Object,
                default(){ return  {};}
            },
            unitConfigs: Object,
            linked: Boolean,
            field: {
                type: Object,
                default() { return {} ;}
            }
        },
        emits: ['update:data', 'update:current', 'click-input', 'close-input'],
        data() {
            return  {
                el: null,
                isDrag: false,
                p: 0,
                s: 0,
                oriValue: 0,
                values: {},
                isWatch: true,
                isEmit: false,
                itv: null
            };
        },
        computed: {
            names() {
                    return ['top','left','bottom', 'right'];
            },
            _current: {
                get() {
                    return this.current;
                },
                set(val){
                    this.$emit('update:current', val);
                }
            },
            options() {
                return this.field.options ?? {};
            },
            isRadius() {
                return this.options.type === 'radius';
            },
            isSimple() {
                return this.isRadius || this.options.simple;
            },
            dragDirection() {
                return (this._current?.name === 'top' || this._current.name === 'bottom' || this.isRadius) ? 'clientY' : 'clientX';
            },
            fieldName() {
                return Object.keys(this.field.css?.properties ?? {})[0] ?? this.field.name;
            }
        },
        watch: {
            data: {
                deep: true,
                immediate: true,
                handler(val) {
                    if(this.isWatch){
                        this.isEmit = false;
                        this.values = this.$helpers.copy(val) ?? {};
                    }
                    this.isWatch = true;
                }
            },
            values: {
                deep: true,
                handler(val) {
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', this.$helpers.copy(val));
                    }
                    this.isEmit = true;
                }
            },
            linked() {
                this.toggleLink();
            }
        },
        created () {
            this.names.forEach(name => {
                let _this = this;
                _this.$watch('values.' +name , function(val){
                    clearTimeout(_this.itv);
                    _this.itv = setTimeout(function(){
                        if(_this.linked){
                            _this.setValueLinked(val, true);
                        }
                        _this.$emit('update:data', _this.$helpers.copy(_this.values));
                    }, 10);

                });
            });
        },
        methods: {
            classDir(name) {
                if(this._current.name === name && this._current.type === this.type){
                    return 'bg-coolGray-50 dark:bg-gray-700';
                }
                switch(name) {
                    case 'top':
                    case 'bottom':
                        return 'bg-coolGray-50 dark:bg-gray-500 dark:text-dark-color hover:bg-coolGray-50';
                    default:
                        return 'bg-white dark:bg-gray-600 dark:text-dark-color hover:bg-coolGray-50';
                }
            },
            classDirSpacing(name) {
                if(this._current.name === name && this._current.type === this.type){
                    return 'bg-coolGray-50 dark:bg-gray-700';
                }
                switch(name) {
                    case 'top':
                    case 'bottom':
                        return 'bg-coolGray-50 dark:bg-gray-500 hover:bg-coolGray-50';
                    default:
                        return 'bg-white dark:bg-gray-600 dark:text-dark-color hover:bg-coolGray-50';
                }
            },
            styleBgInput(name) {
                switch(name) {
                    case 'top':
                        return !this.isRadius ?
                                { 'clip-path': 'polygon(0 0, 100% 0, 50% 50%)', cursor: this.dragDir(this.type || this.fieldName, 'top').c  } :
                                { 'clip-path': 'polygon(0 0, 50% 0, 50% 50%, 0 50%)', cursor: 'n-resize' };
                    case 'left':
                        return !this.isRadius ?
                                { 'clip-path': 'polygon(0 0, 50% 50%, 0% 100%)', cursor: this.dragDir(this.type || this.fieldName, 'left').c  } :
                                { 'clip-path': 'polygon(0 50%, 50% 50%, 50% 100%, 0 100%)', cursor: 's-resize' };
                    case 'bottom':
                        return !this.isRadius ?
                                { 'clip-path': 'polygon(0 100%, 50% 50%, 100% 100%)', cursor: this.dragDir(this.type || this.fieldName, 'bottom').c  } :
                                { 'clip-path': 'polygon(50% 50%, 100% 50%, 100% 100%, 50% 100%)', cursor: 's-resize' };
                    case 'right':
                        return !this.isRadius ?
                                { 'clip-path': 'polygon(100% 0, 50% 50%, 100% 100%)', cursor: this.dragDir(this.type || this.fieldName, 'right').c  } :
                                { 'clip-path': 'polygon(50% 0, 100% 0, 100% 50%, 50% 50%)', cursor: 'n-resize' };
                }
            },
            classInput(name) {
                switch(name) {
                    case 'top':
                        return !this.isRadius ?
                                ((this.type === 'margin' ? 'top-1.5' : 'top-1') + ' left-1/2 -translate-x-1/2') :
                                'top-1/4 left-12 -translate-x-1/2 -translate-y-1/2';
                    case 'left':
                        return !this.isRadius ?
                                ((this.options?.simple ? 'left-12' : 'left-7') + ' top-1/2 -translate-y-1/2 -translate-x-1/2') :
                                'top-3/4 left-12 -translate-x-1/2 -translate-y-1/2';
                    case 'bottom':
                        return !this.isRadius ?
                                ((this.type === 'margin' ? 'bottom-1.5' : 'bottom-1') + ' left-1/2 -translate-x-1/2') :
                                'top-3/4 right-12 translate-x-1/2 -translate-y-1/2';
                    case 'right':
                        return !this.isRadius ?
                                ((this.options?.simple ? 'right-12' : 'right-7') + ' top-1/2 -translate-y-1/2 translate-x-1/2') :
                                'top-1/4 right-12 translate-x-1/2 -translate-y-1/2';
                }
            },
            mouseDown(e, name) {
                this.isDrag = false;
                this.el = e.target;
                this._current = {name, type: this.type};
                this.oriValue = (this.values[name] !== undefined &&
                                    this.values[name] !== null &&
                                    this.values[name] !== 'auto') ?
                                        this.values[name] :
                                        0;
                this.p = e[name === 'top' || name === 'bottom' || this.isRadius ? 'clientY' : 'clientX'];
                document.onmouseup =  this.onMouseUp;
                document.onmousemove =  this.onMouseMove;
            },
            onMouseMove(e){
                let newP = e[this.dragDirection];
                this.s += Math.abs(newP - this.p);
                if(this.s > 3) {
                    this.isDrag = true;
                    this.el = null;
                    let v = !this.dragDir(this._current.type || this.fieldName, this._current.name).d ?
                            ((this.p - newP) * this.unitConfigs.step) :
                            ((newP - this.p) * this.unitConfigs.step);
                    v = Math.round(v * 0.8);
                    this.values[this._current.name] = this.oriValue +  Math.round(v * 100) / 100;
                    this.values[this._current.name] = Math.max(this.unitConfigs.min,
                        Math.min(this.unitConfigs.max, this.values[this._current.name])
                    );
                }

            },
            onMouseUp() {
                if(!this.isDrag && this.el && this.el.classList.contains('input-number')) {
                    this.$emit('click-input', { value: this.values[this._current.name], el: this.el});
                }
                else {
                    this._current = {};
                    this.$emit('close-input');
                }
                this.s = 0;
                this.el = null;
                document.onmouseup = null;
                document.onmousemove = null;
            },
            setValueLinked(val, overwrite = false){
                //if(val === undefined) {return ;}
                let values;
                this.names
                    .forEach(dir => {
                        values = this.values[dir];
                        if(overwrite || (!overwrite && values === undefined)){
                            this.values[dir] = val;
                        }

                    });
            },
            toggleLink(){
                if(this.linked){
                    const firstValue = (() => {
                        let v = undefined;
                        for(let i in this.names){
                            let vn = this.values[this.names[i]];
                            if(vn !== undefined && vn !== null && vn !== '') {
                                v = vn;
                                break;
                            }
                        }
                        return v;
                    })();
                    this.isEmit = true;
                    this.setValueLinked(firstValue,
                                        true);
                }
            }
        }
    };
</script>
