<template>
    <div class="inline-block bg-white text-white rounded w-68 shadow-popup dark:bg-gray-700 dark:text-gray-700">
        <svg
            viewBox="0 0 213.3 213.3"
            class="absolute top-0 w-4 h-4 -mt-3 transform rotate-180 fill-current"
            :class="!posArrow ? 'mr-1 left-8' : '-translate-x-1/2'"
            :style="posArrow ? {left: posArrow} : null"
        >
            <polygon
                class="shadow-xxl"
                points="0,53.333 106.667,160 213.333,53.333"
            />
        </svg>
        <Tabs
            v-if="global && global.type"
            title="Color picker"
            :tabs="['customize', 'global']"
            :has-delete="hasDelete"
            :value="value"
            :type="global?.type"
            :selected="globalSelected"
            @addglobal="addNewGlobalColor"
            @update:delete="$emit('update:delete')"
            @refesh="$emit('refeshColor')"
        >
            <template #global>
                <Global
                    :global="global"
                    :selected="globalSelected"
                    @update:selected="$emit('update:global-selected', $event)"
                />
            </template>
            <template #customize>
                <ColorPicker :value="value" @update:data="$emit('update:value', $event)" />
            </template>
        </Tabs>
        <div v-else>
            <div
                class="flex items-center justify-between px-4 py mb-2 border-b dark:border-gray-600 dark:text-dark-color"
            >
                <span class="text-sm font-medium text-form-title dark:text-dark-color">{{
                    field?.label
                }}</span>
                <span class="flex items-center">
                    <button
                        class="p-2 transition rounded text-form-secondary hover:bg-gray-200 hover:text-form-secondary-hover dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-600"
                        data-tooltip="Reset to default"
                        data-tooltip-location="top-right"
                        type="button"
                        @click="$emit('refeshColor')"
                    >
                        <ecom-icon :icon="icons.faArrowsRotate" class="w-5 h-5" />
                    </button>
                </span>
            </div>
            <ColorPicker :value="value" @update:data="$emit('update:value', $event)" />
        </div>
    </div>
</template>

<script lang="ts">
   import {faArrowsRotate} from '@fortawesome/pro-light-svg-icons'

    import Tabs from '../Tabs/Tabs.vue';
    import Global from '../GlobalPanel/GlobalPanel.vue';
    import ColorPicker from './ColorPicker.vue';

    export default {
        name: 'ColorComponent',
        components: {
            Tabs,
            Global,
            ColorPicker,
        },
        props: {
            value: {
                type: String,
                default: ''
            },
            field: {
                type: Object,
                default() {
                    return {};
                }
            },
            globalSelected: {
                type: String,
                default: ''
            },
            global: {
                type: Object,
                default() {
                    return {};
                }
            },
            posArrow: {
                type: String,
                default: ''
            },
            hasDelete: {
                type: Boolean,
                default: false
            }
        },
        data(){
            return {
                icons: {faArrowsRotate}
            }
        },
        emits: ['refeshColor', 'update:value', 'update:global-selected', 'update:delete'],
        methods: {
            addNewGlobalColor(title) {
                let key = this.$helpers.makeId(8);
                this.$store.dispatch('settings/addDataGlobal', {
                    key,
                    type: this.global?.type,
                    data: {
                        title: title,
                        value: this.$helpers.copy(this.value)
                    }
                });
                this.$emit('update:global-selected', key);
                this.$store.commit(
                    'global/flash',
                    {
                        open: true,
                        message:
                            'Add Global ' +
                            (this.global?.type === 'colors' ? 'Color' : 'Font') +
                            ' Success',
                        type: 'success'
                    },
                    { root: true }
                );
            },
        }
    };
</script>
