<template>
    <div>
        <div class="flex items-center">
            <label class="flex-1 pr-2">
                <Number
                    :field="fieldNumber('W')"
                    :data="value?.width"
                    @update:data="updateNumber('width', $event)"
                />
            </label>
            <span
                v-if="ratio !== undefined"
                class="relative w-3.5 h-3.5 mr-2 text-gray-600 dark:text-dark-color dark:hover:text-primary-600 cursor-pointer group hover:text-gray-800"
                @click="aspect = !aspect"
            >
                <svg
                    v-if="aspect"
                    class="w-full h-full"
                    fill="currentColor" viewBox="0 0 36 36"
                >
                    <path d="M31.7 4.3a7.176 7.176 0 00-10.148 0c-.385.386-4.264 4.222-5.351 5.309a8.307 8.307 0 013.743.607c.519-.52 3.568-3.526 3.783-3.741a4.1 4.1 0 015.8 5.8l-7.119 7.115a4.617 4.617 0 01-3.372 1.3 3.953 3.953 0 01-2.7-1.109 4.154 4.154 0 01-1.241-1.626 2.067 2.067 0 00-.428.318l-1.635 1.712a7.144 7.144 0 001.226 1.673c2.8 2.8 7.875 2.364 10.677-.438l6.765-6.768a7.174 7.174 0 000-10.152z" />
                    <path d="M15.926 25.824c-.52.52-3.5 3.547-3.713 3.762a4.1 4.1 0 11-5.8-5.8L13.6 16.6a4.58 4.58 0 013.366-1.292 4.2 4.2 0 013.784 2.782 2.067 2.067 0 00.428-.318l1.734-1.721a7.165 7.165 0 00-1.226-1.673 7.311 7.311 0 00-10.26.048l-7.187 7.186a7.176 7.176 0 0010.148 10.149c.386-.386 4.194-4.243 5.281-5.33a8.3 8.3 0 01-3.742-.607z" />
                </svg>
                <svg
                    v-else
                    class="w-full h-full"
                    fill="currentColor" viewBox="0 0 36 36"
                >
                    <path d="M11.136 9.523l-1.496 1.44-5.328-5.24 1.496-1.439 5.328 5.239zm20.665 20.754l-1.496 1.439-5.299-5.334 1.495-1.439 5.3 5.334zM11.057 1.8h2.314v4.629h-2.314zM1.8 11.057h4.629v2.314H1.8zm27.771 11.572H34.2v2.314h-4.629zm-6.942 6.942h2.314V34.2h-2.314zm-4.576-5.863l-5.84 5.878a4.1 4.1 0 11-5.8-5.8l5.858-5.859-2.171-2.173-5.861 5.858A7.176 7.176 0 0014.388 31.76l5.841-5.874zm-.141-11.452l5.81-5.777a4.1 4.1 0 015.8 5.8l-5.793 5.793 2.171 2.174 5.8-5.793A7.176 7.176 0 1021.547 4.3l-5.807 5.78z" />
                </svg>
                <span class="absolute px-2 py-1 z-10 mb-1 text-sm text-white transition transform -translate-x-1/2 bg-gray-700 rounded opacity-0 pointer-events-none select-none left-1/2 bottom-full w-max group-hover:opacity-100">
                    <template v-if="aspect">
                        Unlock aspect
                    </template>
                    <template v-else>
                        Lock aspect
                    </template>
                </span>
            </span>
            <label class="flex-1 pr-2">
                <Number
                    :field="fieldNumber('H')"
                    :data="value?.height"
                    @update:data="updateNumber('height', $event)"
                />
            </label>

            <select
                class="relative pl-2 pr-8 py-0.5 transition border-gray-300 rounded shadow-sm h-7-5 sm:text-sm focus:ring-inset focus:ring-1 focus:ring-primary-400 dark:focus:ring-transparent focus:border-primary-400 text-black dark:text-dark-color dark:bg-dark dark:border-gray-600"
                :value="value?.crop || 'center'"
                @input="value.crop = $event.target.value"
            >
                <option value="center">
                    Center
                </option>
                <option value="top">
                    Top
                </option>
                <option value="bottom">
                    Bottom
                </option>
                <option value="left">
                    Left
                </option>
                <option value="right">
                    Right
                </option>
            </select>
        </div>
        <Fields
            class="mt-4 -mb-4"
            :fields="fields"
            :freeform="true"
            v-model:data="value"
        />
    </div>
</template>
<script lang="ts">
    import { defineAsyncComponent } from 'vue';
    import Number from '../number/Number.vue';
    export default {
        name: 'SizeField',
        components: {
            Number,
            Fields: defineAsyncComponent(()=> import('../Fields.vue'))
        },
        props: {
            data: {
                type: Object,
                default() {
                    return {};
                }
            },
            field: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data() {
            return {
                value: {},
                aspect: true,
                isWatch: true,
                isEmit: false
            };
        },
        computed: {
            fields() {
                return [
                    {
                        type: 'toggle',
                        name: 'format',
                        label: 'Progressive <span class="uppercase">JPGE</span>',
                        options: {
                            values: {
                                on: {
                                    value: 'pjpg'
                                },
                                off: {
                                    value: ''
                                }
                            }
                        }

                    }
                ];
            },
            ratio() {
                return this.field?.options?.ratio;
            }
        },
        watch: {
            data: {
                deep: true,
                immediate: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.value = this.$helpers.copy(val ?? {});
                    }
                    this.isWatch = true;
                }
            },
            value: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }
        },
        methods: {
            fieldNumber(placeholder) {
                return {
                    placeholder,
                    options: {
                        control_width: 'auto',
                        slider: false,
                        min: 0
                    }
                };
            },
            updateNumber(key, value) {
                if(value !== '' &&
                    value !== undefined &&
                    value !== null &&
                    !isNaN(value)) {
                        this.value[key] = value;
                    }
                else {
                    delete this.value?.[key];
                }
            },
            updateData(key, value) {
                if(value) {
                    this.value[key] = value;
                }
                else {
                    delete this.value?.[key];
                }
            }
        }
    };
</script>
