                                    <svg class="stroke-current" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="32" height="32" viewBox="0 0 32 32"><g id="icomoon-ignore"></g><path d="M10.722 9.969l-0.754 0.754 5.278 5.278-5.253 5.253 0.754 0.754 5.253-5.253 5.253 5.253 0.754-0.754-5.253-5.253 5.278-5.278-0.754-0.754-5.278 5.278z" fill="#000000"></path>
                                    </svg>
                                </span>
                            </div>
                        </div>
                        <div class="overflow-y-auto">
                            <div class="">
                                <section class="p-5">
                                    <div class="flex w-full rounded-t overflow-hidden">
                                        <div class="ecom-fields__focal-media w-full">
                                            <div class="ecom-fields__focal-image relative flex w-full justify-center overflow-hidden"
                                                ref="flag"
                                            >
                                                <div 
                                                    ref="focal__point"
                                                    :style="{left: position_focal.left ? position_focal.left + '%' : values[0].left ? values[0].left + '%' : '50%', top: position_focal.top ? position_focal.top + '%' : values[0].top ? values[0].top + '%' : '50%' , transform: position_focal.transform}"
                                                    class="focal-point"
                                                    :class="values[0].top && values[0].left ? '' : 'hidden'"
                                                    @mousedown="dragFocalPoint"
