<script lang="ts">
import { Popmenu } from '@/mixins';

import { faCheck, faTrash} from '@fortawesome/pro-light-svg-icons';
import { dragMixin, fileMixin } from '../../mixins';
import PickerModify from './PickerModify.vue';
export default {
    name: 'PickerPreview',
    components: {

        PickerModify
    },
    mixins: [Popmenu, dragMixin, fileMixin],
    props: {
        valueProp: {
            type: Array,
            default() {
                return [];
            }
        },
        options: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    emits: ['update:temp', 'update:image', 'select-item', 'update:modify'],
    data() {
        return {
            index: -1,
            drag: {
                axis: 'y'
            },
            showEditAlt: this.options.multiple,
            currentData: {},
            size: {},
            dragging: false,
            icons: { faCheck, faTrash},
            position_focal: {
                top: 0,
                left: 0,
                transform: 'translate(-50%, -50%)'
            }
        };
    },
    computed: {
        values: {
            get() {
                return this.valueProp;
            },
            set(val) {
                this.$emit('update:image', val);
            }
        },
        isSingle() {
            return !this.options.multiple;
        },
        checkShopifyPicker() {
            return ['menu', 'blog', 'article', 'product', 'collection'].indexOf(this.options.type) >
                -1
                ? true
                : false;
        },
        strSize() {
            let str = '';
            if ('width' in this.size) {
                return this.size.width + ' x ' + this.size.height;
            }
            return str;
        }
    },
    watch: {
        values: {
            deep: true,
            immediate: true,
            handler() {
                this.getSizeImage();
            }
        }
    },
    methods: {
        setValuePosition(top, left) {
            this.position_focal.top = top;
            this.position_focal.left = left;
        },
        setPosition(e) {
            const image = this.$refs.image_popup;
            if(!image) return;
            const { left, bottom, width, height} = image.getBoundingClientRect();
            let x = e.clientX;
            let y = e.clientY;
            let percentLeft = (x - left) / width * 100;
            let percentTop = 100 - (bottom - y) / height * 100;
            this.setValuePosition(percentTop, percentLeft);
        },
        dragFocalPoint(e) {
            e.preventDefault();
            e.stopPropagation();
            this.$refs.image_popup.addEventListener('mousemove', this.setPosition)
        },
        onMouseUp(e){
            e.stopPropagation()
            this.$refs.image_popup.removeEventListener('mousemove', this.setPosition)
        },
        changePositionFocalPoint(e) {
            const el_focal_point = this.$refs.focal__point;
            if(!el_focal_point) return;
            el_focal_point.classList.remove('hidden');
            this.$refs.btn_enable.innerHTML = 'Remove focal point';
            this.setPosition(e);
        },
        saveSet() {
            //save position focal point
            this.valueProp[0].top  = this.position_focal.top;
            this.valueProp[0].left  = this.position_focal.left;
            //save alt image
            let value = this.$refs['inputEditAlt'].value;
            this.values[0].name = value;
            this.showEditAlt = false;
            this.isopen = false;
        },
        cancelSet() {
            this.position_focal.top  = this.values[0].top;
            this.position_focal.left  = this.values[0].left;
            this.isopen = false
        },
        mouseOverBtn() {
            const el_focal_point = this.$refs.focal__point;
            if(!el_focal_point) return;
            this.position_focal.transform += 'scale(1.5)';
        },
        mouseOutBtn() {
            const el_focal_point = this.$refs.focal__point;
            if(!el_focal_point) return;
            this.position_focal.transform = 'translate(-50%, -50%)';
        },
        enableFocalPoint(e) {
            const el_focal_point = this.$refs.focal__point;
            if(! el_focal_point) return;
            let check_focal = el_focal_point.classList.contains('hidden');
            if(check_focal) {
                this.setValuePosition(50, 50);
                el_focal_point.classList.remove('hidden');
                e.target.innerHTML = 'Remove focal point';
                return ;
            }
            el_focal_point.classList.add('hidden');
            e.target.innerHTML = 'Add focal point';
            this.setValuePosition(null, null);
            
        },
        classItem() {
            let cl = 'w-full ';
            if (!this.isSingle) {
                cl += 'transition-bg p-1 transition hover:bg-white dark:bg-dark dark:text-dark-color dark:hover:bg-dark hover:bg-opacity-80 group rounded mb-1';
            } else {
                cl += ' flex-col justify-center';
            }
            return cl;
        },
        fileName(data) {
            let name = this.$helpers.file.name(data);
            return name ? name : data;
        },
        deleteItem(index) {
            if (index > -1) {
                this.values.splice(index, 1);
                this.$emit('update:temp');
            }
        },
        // updateAlt() {
        //     let value = this.$refs['inputEditAlt'].value;
        //     this.values[0].name = value;
        //     this.showEditAlt = false;
        //     this.isopen = false;
        // },
        dragItem(e) {
            this.dragging = true;
            document.onmouseup = () => {
                this.dragging = false;
            };
            if (this.options.type !== 'font' && !this.isSingle) {
                this.mouseDownHandler(e);
            }
        },
        cbEndDrag(pos) {
            if (this.hasSorted && pos.before && pos.after) {
                let before = pos.before[0];
                let after = pos.after[0];
                this.values.splice(after, 0, ...this.values.splice(before, 1));
            }
        },
        getSizeImage() {
            if (this.isSingle && this.options.type === 'image' && this.values?.length) {
                let src = this.values[0].value || this.values[0].thumbnail;
                let img = new Image();
                img.onload = () => {
                    let { width, height } = img;
                    this.size = { width, height };
                };
                img.src = src;
            }
        }
    }
};
</script>
