<template>
    <input
        v-if="!field.options || (['full', 'short', true].indexOf(field.options.toolbar) === -1)"
        v-model="value"
        v-focus="field.options?.autofocus ? 'select' : false"
        type="text"
        :placeholder="field?.options?.placeholder ?? field?.placeholder ?? null"
        class="block w-full transition rounded shadow-sm text-form-input border-form-border placeholder-form-input dark:bg-dark dark:border-gray-600 dark:text-dark-color sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
        :class="field.options?.oneline || field.options?.half || field.options?.control_width ? 'h-8' : 'h-9'"
        :style="field.options?.control_width ? 'width:'+field.options?.control_width : (field.options?.oneline ? 'max-width:60%' : '')"
        :maxlength="field?.options?.max"
        @change="onchange"
    >
    <EditorComponent
        v-else
        :data="value"
        :field="field"
        :type="1"
        @update:data="$emit('update:data', $event)"
    />
</template>

<script lang="ts">
    import EditorComponent from '../../components/editor/Editor.vue';
    export default {
        name: 'EditorField',
        components: {
            EditorComponent
        },
        props: {
            data: {
                type: String,
                default: ''
            },
            field: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data() {
            return {
                ready: false
            };
        },
        computed: {
            value: {
                get(){
                    return this.data != '__empty__' ? this.data : '';
                },
                set(val){
                    if (this.field?.options?.update != 'onchange' || this.field.options.toolbar) {
                        this.$emit('update:data', val);
                    }
                }
            }
        },
        watch: {
            data(val) {
                this.value = val;
            }
        },
        methods: {
            onchange(e) {
                if (this.field?.options?.update == 'onchange') {
                    this.$emit('update:data', e.target.value);
                }
            }
        }
    };
</script>
