<template>
    <div
        v-show="showFull"
        ref="list"
        class="fixed z-10 w-64 overflow-x-hidden overflow-y-auto bg-white dark:bg-dark dark:text-dark-color rounded-md shadow-list"
        :style="shadowList"
    >
        <ul
            v-show="show"
            class="w-full"
        >
            <li
                v-for="item in list"
                :key="item.name"
            >
                <button
                    type="button"
                    class="w-full px-4 py-2.5 flex items-center text-gray-600 text-sm border-0 transition hover:bg-gray-100 focus:outline-none border-l-2 focus:border-blue-700 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark"
                    @click="selectItem(item.name)"
                >
                    <span class="w-5 h-5 mr-4 min-w-5" v-html="item.icon" />
                    {{ item.text }}
                    <template v-if="item.count > -1">
                        ({{ item.count }})
                    </template>
                </button>
            </li>
        </ul>
        <ContentLink
            ref="content"
            :type="type"
            :src="src"
            :scrolled-bottom="scroll.bottom"
            @hide="hide"
            @update:select="$emit('update:select', urlType + $event, type)"
        />
    </div>
</template>

<script lang="ts">
    import ContentLink from './Content.vue';
    export default {
        name: 'GroupsTypes',
        components: {
            ContentLink
        },
        props: {
            src: {
                type: String,
                default: ''
            }
        },
        emits: ['update:select'],
        data(){
            return {
                show: false,
                showFull: false,
                type: '',
                list: [
                    {
                        name: 'collection',
                        text: 'Collections',
                        icon: `<svg class="w-full h-full" fill="currentColor">
                                    <path fill-rule="evenodd"
                                        d="M16 2a2 2 0 0 1 4 0v1h-4V2zM8.379 1a1.5 1.5 0 0 1 1.06.44l4.122 4.12A1.5 1.5 0 0 1 14 6.622V17.5a1.5 1.5 0 0 1-1.5 1.5h-10A1.5 1.5 0 0 1 1 17.5v-15A1.5 1.5 0 0 1 2.5 1h5.879zM4 5h4v2H4V5zm7 4v2H4V9h7zm-7 6v-2h5v2H4zM16 5h4v11l-2 4-2-4V5z">
                                    </path>
                                </svg>`,
                        count: -1
                    },
                    {
                        name: 'product',
                        text: 'Products',
                        icon: `<svg class="w-full h-full" fill="currentColor">
                                    <path
                                        d="M10.293 1.293A1 1 0 0 1 11 1h7a1 1 0 0 1 1 1v7a1 1 0 0 1-.293.707l-9 9a1 1 0 0 1-1.414 0l-7-7a1 1 0 0 1 0-1.414l9-9zM15.5 6a1.5 1.5 0 1 0 0-3 1.5 1.5 0 0 0 0 3z">
                                    </path>
                                </svg>`,
                        count: -1
                    },
                    {
                        name: 'page',
                        text: 'Pages',
                        icon: `<svg class="w-full h-full" fill="currentColor">
                                    <path
                                        d="M12.44.44A1.5 1.5 0 0 0 11.378 0H4.5A1.5 1.5 0 0 0 3 1.5v17A1.5 1.5 0 0 0 4.5 20h11a1.5 1.5 0 0 0 1.5-1.5V5.621a1.5 1.5 0 0 0-.44-1.06L12.44.439z">
                                    </path>
                                </svg>`,
                        count: -1
                    },
                    {
                        name: 'blog',
                        text: 'Blogs',
                        icon: `<svg class="w-full h-full" fill="currentColor">
                                    <path fill-rule="evenodd"
                                        d="M16 2a2 2 0 0 1 4 0v1h-4V2zM8.379 1a1.5 1.5 0 0 1 1.06.44l4.122 4.12A1.5 1.5 0 0 1 14 6.622V17.5a1.5 1.5 0 0 1-1.5 1.5h-10A1.5 1.5 0 0 1 1 17.5v-15A1.5 1.5 0 0 1 2.5 1h5.879zM4 5h4v2H4V5zm7 4v2H4V9h7zm-7 6v-2h5v2H4zM16 5h4v11l-2 4-2-4V5z">
                                    </path>
                                </svg>`,
                        count: -1
                    },
                    {
                        name: 'article',
                        text: 'Blog posts',
                        icon: `<svg class="w-full h-full" fill="currentColor">
                                    <path fill-rule="evenodd"
                                    d="M16 2a2 2 0 0 1 4 0v1h-4V2zM8.379 1a1.5 1.5 0 0 1 1.06.44l4.122 4.12A1.5 1.5 0 0 1 14 6.622V17.5a1.5 1.5 0 0 1-1.5 1.5h-10A1.5 1.5 0 0 1 1 17.5v-15A1.5 1.5 0 0 1 2.5 1h5.879zM4 5h4v2H4V5zm7 4v2H4V9h7zm-7 6v-2h5v2H4zM16 5h4v11l-2 4-2-4V5z">
                                </path>
                                </svg>`,
                        count: -1
                    },
                    {
                        name: 'email',
                        text: 'Email',
                        icon: `<svg fill="currentColor" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--! Font Awesome Pro 6.3.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M48 64C21.5 64 0 85.5 0 112c0 15.1 7.1 29.3 19.2 38.4L236.8 313.6c11.4 8.5 27 8.5 38.4 0L492.8 150.4c12.1-9.1 19.2-23.3 19.2-38.4c0-26.5-21.5-48-48-48H48zM0 176V384c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V176L294.4 339.2c-22.8 17.1-54 17.1-76.8 0L0 176z"/>
                            </svg>`,
                        count: -1
                    },
                    {
                        name: 'phone',
                        text: 'Phone number',
                        icon: `<svg fill="currentColor" xmlns=\"http:\/\/www.w3.org\/2000\/svg\" viewBox=\"0 0 512 512\"><!--! Font Awesome Pro 6.4.0 by @fontawesome - https:\/\/fontawesome.com License - https:\/\/fontawesome.com\/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d=\"M0 32L144 0l80 144-83.8 67c36.1 68.4 92.3 124.6 160.8 160.8L368 288l144 80L480 512H448C200.6 512 0 311.4 0 64L0 32z\"\/><\/svg>`,
                        count: -1
                    },
                ],
                scroll: {
                    has: false,
                    top: true,
                    bottom: false
                },
            };
        },
        computed: {
            shadowList(){
                let boxShadow = '-1px 0px 20px rgb(23 24 24 / 5%), 0px 1px 5px rgb(0 0 0 / 15%)';
                if(this.scroll.has){
                    if(this.scroll.top) {
                        boxShadow += ',inset 0 -2rem 2rem -2rem rgba(0, 0, 0, 0.15)';
                    }
                    else if(this.scroll.bottom) {
                        boxShadow += ',inset 0 2rem 2rem -2rem rgba(0, 0, 0, 0.15)';
                    }
                    else {
                        boxShadow += ',inset 0 2rem 2rem -2rem rgba(0, 0, 0, 0.15),inset 0 -2rem 2rem -2rem rgba(0, 0, 0, 0.15)';
                    }

                }
                return { boxShadow };
            },
            urlType(){
                if(this.type === 'article'){
                    return 'blogs/';
                }
                else if (this.type === 'email' || this.type === 'phone') {
                    return '';
                }
                else{
                    return this.type + 's/';
                }
            },
        },
        watch: {
            showFull: function(val){
                this.$nextTick(() => {
                    this.scroll.has = this.hasScroll();
                });
                if(!val){
                    this.$refs.content.show = false;
                }
            },
        },
        mounted () {
            this.$nextTick(() => {
                this.$list = this.$refs.list;
                window.addEventListener('resize', this.eventChange);
                this.$list.addEventListener('scroll', this.eventChange);
                this.eventChange();
            });
        },
        unmounted(){
            window.removeEventListener('resize', this.eventChange);
        },
        methods: {
            hasScroll(){
                let $el = this.$refs['list'];
                if($el) {
                    return $el.scrollHeight > $el.clientHeight;
                }
                return false;
            },
            selectItem(name){
                this.type = name;
                this.$refs['content'].show = true;
                this.show = false;
            },
            hide(){
                this.show = true;
            },
            eventChange(e){
                if(!e) {
                    return;
                }
                let target = e.target;
                this.scroll.top = target.scrollTop == 0;
                this.scroll.bottom = target.scrollTop + target.clientHeight >= target.scrollHeight;
            }
        }
    };
</script>
