<template>
    <div class="tab pt-2">
        <div class="tab__heading">
            <ul class="flex">
                <li
                    v-for="(tab, i) in tabs"
                    :key="tab.title"
                    class="flex-1"
                    :class="[
                        tabSelected === tab.name ? 'border-primary-600 border-b-2' : 'border-b border-gray-300 dark:border-gray-600'
                    ]"
                >
                    <a
                        href="#"
                        class="flex items-center justify-center w-full h-7-5 text-xs text-center align-middle uppercase font-normal px-3 py-1 whitespace-nowrap"
                        :class="tabSelected === tab.name ? 'text-primary-600' : 'text-gray-500 dark:text-dark-color'"
                        @click.prevent="tabSelected = tab.name"
                    >
                        {{ tab.title }}
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'TabField',
        props: {
            data: {
                type: String,
                default: '',
            },
            field: {
                type: Object,
                default(){
                    return {};
                },
            }
        },
        emits: ['update:data'],
        data(){
            return {
                values: {}
            };
        },
        computed: {
            tabSelected: {
                get(){
                    return this.data && this.data !== '' ? this.data :  (this.field?.options?.tabs?.[0]?.name ?? this.tabs[0].name);
                },
                set(val){
                    this.$emit('update:data', val);
                }
            },
            tabs(){
                return this.field?.options?.tabs ?? [];
            }
        },
        mounted() {
            if (this.tabSelected == 'normal' || !this.tabSelected) {
                this.$emit('update:data', 'normal');
            }
        }
    };
</script>
