<template>
    <div
        class="mt-2"
        :class="id"
    >
        <div class="relative ecom-group-items">
            <div
                v-for="(value, index) in values"
                :key="value"
                class="bg-white dark:bg-gray-700 dark:text-dark-color ecom-group-item item flex flex-col items-center justify-center"
            >
                <div
                    class="relative flex items-center text-gray-500 dark:text-dark-color transition header-group group h-9 border  border-opacity-50 w-full hover:bg-coolGray-50 dark:hover:bg-dark dark:bg-dark"
                    :class="opended(index) ? 'border-b-0 rounded-t border-primary-600 dark:border-gray-500' : 'mb-3 rounded border-gray-400'"
                >
                    <div
                        class="flex items-center w-full pl-5 text-sm truncate transition cursor-pointer select-none h-9 px-2"
                        @click="toggleOpen(index, value?.type)"
                    >
                        <span
                            class="truncate transition-all capitalize duration-75 text-form-title dark:text-dark-color line-clamp-1"
                            :class="opended(index) ? 'w-6/10' : 'item-hover:w-6/10'"
                            v-html="title(value, index)"
                        />
                    </div>
                    <button
                        type="button"
                        class="absolute flex items-center ml-1 rounded left-px dark:hover:bg-dark dark:hover:text-primary-600"
                        :class="opended(index) ? 'hover:bg-form-active-hover' : hasChildren(value?.type) ? 'hover:bg-form-bg-hover' : 'cursor-default'"
                        @click="toggleOpen(index, value?.type)"
                    >
                        <ecom-icon
                            :icon="faChevronRight"
                            class="w-3 h-3 -mt-0.5 transform rotate-0 text-form-title dark:text-dark-color fill-current"
                            :class="[
                                opended(index) ? 'rotate-90' : 'rotate-0',
                                !hasChildren(value?.type) ? 'opacity-30' : ''
                            ]"
                        />
                    </button>
                    <div v-if="values[index]?.control !== false" class="absolute flex px-2" :class="dir">
                        <button
                            type="button"
                            class="text-form-title dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-600 opacity-0 hover:bg-form-bg-hover flex items-center rounded justify-center p-1.5 focus:outline-none focus:ring-0 transition"
                            :class="[
                                !isDraggingStarted ? 'group-hover:opacity-100' : '',
                                acceptAddIndex(index) ? '' : 'hidden' ,
                                ,{
                                    'opacity-100 hover:bg-form-active-hover':opended(index)
                                }
                            ]"
                            title="Clone"
                            @click="duplicateValue(index)"
                        >
                            <ecom-icon :icon="faClone" class="w-3 h-3 p-0.5" />
                        </button>
                        <button
                            v-if="isClearAll"
                            type="button"
                            class="text-form-title dark:text-dark-color flex opacity-0 hover:bg-form-bg-hover dark:hover:bg-dark items-center rounded justify-center p-1.5 focus:outline-none focus:ring-0"
                            :class="{
                                'opacity-100 hover:bg-form-active-hover':opended(index),
                                'group-hover:opacity-100': !isDraggingStarted
                            }"
                            title="Delete"
                            @click="values.splice(index,1)"
                        >
                            <ecom-icon :icon="faTrashCan" class="w-3 h-3 p-0.5 dark:text-dark-color dark:hover:text-primary-600 fill-current" />
                        </button>
                        <button
                            type="button"
                            class="relative flex items-center p-2 transition rounded opacity-0 hover:bg-form-bg-hover dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark ecom-mouse-down cursor-grab text-form-secondary"
                            :class="{
                                'opacity-100 hover:bg-form-active-hover':opended(index),
                                'group-hover:opacity-100': !isDraggingStarted
                            }"
                            @mousedown="onMouseDown"
                            >
                            <svg class="w-3 h-3 -ml-1 -mr-2 transition dark:text-dark-color dark:hover:text-primary-600 fill-current" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
                            </svg>
                            <svg class="w-3 h-3 -mr-1 transition dark:text-dark-color dark:hover:text-primary-600 fill-current" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
                            </svg>
                        </button>
                    </div>
                </div>
                <Fields
                    v-if="opended(index)"
                    class="w-full pt-3 mb-3 border border-primary-600 dark:border-gray-500 rounded-b border-opacity-70 pb-3 dark:bg-gray-700"
                    :deep="1"
                    :ispop="true"
                    :data="!nullCustomType ? values[index].settings : values[index]"
                    :fields="!nullCustomType ? paramByType(value.type) : params"
                />
            </div>
        </div>
        <div class="text-center">
            <div class="relative">
                <div>
                    <button
                        type="button"
                        class="flex items-center w-full p-2 text-sm bg-white dark:bg-gray-700 dark:text-dark-color rounded text-primary-600 hover:text-primary-800 hover:bg-gray-100"
                        :class="{'opacity-50 pointer-events-none' : !acceptAdd('')}"
                        @click="addNewItem(null, $event)"
                    >
                        <ecom-icon :icon="faCirclePlus" class="w-3 h-3 p-px mr-1 ml-2.5" />
                        <span class="text-xm" v-html="acceptAdd('') ? btnAddText : 'Limited'" />
                        <span
                            v-if="nullCustomType && acceptAdd('')"
                            class="ml-1 text-xs leading-5"
                        >
                            <template v-if="textLimit('normal')">({{ textLimit('normal') }})</template>
                        </span>
                    </button>
                </div>
                <ul
                    v-if="!nullCustomType && openMenu"
                    class="absolute max-w-4xl min-w-full overflow-y-auto mb-2 py-1.5 text-left transform -translate-x-1/2 bg-white dark:bg-dark dark:text-dark-color shadow-lg rounded transition ring-1 ring-black ring-opacity-10 top-full left-1/2 z-20"
                    ref="popover-add"
                    :style="posPopover"
                >
                    <li
                        v-for="(item, type, index) in customTypes"
                        :key="type"
                        class="px-3 py-2 text-sm text-gray-700 truncate transition cursor-pointer hover:text-gray-700 hover:bg-gray-100 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark"
                        :class="{ 'border-b border-gray-200 dark:border-gray-600' : index < Object.keys(customTypes).length - 1,
                                'opacity-50 pointer-events-none' : !acceptAdd(type)}"
                        @click="addNewItem(type)"
                    >
                        <span v-html="item.name" />
                        <span
                            v-if="textLimit(type)"
                            class="ml-2 h-4 w-4 text-center rounded-xl bg-gray-200 float-right mt-0.5 text-xs text-gray-600 dark:text-dark-color"
                            :data-tooltip="textLimit(type) > 0 ? 'Max adding' : 'Limited'"
                            data-tooltip-location="left"
                            v-html="textLimit(type)"
                        />
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script lang="ts">

    import { faCirclePlus, faClone, faTrashCan, faChevronRight } from '@fortawesome/pro-light-svg-icons';
    import { dragMixin } from '../../mixins';

    export default {
        name: 'GroupField',
        mixins: [ dragMixin ],
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: Array,
            field: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data(){
            return {
                tabOpened: -1,
                values: [],
                isWatch: true,
                isEmit: false,
                drag: {
                    axis: 'y'
                },
                posPopover: {},
                openMenu: false,
                id: this.$helpers.randid(),
                faCirclePlus, faClone, faTrashCan, faChevronRight
            };
        },
        computed: {
            dir(){
                return window.document.querySelector('html').getAttribute('dir') === 'rtl' ? 'left-2': 'right-px';
            },
            isClearAll(){
                if(this.field?.options?.is_clear_all ?? true){
                    return true;
                }else{
                    return this.values.length > 1;
                }
            },
            btnAddText(){
                return this.field?.options?.add_text ?? 'Add item';
            },
            params(){
                return this.field?.params ?? [];
            },
            max(){
                return this.field?.options?.max;
            },
            customTypes(){
                let types = {};
                this.params.forEach( param => {
                    if( 'settings' in param && 'type' in param ) {
                        types[param['type']] = {
                            name: param['name'],
                            max: param['max']
                        };
                    }
                });
                return types;
            },
            nullCustomType(){
                return !Object.keys(this.customTypes).length;
            },
            count_block(){
                if(this.nullCustomType) {
                    return { normal: this.values.length };
                }
                else {
                    let rs = {};
                    for(let k in this.customTypes) {
                        rs[k] = this.values.filter(x => x.type === k).length;
                    }
                    return rs;
                }
            }
        },
        watch: {
            data: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.values =  val ? val : [];
                    }
                    this.isWatch = true;

                }
            },
            values: {
                immediate: true,
                deep: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }
        },
        methods: {
            setid(index) {
                if (this.values[index].id === undefined) {
                    this.values[index].id = this.$helpers.randid();
                }
            },
            opended(index){
                return this.data.tabOpened === index;
            },
            /**
             * ? Check if groups has no children params, which will hidden
             */
            hasChildren(type) {
                return (
                    this.nullCustomType ?
                    Object.keys(this.params ?? {}).length > 0 :
                    this.params.filter(p => p.type === type)[0]?.settings?.length
                );
            },
            classBtnHeader(index){
                if(this.opended(index)) {
                    return 'text-white hover:bg-primary-600';
                }
                else {
                    return 'text-gray-700 hover:bg-gray-50 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark';
                }
            },
            limitBlock(type){
                if(this.nullCustomType) {
                    if(this.max !== undefined) {
                        return this.max - this.count_block['normal'];
                    }
                }
                else {
                    if(this.customTypes[type]?.max !== undefined) {
                        return this.customTypes[type].max - this.count_block[type];
                    }
                }
                return undefined;
            },
            acceptAdd(type) {
                return type !== undefined &&
                        (this.limitBlock(type) === undefined ||
                        this.limitBlock(type) > 0);
            },
            acceptAddIndex(index) {
                let value = this.values[index],
                    type = ('type' in value && 'settings' in value) ?
                            value['type'] : 'normal';

                return this.acceptAdd(type);
            },
            textLimit(type) {
                let limit = this.limitBlock(type);
                if(limit !== undefined) {
                    return `${limit}`;
                    //return `(${limit > 0 ? ('max ' + limit) : 'limited'})`;
                }
                return undefined;
            },
            isCustomType(value) {
                return 'type' in value && 'settings' in value;
            },
            paramByType(type){
                return this.params.filter(p => p.type === type)[0]?.settings ?? [];
            },
            title(value, index) {
                if(this.isCustomType(value)){
                    return this.customTypes[value.type].name ?? 'Item #' + (index + 1);
                } else {
                    if(value?.title) {
                        if(value.title.length <= 30) {
                            return value.title;
                        } else {
                            return value.title.slice(0, 30) + '...'
                        }

                    }
                    if(value?.label) {
                        if(value.label.length <= 30) {
                            return value.label;
                        } else {
                            return value.label.slice(0, 30) + '...'
                        }

                    }
                    if(value?.name) {
                        if(value.name.length <= 30) {
                            return value.name;
                        } else {
                            return value.name.slice(0, 30) + '...'
                        }

                    }
                    let selectedForTitle = `Item #${index + 1}`;
                    if(this?.field?.title_unique === false) {
                        return selectedForTitle;
                    }
                    if (this?.field?.title_unique && value[this?.field?.title_unique]?.length > 0) {
                        selectedForTitle = value[this?.field?.title_unique];
                    } 
                    else if (this?.field?.params[0].title_unique && value[this?.field?.params[0].name]?.length > 0) {
                       return selectedForTitle = value[this?.field?.params[0].name];
                    }
                    else if (!this?.field?.title_unique && value && typeof value === 'object') {
                        Object.keys(value).forEach(field => {
                            if (selectedForTitle === `Item #${index + 1}` && typeof value[field] === 'string' && value[field].length > 0 && !value[field].includes('ecom-')) {
                                selectedForTitle = value[field];
                            }
                        });
                    }
                    return selectedForTitle;
                }
            },

            toggleOpen(index, type){
                if(this.hasChildren(type)) {
                    this.data.tabOpened = this.opended(index) ? -1 : index;
                }
            },

            addNewItem(type, e){

                if (!this.nullCustomType && e) {

                    let btnAdd = e.target.closest('button'),
                        btnRect = btnAdd.getBoundingClientRect(),
                        btnTop = btnRect.top - btnRect.height / 2,
                        winHeight = window.innerHeight - 70,
                        dir = btnTop < winHeight / 2 ? 'bottom':'top',
                        maxHeight = this.$helpers.intBetween(
                            dir === 'top' ?
                            btnTop - 70 - btnRect.height / 2:
                            winHeight - btnTop - btnRect.height / 2,
                        0 , 300);

                    if (dir === 'top') {
                        this.posPopover.top = 'auto';
                        this.posPopover.bottom = '100%';
                    } else {
                        this.posPopover.bottom = 'auto';
                        this.posPopover.top = '100%';
                    }

                    this.posPopover.maxHeight = maxHeight + 'px';
                    this.openMenu = true;
                    document.addEventListener('click', this.doCloseMenu);

                }

                let newValue;

                if (this.acceptAdd(type)) {
                    if(!this.nullCustomType) {
                        if(type && typeof type === 'string'){
                            newValue = {
                                type: type,
                                settings: {

                                }

                            };
                            // newValue = this.values
                            //     .filter(value => value.type === type)
                            //     .slice(-1)[0] ??  { type, settings: {} };
                        }
                    } else {
                        newValue = {};
                        // newValue = this.values.slice(-1)[0] ?? {};
                    }
                }

                if (newValue !== undefined){
                    let clone = this.$helpers.copy(newValue);
                    delete clone.id;
                    this.values.splice(this.values.length, 0, clone);
                    if(this.data) {
                        this.data.tabOpened = this.values.length-1;
                    }

                }

                if (type !== null) {
                    this.openMenu = false;
                }

            },

            doCloseMenu(e) {
                if (
                    e === undefined ||
                    !e.target.closest('.'+this.id)
                ) {
                    this.openMenu = false;
                    document.removeEventListener('click', this.doCloseMenu);
                }
            },

            duplicateValue(index) {
                if(this.acceptAddIndex(index)) {
                    let clone = this.$helpers.copy(this.values[index]);
                    delete clone.id;
                    this.values.splice(index, 0, clone);
                    this.data.tabOpened = index+1;
                }
            },

            onMouseDown(e){
                this.mouseDownHandler(e);
            },

            cbEndDrag(pos){
                if (this.hasSorted && pos.before && pos.after) {
                    let before = parseInt(pos.before[0]);
                    let after = parseInt(pos.after[0]);
                    if(Math.min(before, after) <= this.data.tabOpened &&
                        Math.max(before, after) >= this.data.tabOpened) {
                            if(before < after) {
                                this.data.tabOpened = this.data.tabOpened === before ? after : (this.data.tabOpened - 1);
                            }
                            else {
                                this.data.tabOpened = this.data.tabOpened === before ? after : (this.data.tabOpened + 1);
                            }
                    }
                    this.values.splice(after, 0, this.values.splice(before, 1)[0]);
                }
            },

        },
        mounted() {
            if(!this.data  || typeof this.data != 'object') {
            }
            else
            {
                Object.defineProperty( this.data, "tabOpened", { enumerable: false, writable: true } );
                this.data.tabOpened = -1;
            }
        },
    };
</script>
<style>
    .dragging{
        outline-style: dashed;
        outline-offset: -2px;
        outline-width: 2px;
        outline-color: rgba(5, 150, 105, 1);
    }
    .dragging *{
        opacity: 0;
    }
    .dragging .header-group{
        opacity: 1;
    }
    .svg-full svg{
        width: 100%;
        height: 100%;
    }
    .ecom-mouse-down{
        cursor: grab;
    }
    .ecom-mouse-down:focus{
        cursor: grabbing;
    }
    .item:hover .item-hover\:w-6\/10 {
        width: 60%;
    }
</style>
