<template>
    <div
        class="text-sm text-gray-500 dark:text-dark-color"
        v-html="content"
    />
</template>

<script lang="ts">
    export default {
        name: 'ParagraphField',
        props: {
            field: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {
            content(){
                return this.$helpers.markdown(this.field?.content ?? '');
            }
        }
    };
</script>
