<template>
    <div
        class="relative"
        :class="[field?.options?.oneline ? 'ml-auto' : '', id]"
    >
        <button
            type="button"
            class="relative flex items-center justify-center w-8 h-8 text-gray-600 dark:text-dark-color dark:bg-gray-600 bg-coolGray-100 rounded"
            :data-tooltip="itemActive?.tooltip ?? null"
            @click="open"
        >
            <!-- <component
                :is="preview.icon"
                v-if="preview.type === 'icon'"
                class="w-4 h-4"
                :class="type === 'align-y' && itemActive?.icon ? 'transform rotate-90' : ''"
            /> -->
            <ecom-icon
                :icon="icons[preview.icon]"
                v-if="preview.type === 'icon'"
                class="w-4 h-4"
                :class="type === 'align-y' && itemActive?.icon ? 'transform rotate-90' : ''"
            />
            <p v-else class="text-xs font-medium" v-html="preview.text" />
        </button>

        <transition
            enter-active-class="transition duration-200 ease-out"
            enter-from-class="transform scale-90 opacity-0"
            enter-to-class="transform scale-100 opacity-100"
            leave-active-class="transition duration-150 ease-in"
            leave-from-class="transform scale-100 opacity-100"
            leave-to-class="transform scale-90 opacity-0"
        >
            <div v-if="isopen" class="absolute z-50 mt-3 top-full -left-4 ecom-fields__popup">
                <div class="inline-block p-1 bg-white text-white dark:bg-dark dark:text-dark rounded shadow-popup">
                    <svg
                        viewBox="0 0 213.3 213.3"
                        class="absolute top-0 w-4 h-4 mr-1 -mt-3 transform rotate-180 left-8 fill-current"
                    >
                        <polygon
                            class="shadow-xxl"
                            points="0,53.333 106.667,160 213.333,53.333"
                        />
                    </svg>
                    <div class="inline-flex rounded">
                        <a
                            v-for="(item, index) in types[type]"
                            :key="index"
                            href="#"
                            class="flex items-center justify-center px-2 text-center transition-all duration-300 h-7-5 border-form-border hover:bg-form-bg-hover"
                            :class="classButton(index)"
                            :data-tooltip="item.tooltip"
                            @click.prevent="selectedIndex = index"
                        >
                            <ecom-icon
                                :icon="icons[item.icon]"
                                v-if="item.icon"
                                class="w-4 h-4 m-auto"
                                :class="[
                                    type === 'align-y' ? 'transform rotate-90' : ''
                                ]"
                            />
                            <span v-else class="text-xs font-medium" v-html="item.text" />
                        </a>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    import { Popmenu } from '@/mixins';
    import { faPen, faArrowRightArrowLeft, faAlignLeft, faAlignRight, faAlignCenter, faAlignJustify, faArrowLeftToLine, faArrowRightToLine, faArrowDownToLine, faArrowUpToLine, faArrowsFromLine, faArrowDownArrowUp, faListUl, faEllipsis, faTableLayout, faHeading, faStars } from '@fortawesome/pro-light-svg-icons';
    export default {
        name: 'ChooseField',
        mixins: [ Popmenu ],
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: [String, Number],
            field: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data(){
            return {
                selectedIndex: -1,
                isWatch: true,
                isEmit: false,
                icons: {
                    faPen, faArrowRightArrowLeft, faAlignLeft, faAlignRight, faAlignCenter, faAlignJustify, faArrowLeftToLine, faArrowRightToLine, faArrowDownToLine, faArrowUpToLine, faArrowsFromLine, faArrowDownArrowUp, faListUl, faEllipsis, faTableLayout, faHeading, faStars
                }
            };
        },
        computed: {
            keep() {
                return false;
            },
            type(){
                return this.field?.options?.type ?? 'align';
            },
            types(){
                return {
                    'align-x': [
                        { icon: 'faArrowLeftToLine', 'tooltip': 'Left' },
                        { icon: 'faArrowRightToLine', 'tooltip': 'Right' },
                    ],
                    'align-x-full': [
                        { icon: 'faArrowLeftToLine', 'tooltip': 'Left' },
                        { icon: 'faAlignCenter', 'tooltip': 'Center' },
                        { icon: 'faArrowRightToLine', 'tooltip': 'Right' },
                    ],
                    'align-y': [
                        { icon: 'faArrowLeftToLine', 'tooltip': 'Top' },
                        { icon: 'faArrowRightToLine', 'tooltip': 'Bottom' },
                    ],
                     'align-y-full': [
                        { icon: 'faArrowUpToLine', 'tooltip': 'Top' },
                        { icon: 'faArrowsFromLine', 'tooltip': 'Center' },
                        { icon: 'faArrowDownToLine', 'tooltip': 'Bottom' },
                    ],
                    'align-position':[
                        { icon: 'faArrowLeftToLine', 'tooltip': 'Left' },
                        { icon: 'faArrowUpToLine', 'tooltip': 'Top' },
                        { icon: 'faArrowDownToLine', 'tooltip': 'Bottom' },
                        { icon: 'faArrowRightToLine', 'tooltip': 'Right' },
                    ],
                    'align-full': [
                        { icon: 'faAlignLeft', 'tooltip': 'Left' },
                        { icon: 'faAlignCenter', 'tooltip': 'Center' },
                        { icon: 'faAlignRight', 'tooltip': 'Right' },
                        { icon: 'faAlignJustify', 'tooltip': 'Justify' },
                    ],
                    'text-align': [
                        { icon: 'faAlignLeft', 'tooltip': 'Left' },
                        { icon: 'faAlignCenter', 'tooltip': 'Center' },
                        { icon: 'faAlignRight', 'tooltip': 'Right' },
                    ],
                    'vertical-align': [
                        { icon: 'faArrowUpToLine', 'tooltip': 'Top' },
                        { icon: 'faArrowsFromLine', 'tooltip': 'Center' },
                        { icon: 'faArrowDownToLine', 'tooltip': 'Bottom' },
                    ],
                    'layout': [
                        { icon: 'faListUl', 'tooltip': 'Default' },
                        { icon: 'faEllipsis', 'tooltip': 'Inline' }
                    ],
                    'heading': [
                        { text: 'H1', value: 'h1' },
                        { text: 'H2', value: 'h2' },
                        { text: 'H3', value: 'h3' },
                        { text: 'H4', value: 'h4' },
                        { text: 'H5', value: 'h5' },
                        { text: 'H6', value: 'h6' },
                        { text: 'LINK', value: 'a' }
                    ],
                    'rating': [
                        { text: '0', value: '0' },
                        { text: '1', value: '1' },
                        { text: '2', value: '2' },
                        { text: '3', value: '3' },
                        { text: '4', value: '4' },
                        { text: '5', value: '5' },
                    ]
                };
            },
            values(){
                if (this.field?.options?.values) {
                    return this.field.options.values;
                }
                let res = [];
                if (this.types?.[this.type]) {
                    this.types?.[this.type].forEach((item) => {
                        res.push(item?.value ?? '');
                    });
                }
                return res;
            },
            value(){
                return this.values?.[this.selectedIndex];
            },
            itemActive() {
                let t = this.types[this.type];
                return t?.[this.selectedIndex];
            },
            typeIcon() {
                let options = this.field.options, icon = '';
                let type = options?.type;
                switch (type)
                {
                    case 'align-y-full' :
                    case 'align-y' : {
                        icon = 'faArrowDownArrowUp';
                        break;
                    }
                    case 'layout' : {
                        icon = 'faTableLayout';
                        break;
                    }
                    case 'heading' : {
                        icon = 'faHeading';
                        break;
                    }
                    case 'rating' : {
                        icon = 'faStars';
                        break;
                    }
                    default : {
                        icon = 'faArrowRightArrowLeft';
                    }
                }
                return icon;
            },
            preview() {
                if(this.itemActive?.icon) {
                    return {
                        type: 'icon',
                        icon: this.itemActive.icon
                    };
                }
                else if (this.itemActive?.text) {
                    return {
                        type: 'text',
                        text: this.itemActive.text
                    };
                }
                else {
                    return {
                        type: 'icon',
                        icon: this.typeIcon
                    };
                }
            },
        },

        watch: {
            data: {
                immediate: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.selectedIndex = this.values.indexOf(val ?? '');
                    }
                this.isWatch = true;

                }
            },
            value: {
                immediate: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }

        },
        methods: {
            classButton(index){
                let length = this.types[this.type].length;
                let cl = '';
                if(index === length - 1){
                    cl += 'rounded-r ';
                }
                else {
                    if (index === 0){
                        cl += 'rounded-l  ';
                    }
                }
                if(this.selectedIndex === index){
                    cl += 'hover:bg-form-bg-active bg-form-bg-active text-white border-form-bg-active ';
                }
                else{
                    cl += 'bg-white dark:bg-dark dark:text-dark-color border-form-border text-form-secondary ';
                }
                return cl;
            }
        }
    };
</script>