<template>
    <div
        class="relative flex items-center justify-end -space-x-px"
        :class="id"
    >
         <button
            class="relative flex items-center justify-center w-8 h-8 text-gray-600 dark:text-dark-color dark:bg-gray-600 bg-coolGray-100 rounded"
            :data-tooltip="globalTitle ?? 'Click to change'"
            @click="open"
            type="button"
        >
            <ecom-icon v-if="typeIcon" :icon="typeIcon" class="w-4 h-4 fill-current" />
        </button>
        <transition
            enter-active-class="transition duration-200 ease-out"
            enter-from-class="transform scale-90 opacity-0"
            enter-to-class="transform scale-100 opacity-100"
            leave-active-class="transition duration-150 ease-in"
            leave-from-class="transform scale-100 opacity-100"
            leave-to-class="transform scale-90 opacity-0"
        >
            <div
                v-if="isopen"
                ref="ecom-fields__popup"
                class="absolute z-50 mt-3 -left-2 ecom-fields__popup"
                :class="[popupPosition === 'bottom' ? 'top-full' : 'bottom-full', isShowPopup ? 'visible' : 'invisible']"
            >
                <div class="inline-block bg-white rounded shadow-popup dark:bg-gray-700 dark:text-dark-color dark:border dark:border-gray-200"
                    :class="type === 'dropdown' ? 'w-max min-w-40 max-w-68' : 'w-68'"
                >
                    <svg
                        viewBox="0 0 213.3 213.3"
                        class="absolute w-4 h-4 mr-1 -mt-3 left-4 fill-current text-white dark:text-gray-700"
                        :class="popupPosition === 'bottom' ? 'top-0 transform rotate-180' : '-bottom-1'"
                    >
                        <polygon
                            class="shadow-xxl"
                            points="0,53.333 106.667,160 213.333,53.333"
                        />
                    </svg>
                    <Tabs
                        v-if="global"
                        :tabs="['customize','global']"
                        :title="field?.options?.global?.type"
                        :type="global?.type"
                        :value="values"
                        :selected="dGlobal.selected"
                        @addglobal="addNewGlobal"
                        @refesh="backToDefault"
                    >
                        <template #global>
                            <Global
                                v-model:selected="dGlobal.selected"
                                :global="global"
                            />
                        </template>
                        <template #customize>
                            <Fields
                                :fields="fields"
                                :deep="1"
                                :data="valuesType ?? {}"
                                :ispop="true"
                                class="relative p-3"
                                @update:data="updateData"
                            />
                        </template>
                    </Tabs>
                    <div v-else>
                        <div v-if="type !== 'dropdown'" class="flex items-center justify-between px-4 py-2 mb-4 border-b dark:border-gray-600">
                            <span class="text-sm font-medium text-form-title dark:text-dark-color" v-html="field?.label"/>
                            <span class="flex items-center">
                                <button
                                    type="button"
                                    class="p-2 transition rounded text-form-secondary hover:bg-gray-200 hover:text-form-secondary-hover dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-600"
                                    :class="{'opacity-50 pointer-events-none': Object.keys(values).length === 0}"
                                    data-tooltip="Reset to default"
                                    data-tooltip-location="top-right"
                                    @click="backToDefault"
                                >
                                    <ecom-icon :icon="icons.faArrowsRotate" class="w-4 h-4" />
                                </button>
                            </span>
                        </div>
                        <PopupDropdown
                            v-if="type ==='dropdown'"
                            :data="data"
                            :values="optionsValues"
                            @update:data="$emit('update:data', $event)"
                            @close="doClose"
                        />
                        <Fields
                            v-else
                            :fields="fields"
                            :deep="1"
                            :data="valuesType"
                            :ispop="true"
                            class="relative p-3"
                            @update:data="updateData"
                        />
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
    import { faFontCase, faFiles, faPen, faBox, faArrowsRotate, faBorderAll, faAngleDown, faFaceSmilePlus, faArrowsLeftRight, faScaleBalanced, faItalic, faTextSlash, faTableLayout, faCrosshairs, faPercent, faTags, faPersonRunning, faStarShooting, faW, faH, faDistributeSpacingHorizontal, faAlignJustify, faFilter, faPaperclip, faCrop, faImage, faArrowsUpDown, faArrowPointer } from '@fortawesome/pro-light-svg-icons';
    import Tabs from '../../components/molecules/Tabs/Tabs.vue';
    import PopupDropdown from '../../components/molecules/DropdownOptions/DropdownOptions.vue';
    import Global from '../../components/molecules/GlobalPanel/GlobalPanel.vue';

    import { dropdownTypes, popupTypes } from '../../constants';

    import { Popmenu } from '@/mixins';

    export default {
        name: 'PopupGroup',
        components: {
            Tabs,
            Global,
            PopupDropdown,
        },
        provide: {
            'teleport_id': '.ecom-fields__popup'
        },
        mixins: [ Popmenu ],
        props: {
            field: {
                type: Object,
                default () {
                    return {};
                }
            },
            // eslint-disable-next-line vue/require-default-prop
            data: [Object, String, Number, Boolean]
        },
        emits: ['update:data', 'close'],
        data () {
            return {
                values: this.data ?? {},
                dGlobal: {
                    selected: null,
                },
                isWatch: true,
                isEmit: false,
                icons: {
                    faFontCase, faFiles, faPen, faBox, faArrowsRotate, faBorderAll, faAngleDown, faFaceSmilePlus, faArrowsLeftRight, faScaleBalanced, faItalic, faTextSlash, faTableLayout, faCrosshairs, faPercent, faTags, faPersonRunning, faStarShooting, faW, faH, faDistributeSpacingHorizontal, faAlignJustify, faFilter, faPaperclip, faCrop, faImage, faArrowsUpDown, faArrowPointer
                }
            };
        },
        computed: {
            globalTitle() {
                if(this.dGlobal) {
                    return this.$store.getters['settings/globalTitle'](this.global?.type, this.dGlobal.selected);
                }
                return '';
            },
            activeStatus(){
                return 'bg-'+(this.dGlobal.selected ? 'green-600' :
                            (Object.values(this.values).filter((v) =>
                                {
                                    return v !== null ?  (typeof v != 'object' || Object.keys(v).length > 0) : false;
                                }).length > 0 ?
                                'blue-600' :
                                'transparent'
                            )
                        );
            },
            global () {
                return this?.field?.options?.global ?? false;
            },
            title () {
                return this?.field?.options?.type ?? 'Untilted';
            },
            typeIcon() {
                let options = this.field.options, type  =  options?.icon_type??options?.type, icon = '';
                switch (type)
                {
                    case 'typography' : {
                        icon = this.icons.faFontCase;
                        break;
                    }
                    case 'text-shadow' : {
                        icon = this.icons.faFiles;
                        break;
                    }
                    case 'box-shadow' : {
                        icon = this.icons.faBox;
                        break;
                    }
                    case 'border' : {
                        icon = this.icons.faBorderAll;
                        break;
                    }
                    case 'icon' : {
                        icon = this.icons.faFaceSmilePlus;
                        break;
                    }
                    case 'alignment' : {
                        icon = this.icons.faArrowsLeftRight;
                        break;
                    }
                    case 'transitions' : {
                        icon = this.icons.faPersonRunning;
                        break;
                    }
                    case 'dropdown' : {
                        icon = this.dropdownIcon
                        break;
                    }
                    case 'popup' : {
                        icon = this.popupIcon
                        break;
                    }
                    case 'filter' : {
                        icon = this.icons.faFilter
                        break;
                    }
                    case 'sorting':
                        icon = this.icons.faArrowsUpDown;
                        break;
                    case 'percent':
                        icon = this.icons.faPercent;
                        break;
                    case 'animation':
                        icon = this.icons.faStarShooting;
                        break;
                    case 'cursor':
                        icon = this.icons.faArrowPointer;
                        break;
                    default : {
                        icon = this.icons.faPen;
                    }
                }
                return icon;
            },
            dropdownIcon() {
                let name = this.field.name, icon = '';
                switch (name)
                {
                    case 'font-weight' : {
                        icon = this.icons.faScaleBalanced;
                        break;
                    }
                    case 'text-transform' : {
                        icon = this.icons.faFontCase;
                        break;
                    }
                    case 'font-style' : {
                        icon = this.icons.faItalic;
                        break;
                    }
                    case 'text-decoration' : {
                        icon = this.icons.faTextSlash;
                        break;
                    }
                    case 'product_source' : {
                        icon = this.icons.faTags;
                        break;
                    }
                    case 'layout' : {
                        icon = this.icons.faTableLayout;
                        break;
                    }
                    case 'position' :
                    case 'image_position' : {
                        icon = this.icons.faCrosshairs;
                        break;
                    }
                    case 'image_ratio' : {
                        icon = this.icons.faPercent;
                        break;
                    }
                    case 'transition' : {
                        icon = this.icons.faPersonRunning;
                        break;
                    }
                    case 'timing' : {
                        icon = this.icons.faStarShooting;
                        break;
                    }
                    case 'width' :
                    case 'width-max' :
                    case 'content-width' : {
                        icon = this.icons.faW
                        break;
                    }
                    case 'height' : {
                        icon = this.icons.faH
                        break;
                    }
                    case 'align-content' : {
                        icon = this.icons.faDistributeSpacingHorizontal
                        break;
                    }
                    case 'padding' : {
                        icon = this.icons.faAlignJustify
                        break;
                    }
                    case 'link' : {
                        icon = this.icons.faPaperclip
                        break;
                    }
                    case 'size' : {
                        icon = this.icons.faCrop
                        break;
                    }
                    case 'imageObjectFit' : {
                        icon = this.icons.faImage
                        break;
                    }
                    case 'aspect_ratio' : {
                        icon = this.icons.faPercent
                        break;
                    }
                    case 'source' : {
                        icon = this.icons.faTags
                        break;
                    }

                    default : {
                        icon = this.icons.faPen;
                    }
                }

                return icon;
            },
            popupIcon() {
                let name = this.field.name, icon = '';
                switch (name)
                {
                    default : {
                        icon = this.icons.faPen;
                    }
                }

                return icon;
            },
            type() {
                let options = this.field.options;
                let type = options?.type;
                if((type &&
                    (type === 'typography' ||
                    type === 'border-offset' ||
                    type === 'border')) ||
                    (!type && options.fields))
                {
                    return '';
                }
                return type;
            },
            options() {
                return this.field?.options ?? {};
            },
            fields () {
                return popupTypes[this.options?.type] ?? (this.options?.fields ?? []);
            },
            valuesType() {
                if(this.type) {
                    return this.values?.[this.type];
                }
                return this.values;
            },
            optionsValues () {
                let values = this.options?.values;
                const add = (arr, obj) => {
                    if(obj.constructor === Object){
                        let k;
                        for(k in obj) {
                            arr.push({
                                name: k,
                                title: obj[k]
                            });
                        }
                    }
                };
                if(typeof values === 'string') {

                    let ts = dropdownTypes[values] ?? [];
                    if(ts.constructor === Object) {
                        let tss = [];
                        add(tss, ts);
                        ts = tss;
                    }
                    if(
                        ts.findIndex(x => x.name === '__delete__') === -1 &&
                        ts.findIndex(x => x.title === 'Default') === -1 &&
                        this.options.default !== false
                    ) {
                        ts.splice(0,0, {name: '__delete__', title: 'Default'});
                    }
                    return ts;
                }
                let type = [],
                    k,
                    value;
                if(values.constructor === Object) {
                    add(type, values);
                }
                else if(values.constructor === Array) {
                    for(k in values){
                        value = values[k];
                        if(value.constructor === Object) {
                            type.push({
                                name: value.title,
                                title: value.title,
                                disabled: true
                            });
                            if(value.values.constructor === Object) {
                                add(type, value.values);
                            }
                        }
                        else {
                            type.push({name: value, title: value});
                        }
                    }
                }
                if(this.options.default !== false &&
                    type.findIndex(x => x.name === '__delete__') === -1 &&
                    type.findIndex(x => x.title === 'Default') === -1) {
                    type.splice(0,0, {name: '__delete__', title: 'Default'});
                }
                return type;
            },
        },
        watch: {
            //refresh value when change screen
            isopen(val) {
                if(val === true) {
                    this.values = this.data || {};
                }
            },
            data: {
                immediate: true,
                deep: true,
                handler: function (val) {
                    if (this.isWatch) {
                        this.isEmit = false;
                        this.values = this.$helpers.copy(val) ?? {};
                    }
                    if (
                        this.type ==='dropdown' &&
                        this.data === undefined &&
                        this.field.value
                    ) {
                        this.$emit('update:data', this.field.value);
                    }
                    this.isWatch = true;
                    this.dGlobal.selected = this.global?.value ?? '';
                }
            },
            values: {
                immediate: true,
                deep: true,
                handler: function (val) {

                    if (this.isEmit) {
                        if(this.type && typeof val?.[this.type] === 'object' &&
                            Object.keys(val[this.type]).length === 0) {
                            delete val[this.type];
                        }
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            },
            'dGlobal.selected': function (val) {
                if (val && this.global) {
                    this.$emit('update:data', {
                        ['global-' + this.global.type]: val
                    });
                }
            }
        },
        methods: {
            backToDefault () {
                this.values = {};
            },
            addNewGlobal (title) {
                let key = this.$helpers.makeId(8);
                this.$store.dispatch('settings/addDataGlobal' , {
                    key,
                    type: this.global?.type,
                    data: {
                        title: title,
                        value: this.$helpers.copy(this.values)
                    }
                });
                this.dGlobal.selected = key;
                this.$store.commit(
                    'global/flash',
                    {
                        open: true,
                        message: 'Add Global ' + (this.global?.type === 'colors' ? 'Color' : 'Typography') + ' Success',
                        type: 'success',
                    },
                    { root: true }
                );
            },
            updateData(val) {
                if(val) {
                    if(this.type) {
                        this.values[this.type] = val;
                    }
                    else {
                        this.values = val;
                    }
                }
            }
        }
    };
</script>

<style>
div.ecom-picker__wrp{
    min-width: 20rem;
}
.ecom-fields__popup div.ecom-picker__wrp{
    background: #fff;
    padding: 0px;
    border-radius: 05px;
   
}
html.dark .ecom-fields__popup div.ecom-picker__wrp{
    background: transparent;
    min-width: 20rem;
}
#sidebar-theme-settings-form .ecom-fields__popup {
    left: -25px;
}
#sidebar-theme-settings-form  svg.rotate-180.absolute{
    left: 2rem;
}

</style>
