/**
 *
 *  Handle Editor funtion
 *
 */
import { store } from "@/store";

export const textHelper = {
    insertHtmlTag: function (selection, inline = false, oldHeadingTag = "") {
        const action = selection.action;
        const range = selection.range;
        if (!range) return;
        let tag = "";

        if (!selection.range || !action) return;

        switch (action) {
            case "bold":
                tag = "b";
                break;
            case "italic":
                tag = "i";
                break;
            case "underline":
                tag = "u";
                break;
            case "h1":
                tag = "h1";
                break;
            case "h2":
                tag = "h2";
                break;
            case "h3":
                tag = "h3";
                break;
            case "h4":
                tag = "h4";
                break;
            case "h5":
                tag = "h5";
                break;
            case "h6":
                tag = "h6";
                break;
            case "code":
                tag = "code";
                break;
            default:
                break;
        }

        if (!tag) return;

        if (selection?.state[action] || oldHeadingTag) {
            // Un tag
            const domFragement = range.cloneContents();
            if (oldHeadingTag) {
                tag = oldHeadingTag;
            }
            let node = domFragement.querySelectorAll(tag);

            if (node.length === 0) {
                // If selection range inside node tag
                let currentNode = selection.selection.focusNode;

                //Find node of tag
                while (currentNode && currentNode !== "BODY") {
                    if (currentNode?.tagName === tag.toUpperCase()) {
                        node = currentNode;
                        break;
                    }

                    currentNode = currentNode?.parentNode;
                }

                if (!currentNode) return;

                if (oldHeadingTag && currentNode) {
                    const parent = node.parentNode;
                    const tagDom = document.createElement(action);
                    tagDom.innerHTML = currentNode.innerHTML;

                    parent.insertBefore(tagDom, node);

                    parent.removeChild(node);
                    //range.deleteContents();
                    if (inline) {
                        store.commit("builder/setTextEditor", {
                            open: false
                        });
                    }
                    return;
                }

                if (domFragement.textContent.trim() === currentNode?.textContent.trim()) {
                    const parent = node.parentNode;
                    while (node.firstChild) parent.insertBefore(node.firstChild, node);
                    parent.removeChild(node);
                    range.deleteContents();
                    if (inline) {
                        store.commit("builder/setTextEditor", {
                            open: false
                        });
                    }
                } else {
                    const temp = document.createElement("span"),
                        nodeHTML = currentNode.innerHTML; //var temp: can not get innerHTML of domFragment, need insert to dom then get innerHTML.
                    temp.appendChild(range.cloneContents());

                    let before = nodeHTML.slice(0, range.startOffset),
                        after = nodeHTML.slice(
                            range.startOffset + temp.innerHTML.length,
                            nodeHTML.length
                        );
                    let html = `<${tag}>${before}</${tag}>${temp.innerHTML}<${tag}>${after}</${tag}>`;

                    const span = document.createElement("span");
                    span.innerHTML = html;

                    //Replace node with new node
                    const parent = node.parentNode;

                    while (span.firstChild) parent.insertBefore(span.firstChild, node);
                    parent.removeChild(node);
                    range.deleteContents();
                    if (inline) {
                        store.commit("builder/setTextEditor", {
                            open: false
                        });
                    }
                }
            } else {
                const parent = node[0].parentNode;
                if (oldHeadingTag) {
                    const tagDom = document.createElement(action);
                    tagDom.innerHTML = node[0].innerHTML;
                    parent.insertBefore(tagDom, node[0]);
                    parent.removeChild(node[0]);
                } else {
                    node.forEach((el) => {
                        while (el.firstChild) parent.insertBefore(el.firstChild, el);
                        parent.removeChild(el);
                    });
                }

                range.deleteContents();
                range.insertNode(parent);
            }
        } else {
            //Inser Tag
            const tagDom = document.createElement(tag);
            tagDom.appendChild(range.extractContents());
            range.insertNode(tagDom);
        }
    },

    inserColor: function (selection, color) {
        const range = selection.range;
        if (!range) return;
        const domFragement = range.cloneContents();
        let node = domFragement.querySelectorAll("font");

        if (node.length == 1 && node[0]?.tagName === "FONT") {
            node[0].setAttribute("color", color);
            range.deleteContents();
            range.insertNode(node[0]);
        } else {
            let currentNode = selection.selection.focusNode;

            //Find node of tag
            while (currentNode && currentNode !== "BODY") {
                if (currentNode?.tagName === "FONT") {
                    node = currentNode;
                    break;
                }

                currentNode = currentNode?.parentNode;
            }

            if (currentNode && currentNode?.tagName === "FONT") {
                currentNode.setAttribute("color", color);
                // const span = document.createElement('span');
                // span.innerHTML = currentNode.outerHTML;

                // //Replace node with new node
                // const parent = currentNode.parentNode

                // parent.insertBefore(span.firstChild, currentNode);
                // parent.removeChild(currentNode);
            } else {
                const tagDom = document.createElement("font");
                tagDom.setAttribute("color", color);

                tagDom.appendChild(range.extractContents());
                range.insertNode(tagDom);
            }
        }
    },

    inserBackground: function (selection, color) {
        const range = selection.range;
        if (!range) return;
        const domFragement = range.cloneContents();
        let node = domFragement.querySelectorAll('span[style*="background-color"]');

        if (node.length == 1 && node[0]?.tagName === "SPAN") {
            node[0].style.backgroundColor = color;
            range.deleteContents();
            range.insertNode(node[0]);
        } else {
            let currentNode = selection.selection.focusNode;

            //Find node of tag
            while (currentNode && currentNode !== "BODY") {
                if (currentNode?.tagName === "SPAN" && currentNode?.style?.backgroundColor) {
                    node = currentNode;
                    break;
                }

                currentNode = currentNode?.parentNode;
            }

            if (currentNode && currentNode?.tagName === "SPAN") {
                currentNode.style.backgroundColor = color;
            } else {
                const tagDom = document.createElement("span");
                tagDom.style.backgroundColor = color;

                tagDom.appendChild(range.extractContents());
                range.insertNode(tagDom);
            }
        }
    },

    insertOrderedlist: function (selection, content, action) {
        const range = selection.range;
        if (!range) return;
        const tag = action === "insertorderedlist" ? "ol" : "ul",
            domFragement = range.cloneContents();

        if (selection?.state[action]) {
            let node = domFragement.querySelector(tag);
            let currentNode = selection.selection.focusNode;
            if (node) {
                let html = "";
                const childrens = node.querySelectorAll("li");
                childrens.length > 0 &&
                    childrens.forEach((li) => {
                        html += li.innerHTML + " ";
                    });

                const span = document.createElement("span");
                span.innerHTML = html;

                //Replace node with new node
                const parent = node.parentNode;

                while (span.firstChild) parent.insertBefore(span.firstChild, node);
                parent.removeChild(node);
                range.deleteContents();
            } else {
                //Find node of tag
                while (currentNode && currentNode !== "BODY") {
                    if (currentNode?.tagName === tag.toUpperCase()) {
                        node = currentNode;
                        break;
                    }

                    currentNode = currentNode?.parentNode;
                }
                if (currentNode) {
                    let html = "";
                    const childrens = currentNode.querySelectorAll("li");
                    childrens.length > 0 &&
                        childrens.forEach((li) => {
                            html += li.innerHTML + " ";
                        });

                    const span = document.createElement("span");
                    span.innerHTML = html;

                    //Replace node with new node
                    const parent = currentNode.parentNode;

                    while (span.firstChild) parent.insertBefore(span.firstChild, currentNode);
                    parent.removeChild(currentNode);
                    range.deleteContents();
                }
            }
        } else {
            const tagDom = document.createElement(tag),
                li = document.createElement("li");

            li.appendChild(range.extractContents());
            tagDom.appendChild(li);

            range.insertNode(tagDom);
        }
    },

    removeFormat: function (selection) {
        const range = selection.range;
        if (!range) return;
        if (range.startContainer.nodeName === "#text" && range.endContainer.nodeName === "#text") {
            const node = range.startContainer.parentNode;
            if (node) {
                const parentNode = node.parentNode;
                const tagDom = document.createTextNode(node.innerText);
                parentNode.insertBefore(tagDom, node);
                parentNode.removeChild(node);
            }
        } else if (
            range.startContainer.nodeName === "#text" ||
            range.endContainer.nodeName === "#text"
        ) {
            const node =
                range.startContainer.nodeName === "#text"
                    ? range.startContainer
                    : range.endContainer;
            if (node) {
                const parentNode = node.parentNode;
                const tagDom = document.createTextNode(node.innerText);
                parentNode.insertBefore(tagDom, node);
                parentNode.removeChild(node);
            }
        } else {
            const tagDom = document.createTextNode(range.extractContents().textContent);
            range.insertNode(tagDom);
        }
    },

    insertLink: function (selection, link) {
        const range = selection.range;
        if (!range) return;
        const currentNode = selection.selection.focusNode;
        if(currentNode && currentNode.nodeName === 'A') {
          currentNode.remove();
        }
        range.insertNode(link);
    },

    removeLink: function (selection) {
        const range = selection.range;
        if (!range) return;
        const domFragement = range.cloneContents(),
            tag = "a";

        let node = domFragement.querySelector(tag);
        let currentNode = selection.selection.focusNode;
        if (node) {
            const span = document.createElement("span");
            span.innerHTML = node.textContent;

            //Replace node with new node
            const parent = node.parentNode;

            parent.insertBefore(span.firstChild, node);
            parent.removeChild(node);
            range.deleteContents();
        } else {
            //Find node of tag
            while (currentNode && currentNode !== "BODY") {
                if (currentNode?.tagName === tag.toUpperCase()) {
                    node = currentNode;
                    break;
                }

                currentNode = currentNode?.parentNode;
            }
            if (currentNode) {
                const span = document.createElement("span");
                span.innerHTML = currentNode.textContent;

                //Replace node with new node
                const parent = currentNode.parentNode;

                parent.insertBefore(span.firstChild, currentNode);
                parent.removeChild(currentNode);
                range.deleteContents();
            }
        }
    },

    insertHTML: function (selection, html) {
        const range = selection.range;
        if (!range) return;
        range.insertNode(html);
    },

    insertText: function (selection, text) {
        const range = selection.range;
        if (!range) return;
        const tagDom = document.createTextNode(text);
        range.deleteContents();
        range.insertNode(tagDom);
    }
};
