<template>
    <div
        class="ecom-picker relative"
        :class="classRoot"
    >
        <div
            v-if="optionFormated.type === 'icon' && !optionFormated.multiple"
            class="inline-flex rounded"
        >
            <button
                type="button"
                class="relative flex items-center justify-center text-gray-600 w-8 h-8 bg-coolGray-100 rounded dark:text-dark-color dark:bg-dark"
                :data-tooltip="'Click to change'"
                @click="$refs.picker.show = true"
            >
                <ecom-icon
                    :icon="faPen"
                    v-if="value.length === 0"
                    class="w-4 h-4 svg-full"
                />
                <span
                    v-else
                    class="block w-4 h-4 svg-full"
                    v-html="value[0]?.thumbnail || value[0]?.value"
                />
            </button>
        </div>
        <template v-else-if="optionFormated.simple">
            <button
                type="button"
                :style="optionFormated.type === 'font' ? 'max-width:135px;height:32px' : ''"
                class="relative w-full flex items-center min-w-30 border border-form-border rounded text-left text-form-input dark:text-dark-color text-sm h-7-5 pr-7 pl-2"
                :class="value.length ? 'pr-7' : 'pr-2'"
                @click="$refs.picker.show = true"
            >
                <template v-if="value.length">
                    <template v-if="optionFormated.type === 'font'">
                        <link rel="stylesheet" :href="fontURL(value[0])">
                        <span class="truncate" :style="{ fontFamily:value[0].name }">{{ value[0].name }}</span>
                    </template>
                    <template v-else>
                        <img
                            class="h-full rounded mr-2 py-2"
                            :src="value[0].thumbnail ?? '/images/placeholder.png'"
                        >
                        <span class="truncate" v-html="value[0].name" />
                    </template>
                </template>
                <span v-else class="truncate dark:text-dark-color">
                    {{ optionFormated.title }}
                </span>
                <svg class="absolute top-1/2 right-3 transform -translate-y-1/2 w-2 h-3 dark:text-dark-color fill-current" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M4 12L0 8H8L4 12ZM4 0L8 4H0L4 0Z" fill="currentColor" />
                </svg>
            </button>
        </template>
        <template v-else>
            <div
                v-if="!value.length"
                class="flex items-center justify-center border dark:border-gray-700 border-gray-200 hover:border-primary-600 py-6 rounded w-full h-full dark:bg-dark"
            >
                <Button
                    :text="options.button_text"
                    @click="$refs.picker.show = true"
                />
            </div>
            <div v-else>

                <PickerPreview
                    :value-prop="value"
                    :options="optionsPreview"
                    @update:temp="updateTemp"
                    @update:image="updateImage"
                    @selectItem="selectItem"
                    @update:modify="updateModify"
                />
                <span
                    class="flex flex-col relative p-2  border-white divide-y dark:border-transparent dark:divide-gray-600"
                    :class="[options?.layout==='list' ? 'border-t-2': 'border-t-4']"
                >
                    <!-- <div class="flex" v-if="options.multiple">
                        <p class="text-gray-600 dark:text-dark-color font-bold text-sm"> Selected: ({{ value.length}}) </p>
                    </div> -->
                    <div class="flex gap-3 py-2">
                        <button
                            class="inline-flex justify-center items-center font-medium smoothing h-10 text-sm px-3 w-1/2 rounded rounded-r-none border border-gray-300 dark:border-dark text-gray-500 shadow-sm bg-white dark:bg-dark  dark:text-dark-color dark:hover:bg-dark dark:active:bg-dark hover:bg-gray-100 active:bg-gray-200 focus:ring-primary-600"
                            @click="$refs.picker.show = true"
                        >
                            {{textButton.change}}
                        </button>
                        <button
                            class="inline-flex justify-center items-center font-medium smoothing h-10 text-sm px-3 w-1/2 rounded rounded-l-none border border-gray-300 dark:border-dark text-red-600 hover:text-red-800 dark:bg-dark  dark:text-dark-color shadow-sm bg-white hover:bg-gray-100 active:bg-gray-200 focus:ring-red-600 dark:hover:bg-dark dark:active:bg-dark"
                            @click="deleteItem"
                        >
                            {{textButton.delete}}
                        </button>
                    </div>
                </span>
            </div>
        </template>
        <PickerComponent
            ref="picker"
            :value="selected ?? value"
            :is-change="isChange"
            :options="optionFormated"
            @isSelected="isSelected"
            @update:tabs="updateTabs"
        />
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import PickerComponent from './PickerPopup.vue';
    import PickerPreview from './PickerPreview.vue';
    import { faFaceSmilePlus, faPen } from '@fortawesome/pro-light-svg-icons';
    export default {
        name: 'Picker',
        components: {
            PickerComponent,
            PickerPreview
        },
        props: {
            valueProp: {
                type: Array,
                default(){
                    return [];
                }
            },
            options: {
                type: Object,
                default(){
                    return {};
                }
            },
        },
        emits: ['update:data', 'update:tabs'],
        data(){
            return{
                search: '',
                selected: null,
                selectedTemp: null,
                selectedIndex: -1,
                value: '',
                valueTemp: '',
                isWatch: true,
                isEmit: false,
                faFaceSmilePlus,
                faPen
            };
        },
        computed: {
            optionFormated(){
              return {
                title: this.options?.title ?? '',
                useSearch: this.options?.search ?? true,
                multiple: this.selected ? false : (this.options?.multiple ?? false),
                useDelete: this.options?.delete ?? false,
                drag: this.options?.drag ?? false,
                layout: this.options?.layout ?? 'grid',
                type: this.options?.type ?? 'menu',
                editAlt: this.options?.editAlt ?? false,
                modify: this.options?.modify ?? [],
                tabs: this.options?.tabs ?? {},
                simple: this.options?.simple ?? false,
                responsive: this.options?.responsive ?? false,
                textSelect: this.selected ? 'Update' : 'Select',
                position: this.options?.position ?? 'absolute',
              };
            },
            optionsPreview(){
              let { useDelete , layout, type, drag, modify, editAlt} = this.optionFormated;
              let multiple = this.options?.multiple ?? false;
              return {
                useDelete, layout, type, drag, multiple, modify, editAlt
              };
            },
            kValue() {
                let k = 'value';
                if (this.screen !== 'desktop') {
                    k += '__' + this.screen;
                }
                return k;
            },
            isChange(){
                if(this.selected) {
                    return JSON.stringify(this.selectedTemp) != JSON.stringify(this.selected);
                }
                return JSON.stringify(this.valueTemp) != JSON.stringify(this.value);
            },
            textButton(){
              return {
                change: this.optionFormated.multiple || this.selected ? 'Add more' : 'Change',
                delete: this.optionFormated.multiple || this.selected ? 'Remove all' : 'Delete',
                setting: 'Settings'
              };
            },
            isFullImage(){
                return (this.options.type == 'image' && !this.options.multiple);
            },
            classRoot(){
                let css = '';
                if(!this.optionFormated.simple && (this.optionFormated.type !== 'icon' || this.optionFormated.multiple)) {
                    css += 'w-full max-w-xs bg-gray-100 dark:bg-gray-700 mt-2 rounded';
                }
                else {
                    css += 'flex';
                }

                return css;
            },
            iconUploaded() {
                return this.value.length && !('cate' in this.value[0]);
            },
            iconSystem() {
                return this.value.length && 'cate' in this.value[0];
            },
            ...mapGetters('builder', ['screen']),

        },
        watch: {
            value: {
                deep: true,
                immediate: true,
                handler: function(val){
                if(this.isEmit){
                    this.isWatch = false;
                    this.$emit('update:data', this.cloneData(val));
                    if(this.optionFormated?.tabs?.cur) {
                        this.updateTabs(this.optionFormated?.tabs?.cur)
                    }
                }
                this.isEmit = true;
                }
            } ,
            valueProp: {
                deep: true,
                immediate: true,
                handler: function(val){
                if(this.isWatch){
                    this.isEmit = false;
                    this.value = this.cloneData(val),
                    this.valueTemp = this.cloneData(val);
                }
                this.isWatch = true;

                }
            },
            selected: {
                deep: true,
                handler: function(val) {
                    let item = val?.[0];
                    if(item) {
                        this.value[this.selectedIndex] = item;
                    }
                }
            }
        },
        methods: {
            cloneData(data){
                if(!data || typeof data !== 'object') return data;
                return JSON.parse(JSON.stringify(data));
            },
            selectItem({item, i}) {
                this.selectedIndex = i;
                this.selected = [item];
                this.selectedTemp = this.cloneData([item]);
                this.$refs.picker.show = true;
            },
            updateSearch(val){
                this.search = val;
            },
            fontURL({name, value:src}){
                return this.$helpers.getLinkFont(name, src);
            },
            updateModify() {
                this.valueTemp = this.cloneData(this.value);
            },
            isSelected(val){
                this.selected = null;
                this.selectedTemp = null;
                this.selectedIndex = -1;
                if(!val && this.isChange){
                    this.value = this.cloneData(this.valueTemp);
                }else{
                    this.updateTemp();
                }
            },
            updateTemp(){
              this.valueTemp = this.cloneData(this.value);
            },
            selectIconUpload() {
                this.updateTabs('uploaded');
                this.$refs.picker.show = true;
            },
            selectIconSystem() {
                this.updateTabs('system');
                this.$refs.picker.show = true;
            },
            updateTabs(val){
                this.$emit('update:tabs', val);
            },
            updateImage(val){
              this.value = this.cloneData(val);
              this.valueTemp = this.cloneData(val);
            },
            deleteItem(){
                this.value = [];
                this.valueTemp = [];
            }
        }
    };
</script>