<template>
    <Picker
        :value-prop="value"
        :options="options"
        @update:data="updateData"
        @update:tabs="updateTabs"
    />
</template>

<script lang="ts">
    import Picker from '../../components/picker/Picker.vue';
    export default {
        name: 'PickerField',
        components: {
            Picker
        },
        props: {
            field: {
                type: Object,
                default(){
                    return {};
                }
            },
            // eslint-disable-next-line vue/require-default-prop
            data: {
                type: [Array, String, Object]
            }
        },
        emits: ['update:data'],
        data(){
            return {
                value: [],
                tabs: '',
                defaultOptions: {
                    search: true,
                    multiple: false,
                    delete: true,
                    drag: true,
                }
            };
        },
        computed: {
            type(){
                return this.field?.options?.type ?? 'image';
            },
            options(){
                let options = this.field?.options;
                options = Object.assign(this.defaultOptions, options, {type: this.type});
                let title = 'Select ' + this.type.replace(/s$/g, '').replace(/\_/g,' ');
                options.button_text = title;
                options.title = title;
                options.layout = this.type === 'font' ? 'list' : (options.layout??'grid');
                if(this.type === 'image'){
                    options.tabs = {
                        cur: this.tabs || 'uploaded',
                        list: [
                            {value: 'uploaded', text: 'Uploaded'},
                            {value: 'free', text: 'Free Images'},
                        ]
                    };
                }
                else if(this.type === 'video'){
                    options.tabs = {
                        cur: 'uploaded',
                        list: [
                            {value: 'uploaded', text: 'Uploaded'}
                        ]
                    };
                }
                else if(this.type === 'audio'){
                    options.tabs = {
                        cur: 'uploaded',
                        list: [
                            {value: 'uploaded', text: 'Uploaded'}
                        ]
                    };
                }
                else if(this.type === 'icon'){
                    options.tabs = {
                        cur: this.tabs || 'system',
                        list: [
                            {value: 'system', text: 'System'},
                            {value: 'uploaded', text: 'Uploaded'},
                        ]
                    };
                }
                else if(this.type === 'font'){
                    options.tabs = {
                        cur: this.tabs || 'uploaded',
                        list: [
                            {value: 'uploaded', text: 'Uploaded'},
                            {value: 'google', text: 'Google Fonts'},
                        ]
                    };
                }
                else if(this.type === 'section')
                {
                    options.tabs = {
                        cur: 'created',
                        list: [{
                            value: 'created',text: 'Created'
                        }]
                    }
                }
                return options;
            },
            output(){
                return this.field?.options?.output ?? false;
            }
        },
        watch: {
            data: {
                immediate: true,
                deep: true,
                handler: function(){
                    this.value = this.getValue();
                }
            }
        },
        methods: {
             cloneData(data){
                if(!data || typeof data !== 'object') return data;
                return JSON.parse(JSON.stringify(data));
            },
            getValue(){
                let valueTemp = this.cloneData(this.data ?? (this.field?.value ?? []));
                if(valueTemp.constructor === Array){
                    valueTemp.forEach((element, index) => {
                        if(typeof element === 'object'){
                            element.id = element?.id ?? this.$helpers.makeId(8),
                            element.fn = 'item';
                        }
                        else if(typeof element ==='string'){
                            valueTemp[index] = {
                                [this.output] : element,
                                id: element?.id ?? this.$helpers.makeId(8),
                                fn: 'item'
                            };
                        }
                    });
                }
                else if(valueTemp.constructor === String){
                    valueTemp = [{
                        id: this.$helpers.makeId(8),
                        fn: 'item',
                        value: valueTemp
                    }];
                }
                else if(valueTemp.constructor === Object){
                    if(Object.keys(valueTemp).length) {
                        valueTemp.id = valueTemp?.id ?? this.$helpers.makeId(8);
                        valueTemp = [valueTemp];
                    }
                    else {
                        valueTemp = [];
                    }
                }
                return valueTemp;
            },
            updateTabs(val){
                this.tabs = val
            },
            updateData(val){

                let valTemp = this.cloneData(val);
                let output = valTemp;
                if(this.output){
                    if(this.options.multiple){
                        if(typeof this.output === 'string') {
                            output = valTemp.map(x => x[this.output]);
                        }
                        else if(typeof this.output === 'object'){
                            output = valTemp.map(x => {
                                let rs = {};
                                this.output.forEach(k => rs[k] = x[k]);
                                return rs;
                            });
                        }
                    }else{
                        if(typeof this.output === 'string') {
                            output = valTemp?.[0]?.[this.output] ?? '';
                        }
                        else if(typeof this.output === 'object'){
                            let outputFirst = valTemp?.[0];
                            let rs = {};
                            if(outputFirst) {
                                this.output.forEach(k => rs[k] = outputFirst[k]);
                            }
                            output = rs;
                        }
                    }
                }
                else {
                    if(!this.options.multiple){
                        output = valTemp?.[0] ?? {};
                    }
                }
                this.$emit('update:data', output);
            }
        }
    };
</script>
