<template>
  <!-- <div class="inline-flex">
      <input
          :id="path ? path : null"
          v-model="enabled"
          type="checkbox"
          class="focus:ring-0 focus:ring-transparent h-5 w-5 transform scale-90 text-primary-600 border-gray-400 border-2 rounded cursor-pointer"
      >
</div> -->
  <div
    class="w-10 h-6 inline-flex items-center bg-gray-200 dark:bg-gray-600 rounded-full p-1 duration-200 cursor-pointer"
    :class="{ 'bg-primary-600 dark:bg-primary-600': enabled }"
    :aria-checked="value.toString()"
    @click="toggle"
  >
    <div
      class="bg-white dark:bg-gray-300 w-4 h-4 rounded-full shadow-md transform duration-200"
      :class="{ 'translate-x-4': enabled }"
    />
    <input
        :id="path ? path : null"
        v-model="enabled"
        type="checkbox"
        class="hidden"
    >
  </div>
</template>

<script>
  export default {
      name: 'SwitchField',
      components: {

      },
      props: {
          // eslint-disable-next-line vue/require-default-prop
          data: [String, Boolean, Number],
          path: {
              type: String,
              default: ''
          },
          field: {
              type: Object,
              default(){
                  return {};
              }
          }
      },
      emits: ['update:data'],
      data(){
          return {
              enabled: false,
              isWatch: true,
              isEmit: false
          };
      },
      computed: {
          options(){
              return this.field?.options ?? {};
          },
          optionValues(){
              return this.options.values ?? {};
          },
          currentValue(){
              return this.optionValues[this.enabled ? 'on' : 'off'];
          },
          text(){
              return this.currentValue?.label ?? 'OFF';
          },
          value(){
              return this.currentValue?.value ?? false;
          }
      },
      watch: {
          data: {
              immediate: true,
              handler: function(val){
                  if(this.isWatch){
                      this.isEmit = false;
                      this.enabled = this.getEnable(val);
                  }
              this.isWatch = true;
              }
          },
          value: {
              immediate: true,
              handler: function(val){
                  if(this.isEmit){
                      this.isWatch = false;
                      this.$emit('update:data', val);
                  }
                  this.isEmit = true;
              }
          }

      },
      methods: {
          getEnable(value){
              let result = this.options?.default ?? false;
              for(let k in this.optionValues){
                  if(this.optionValues[k].value === value){
                      result = k === 'on';
                  }
              }
              return result;
          },
          toggle() {
            this.enabled = !this.enabled;
          }
      }
  };
</script>
<style>
  .shadow-toggle{
      box-shadow: inset 0 1px 2px rgb(0 0 0 / 12%), inset 0 0 2px rgb(0 0 0 / 15%);
  }
  .shadow-switch {
      box-shadow: -1px 1px 5px rgb(0 0 0 / 20%);
  }
  .text-tiny{
      font-size: 0.65rem;
      line-height: 2;
  }
</style>