<template>
    <div
        v-if="payload.show"
        class="absolute z-50 p-4 bg-white dark:bg-gray-700 dark:text-dark-color rounded left-2 right-2 shadow-popup ecom-popup-dimension"
        :style="{top: payload.pos.top + 'px'}"
    >
         <span class="text-white dark:text-gray-700">
            <svg viewBox="0 0 213.3 213.3" class="absolute top-0 w-4 h-4 -mt-3 transform rotate-180 -translate-x-1/2 left-1/2 fill-current">
            <polygon class="shadow-xxl" points="0,53.333 106.667,160 213.333,53.333" />
        </svg>
         </span>
        <Number
            v-model:data="value"
            :field="{options: unitConfigs, focus: true}"
            class="mb-4"
        />
        <ul class="flex items-center flex-wrap -mx-0.5 -mb-1">
            <li v-for="v in samples"
                :key="v"
                class="px-2 capitalize py-1 mx-0.5 mb-1 bg-white dark:bg-dark dark:text-dark-color dark:hover:bg-dark border border-gray-300 dark:border-gray-600 rounded text-xs text-form-input hover:bg-gray-100 cursor-pointer transition"
                @click="value = v === 'auto' ? v : parseFloat(v)"
            >
                {{ v }}
            </li>
        </ul>
        <button
            v-if="value !== '' && value !== undefined"
            type="button"
            class="flex items-center mt-4 text-xs transition group text-form-input hover:text-primary-600 dark:text-dark-color"
            @click="value = ''"
        >
            <ecom-icon
                :icon="faArrowsRotate" class="w-3 h-3 text-gray-600 dark:text-dark-color mr-1.5 group-hover:text-primary-600 transition"
            />
            Reset
        </button>
    </div>
  </template>

  <script lang="ts">
    import { faArrowsRotate } from '@fortawesome/pro-light-svg-icons';
    import Number from '../number/Number.vue';
    export default {
        name: 'Input',
        components: {
            Number
        },
        props: {
            payload: {
                type: Object,
                default() {
                    return  {};
                }
            },
            unitConfigs: {
                type: Object,
                default() {
                    return {};
                }
            },
            type: {
                type: String,
                default: ''
            }
        },
        emits: ['update:number'],
        data() {
            return {
                acceptClose: true,
                faArrowsRotate
            };
        },
        computed: {
            samples() {
                let samples = [];
                if(this.type === 'margin') {
                    samples.push('auto');
                }
                if(this.unitConfigs.max <= 0) {
                    return [];
                }
                let length = 8;
                let p = (this.unitConfigs.max - (this.unitConfigs.min < 0 ? 0 : this.unitConfigs.min)) / length;
                samples.push(...Array.from({length}, (_, i) => Math.round(i * p * 2) / 10));
                return samples;
            } ,
            _payload() {
                return this.payload;
            },
            value: {
                get(){
                    return this.payload.value;
                },
                set(val) {
                    this.$emit('update:number', val);
                }
            },
        },
        methods: {
            closePopup() {
                if(!this.acceptClose) {
                    this.$parent.input = {};
                    this.$parent.current = {};
                }
                this.acceptClose = true;
                document.removeEventListener('click', this.doClose);
            },
            doClose(e) {
              if (
                  e === undefined ||
                  !e.target.closest('.ecom-popup-dimension')
              ) {
                  this.closePopup();
              }
          }
        },
        watch: {
            'payload.show': {
              deep: true,
              immediate: true,
              handler: function(val){
                  if(val) {
                      setTimeout(() => document.addEventListener('click', this.doClose), 500);
                  } else {
                      document.removeEventListener('click', this.doClose);
                  }
              }
          }
        }
    };
  </script>
