<template>
    <li
        ref="content"
        v-detectVisible="detectVisible"
        class="h-10"
    >
        <button
            v-if="show"
            type="button"
            class="flex items-center w-full h-full px-4 py-2 text-sm text-gray-600 transition border-0 border-l-2 border-transparent hover:bg-gray-100 focus:outline-none focus:border-blue-700 dark:text-dark-color dark:hover:text-primary-600 dark:hover:bg-dark"
            :class="{'bg-gray-100 border-blue-700' : src.endsWith(item.value)}"
            :disabled="src.endsWith(item.value)"
        >
            <span class="float-left w-5 h-5 mr-4 min-w-5">
                <img
                    class="block object-cover w-full h-full rounded"
                    :src="item.thumbnail ?? '/images/placeholder.png'"
                    :alt="item.name"
                >
            </span>
            <span class="truncate">{{ item.name }}</span>
        </button>
    </li>
</template>

<script lang="ts">
    import { detectVisible } from '../../../directives';

    export default {
        name: 'ItemContent',
        directives: {
            detectVisible
        },
        props: {
            item: {
                type: Object,
                default(){
                    return {};
                }
            },
            src: {
                type: String,
                default: ''
            },
        },
        data(){
            return{
                show: false,
            };
        },
        methods: {
            detectVisible(){
                this.show = true;
            }
        }
    };
</script>
