const modules = import.meta.glob('./**/*.vue', {
    eager: true
});

const fields: any = {};

for (let path in modules) {
    if (path && path.split('/').pop()) {
        let name = ((path.split('/') as string[]).pop() as string).replace(/\.vue/,'').toLowerCase() + 'Field';

        if (name !== 'fieldsField') {
            fields[name] = modules[path].default;
        }
    }
}
export default fields;
