<template>
    <div class="relative p-2 max-h-1/3 overflow-y-auto">
        <button
            v-for="v in values"
            :key="v.name"
            class="whitespace-nowrap text-left w-full rounded text-sm flex min-w-full px-2 py-2 mb-0.5 transition text-gray-800 dark:hover:bg-transparent dark:hover:text-primary-600 hover:bg-gray-100 hover:text-gray-900"
            :class="isActive(v) ? 'relative bg-primary-50 dark:bg-gray-600' : ''"
            @click="choose(v.name)"
        >
            <span class="truncate text-sm dark:text-dark-color dark:hover:text-primary-600">{{ v.title }}</span>
            <span
                v-if="isActive(v)"
                class="absolute -left-2 rounded-r top-0 h-full bg-primary-600"
                style="width:3px"
            />
        </button>
    </div>
</template>

<script>
    export default {
        name: 'PopupDropdown',
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: {
                type: [Number, String, Boolean]
            },
            values: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        emits: ['update:data', 'close'],
        computed: {
            value() {
                return this.data;
            }
        },
        methods: {
            isActive(item) {
                return this.value === item.name ||
                    (item.name === '__delete__' &&
                        (this.value === undefined ||
                        this.value === '__delete__') ||
                        this.value === '__empty__'
                    );
            },
            choose(val) {
                this.$emit('update:data', val);
                this.$emit('close');
            }
        }
    };
</script>
