<template>
    <div
        class="relative"
        :style="(field.options?.control_width ? 'width:'+field.options?.control_width : '')"
    >
        <select
            v-model="value"
            class="w-full px-2 py-0 text-sm transition border rounded border-form-border dark:border-gray-600 text-form-input focus:outline-none focus:ring-offset-1 focus:border-gray-300 focus:ring-2 focus:ring-primary-400 dark:focus:ring-transparent dark:focus:ring-offset-transparent pr-7 select-hide-arrow cursor-pointer dark:bg-dark dark:text-dark-color"
            :class="[options?.oneline || options?.half || field.options?.control_width ? 'h-8' : 'h-9', cl]"
            :style="options?.oneline ? 'min-width:'+(options?.minWidth ?? '135px;') : ''"
        >
            <option
                v-for="t in types"
                :key="t.name"
                :value="t.name"
                :disabled="t.disabled"
                v-html="t.title"
            />
        </select>
        <svg
            class="absolute w-2 text-gray-500 dark:text-dark-color transform -translate-y-1/2 top-1/2"
            :class="options?.oneline || options?.half ? 'h-2.5 right-2' : 'h-3 right-3'"
            viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg"
        >
            <path fill-rule="evenodd" clip-rule="evenodd" d="M4 12L0 8H8L4 12ZM4 0L8 4H0L4 0Z" fill="currentColor" />
        </svg>
    </div>
</template>

<script lang="ts">

    import { dropdownTypes } from '../../constants';

    export default {
        name: 'DropdownField',
        components: {
        },
        props: {
            data: {
                type: [String, Number],
                default(param) {
                    return typeof param === 'number' ? 0 : '__delete__';
                }
            },
            cl: {
                type: String,
                default: ''
            },
            field: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data () {
            return {
                value: '__delete__',
                search: '',
                isWatch: true,
                isEmit: false
            };
        },
        computed: {
            options () {
                return this.field?.options ?? {};
            },
            type () {
                return this.options?.type;
            },
            optionsValue(){
                return this?.options?.values ?? {};
            },
            types () {
                if(this.type) {
                    let ts = dropdownTypes[this.type] ?? [];
                    if(
                        ts.findIndex(x => x.name === '__delete__') === -1 &&
                        ts.findIndex(x => x.title === 'Default') === -1 &&
                        this.options.default !== false
                    ) {
                        ts.splice(0,0, {name: '__delete__', title: 'Default'});
                    }
                    return ts;
                }
                const add = (arr, obj) => {
                    if(obj.constructor === Object){
                        let k;
                        for(k in obj) {
                            arr.push({
                                name: k,
                                title: obj[k]
                            });
                        }
                    }
                };
                let type = [],
                    k,
                    value;
                if(this.optionsValue.constructor === Object) {
                    add(type, this.optionsValue);
                }
                else if(this.optionsValue.constructor === Array) {
                    for(k in this.optionsValue){
                        value = this.optionsValue[k];
                        if(value.constructor === Object) {
                            type.push({...value});
                            // if(value.values?.constructor === Object) {
                            //     add(type, value.values);
                            // }
                        }
                        else {
                            type.push({name: value, title: value});
                        }
                    }
                }
                if(this.options.default !== false &&
                    type.findIndex(x => x.name === '__delete__') === -1 &&
                    type.findIndex(x => x.title === 'Default') === -1) {
                    type.splice(0,0, {name: '__delete__', title: 'Default'});
                }
                return type;
            },
            hasDisabled () {
                return this.types.some(type => type.disabled);
            },
            typesFiltered () {
                return this.types.filter(type => {
                    return type.disabled || type.name.indexOf(this.search) > -1;
                });
            },
        },
        watch: {
            data: {
                immediate: true,
                handler: function () {
                    if (this.isWatch) {
                        this.isEmit = false;
                        this.setValue();
                    }
                    this.isWatch = true;

                }
            },
            value: {
                immediate: true,
                handler: function (val) {
                    if (this.isEmit) {
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }

        },
        created () {
            this.setValue();
        },
        methods: {
            classItem (act, selected, disabled) {
                let cl = '';
                if (disabled) {
                    cl += 'text-gray-400 font-medium';
                } else {
                    if (act) {
                        cl += (this.hasDisabled ? 'px-4 ' : '') + 'bg-gray-300 ';
                    } else {
                        if (selected) {
                            cl += 'text-gray-700 ';
                        }
                        cl += (this.hasDisabled ? 'px-4 ' : '') + 'cursor-pointer text-gray-600 bg-white hover:text-white hover:bg-primary-600 dark:hover:text-primary-600 dark:text-dark-color dark:bg-dark';
                    }
                }
                return cl;
            },
            setValue () {
                this.value = this.data ?? (this.field?.value ?? false);
                if (this.value === false) {
                    let defaultValue = this.options?.default;
                    if (defaultValue) {
                        this.value = defaultValue;
                        this.isEmit = false;
                    } else {
                        this.value = '';
                    }
                }
            },
            textOfValue (val) {
                return this.types.reduce((acc, cur) => {
                    if (cur.name === val) {
                        acc = cur.title;
                    }
                    return acc;
                }, 'Default');
            }
        }
    };
</script>
<style>
    .ecom-field-select {
        -webkit-appearance: none;
        -moz-appearance: none;
        background: transparent;
    }
    .select-hide-arrow {
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        background-image: none;
    }
</style>
