export default {
    computed: {
        options() {
            return this.field?.options;
        },
        format() {
            let format = this.field?.options?.format;
            if(!format) {
                switch(this.field?.options?.type) {
                    case 'datetime':
                        format = 'MM DD, YYYY hh:mmZ';
                        break;
                    case 'time':
                        format = 'hh:mm';
                        break;
                    default:
                        format = 'MM DD, YYYY';
                        break;
                }
            }
            return format;
        },
        min() {
            return this.options.min || '';
        },
        max() {
            return this.options.max || '';
        },
        range() {
            return [this.min, this.max];
        },
        minDate() {
            let date = new Date(this.options?.min);
            return date instanceof Date && !isNaN(date) ? date : new Date('January 1, 2020');
        },
        maxDate() {
            let date = new Date(this.options?.max);
            return date instanceof Date && !isNaN(date) ? date : new Date('January 1, 2050');
        },
    },
    methods: {
        getValueFormat(values) {
            const validateValue = (type, v) => {
                if(v === undefined) {
                    if (type === 'hour' && values['minute'] !== undefined) {
                        return '12';
                    }
                    else if (type === 'minute' && values['hour'] !== undefined) {
                        return '0';
                    }
                }
                return this.num2digits(v);
            };
            let regexs = {
                day: /DD/,
                month: /MM/,
                year: /YYYY/,
                hour: /hh/,
                minute: /mm/,
                gmt: /Z/,
            };
            let value = this.format;
            for(let r in regexs) {
                let v = validateValue(r, values[r]);
                if(v) {
                    if(r === 'month') {
                        value = value.replace(regexs[r], parseInt(v));
                    } else if(r === 'gmt') {
                        value = value.replace(regexs[r], ` GMT${v}`);
                    }
                    else {
                        value = value.replace(regexs[r], v);
                    }
                }
            }
            return value ;
        },
        num2digits(num) {
            if(typeof num !== 'number') {
                return num;
            }
            return (num < 10 ? '0' : '') + num;
        }
    }
}