<template>
    <div
        class="relative"
        :class="isSimple ? 'h-22' : 'h-40'"
    >
            <div
                v-if="current.name && !input.show"
                class="fixed top-0 left-0 z-50 w-full h-full"
                :style="{cursor}"
            />

        <div
            v-if="unitsLength > 1"
            class="absolute right-0 flex flex-wrap mb-1 bottom-full"
        >
            <button
                v-for="key in unitsKey"
                :key="key"
                type="button"
                class="px-1 uppercase border-0 focus:outline-none focus:ring-0 min-w-5 text-tiny"
                :class="unit === key ? 'underline text-primary-600' : 'dark:text-dark-color text-form-secondary-hover'"
                @click="unit = key"
            >
                {{ key }}
            </button>
        </div>
        <Box
            v-if="isSimple"
            v-model:data="valueInput"
            v-model:current="current"
            size="small"
            class="w-full"
            v-bind="bindAttr"
            @click-input="clickInput"
            @close-input="closeInput"
        />
        <template v-else>
            <Box
                v-model:data="valueInput['margin']"
                v-model:current="current"
                type="margin"
                v-bind="bindAttr"
                @click-input="clickInput"
                @close-input="closeInput"
            />
            <Box
                v-model:data="valueInput['padding']"
                v-model:current="current"
                type="padding"
                size="small"
                class="w-57"
                :linked="linked"
                :unit-configs="unitConfigs"
                :v-bind="bindAttr"
                @click-input="clickInput"
                @close-input="closeInput"
            />
        </template>
        <button
            class="absolute flex items-center justify-center transition transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2 ring-gray-300 dark:ring-gray-600 z-1 box-sizing"
            :class="[linked ? 'bg-primary-600 ring-0 text-white hover:bg-primary-500' : 'bg-white dark:bg-dark dark:text-dark-color ring-1 hover:bg-primary-600 hover:text-white',
                        !isSimple ? 'rounded-sm w-10 h-6': 'rounded w-9 h-7'
                    ]"
            type="button"
            :data-tooltip="linked ? 'Unlink' : 'Link'"
            @click="linked = !linked"
        >
            <ecom-icon :icon="icons.faLink" class="w-4 h-4" />
        </button>
        <InputComponent
            ref="input"
            v-model:number="input.value"
            :payload="input"
            :unit-configs="unitConfigs"
            :type="current.type"
        />
    </div>
</template>

<script lang="ts">
    import {faLink} from '@fortawesome/pro-light-svg-icons';
    import InputComponent from './Input.vue';
    import Box from './Box.vue';

    import { dragMixin } from '../../mixins'

    export default {
        name: 'Dimension',
        components: {
            Box,
            InputComponent
        },
        mixins: [dragMixin],
        props: {
            data: Object,
            field: {
                type: Object,
                default() { return {} ;}
            }
        },
        emits: ['update:data'],
        data() {
            return {
                icons: {
                    faLink
                },
                linked: false,
                isWatch: true,
                isEmit: false,
                current: {},
                unit: null,
                input: {
                    value: null,
                    show: false,
                    pos: {}
                },
                valueInput: {}
            };
        },
        computed: {
            bindAttr() {
                return {
                    unit: this.unit,
                    linked: this.linked,
                    field: this.field,
                    unitConfigs: this.unitConfigs
                };
            },
            options() {
                return this.field.options ?? {};
            },
            units() {
                let unitsDefault = {
                    margin: {
                        px: {
                            min: -500,
                            max: 500
                        },
                        '%': {
                            min: -100,
                            max: 100
                        },
                        em: {
                            min: -100,
                            max: 100
                        },
                        rem: {
                            min: -100,
                            max: 100
                        }
                    },
                    padding: {
                        px: {
                            min: 0,
                            max: 500
                        },
                        '%': {
                            min: 0,
                            max: 100
                        },
                        em: {
                            min: 0,
                            max: 100
                        },
                        rem: {
                            min: 0,
                            max: 100
                        }
                    },
                    radius: {
                        px: {
                            min: 0,
                            max: 400
                        },
                        '%': {
                            min: 0,
                            max: 100
                        }
                    }
                };
                let units = this.options.units;
                if(units === 'default') {
                    if(this.isSimple) {
                        if(this.isRadius) {
                            return unitsDefault.radius;
                        }
                        return unitsDefault[this.fieldName ?? 'padding'];
                    }
                    return {
                        margin: unitsDefault.margin,
                        padding: unitsDefault.padding
                    };
                }
                return units ?? {};
            },
            unitsKey(){
                if(this.isSimple) {
                    return Object.keys(this.units);
                }
                else {
                    return Object.keys(this.units.margin ?? {});
                }
            },
            unitsLength(){
                return this.unitsKey.length;
            },
            unitConfigs() {
                let configs = {};
                let { type } = this.current;
                let d = {
                    min: type === 'margin' ? -1000 : 0,
                    max: 1000,
                    step: 1
                };
                ['min', 'max', 'step'].forEach(c => {
                    let units = !this.isSimple ? this.units?.[type] : this.units;
                    configs[c] = units?.[this.unit]?.[type]?.[c] ??
                        units?.[this.unit]?.[c] ??
                        this.options?.[c] ?? d[c];
                });
                return configs;
            },
            isSimple() {
                return this.options.simple || this.options.type === 'radius';
            },
            isRadius() {
                return this.options.type === 'radius';
            },
            values(){
                let values = {};
                const set = (obj, vs) => {
                    for(let key in vs){
                        let v = vs[key];

                        if(v !== undefined && v !== null && v !== '' && ((isNaN(v) && typeof v !== 'number') || (!isNaN(v) && typeof v === 'number'))) {
                            if(!this.unit){
                                obj[key] = v;
                            }
                            else {
                                obj[key] = typeof v === 'number' ? (v + this.unit.replace('NaN', '')) : v;
                            }
                        }
                    }
                };
                if(!this.isSimple) {
                    for(let type in this.valueInput) {
                        values[type] = values[type] ?? {};
                        set(values[type], this.valueInput[type]);
                    }
                }
                else {
                    set(values, this.valueInput);
                }

                return values;
            },
            cursor() {
                return this.dragDir(this.current.type || this.fieldName, this.current.name)?.c;
            },
            fieldName() {
                return Object.keys(this.field.css?.properties ?? {})[0] ?? this.field.name;
            }
        },
        watch: {
            values: {
                immediate: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            } ,
            data: {
                deep: true,
                immediate: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;
                        this.extractData(val ?? {});
                    }
                    this.isWatch = true;

                }
            },
            'input.value': function(val) {
                let {type, name} = this.current;
                if(name) {
                    if(val !== '') {
                        let v;
                        if(type) {
                            this.valueInput[type] = this.valueInput[type] ?? {};
                            v = this.valueInput[type];
                        }
                        else {
                            v = this.valueInput;
                        }
                        v[name] = val;
                    }
                    else {
                        if(type) {
                            delete this.valueInput?.[type]?.[name];
                            if(Object.keys(this.valueInput[type]).length === 0) {
                                delete this.valueInput[type];
                            }
                        }
                        else {
                            delete this.valueInput?.[name];
                        }

                    }

                }

            }
        },
        methods: {
            getUnit(value){
                let unit = '';
                if(typeof value === 'string'){
                    unit = /\D+$/g.exec(value)?.[0] ?? '';
                }
                return unit;
            },
            extractData(values){
                if(Object.keys(values).length){
                    this.link = false;
                    let valueInput = {};
                    let unit = '';
                    const set = (obj, v) => {
                        for(let key in v){
                            if(v[key] !== undefined) {
                                obj[key] = v[key] === 'auto' ? v[key] : parseFloat(v[key]);
                            }

                            unit = this.getUnit(v[key]);

                        }
                    };
                    if(!this.isSimple) {
                        for(let type in values) {
                            valueInput[type] = valueInput[type] ?? {};
                            set(valueInput[type], values[type]);
                        }
                    }
                    else {
                        set(valueInput, values);
                    }
                    this.unit = unit || (this.unitsKey[0] ?? '');
                    this.valueInput = valueInput;
                }
                else {
                    this.unit = this.unitsKey?.[0] ?? '';
                    this.valueInput = {};
                }
                this.linked = this.isLinked(this.valueInput);
            },
            isLinked(obj) {
                const sameValue = (o) => {
                    if(o !== null && typeof o === 'object') {
                        let vs = Object.values(o);
                        if(vs.length === 4) {
                            return vs[0] === vs[1] &&
                                    vs[1] === vs[2] &&
                                    vs[2] === vs[3];
                        }
                    }
                    return false;
                };
                let c = true;
                if(typeof Object.values(obj)[0] === 'object') {
                    for(const k in obj) {
                        c = sameValue(obj[k]);
                    }
                }
                else {
                    c = sameValue(obj);
                }
                return !!c;
            },
            clickInput({value, el}) {
                if(!el) {
                    this.input = {};
                    return;
                }
                if(!this.input.show) {
                    this.$refs.input.acceptClose = false;
                    let rect = this.$el.getBoundingClientRect(),
                        rectEl = el.getBoundingClientRect();
                    this.input = {
                        value, show: true,
                        pos: {
                            top: rectEl.y - rect.y + el.offsetHeight + 10,
                            left: rectEl.x - rect.x
                        }
                    };
                }
                else {
                    this.current = {};
                    this.input = {};
                }

            },
            closeInput() {
                this.input = {};
            }
        }
    };
</script>
<style>
    .w-57 {
        width: 57%
    }
    .w-22 {
        width: 5.5rem;
    }
    .h-34 {
        height: 8.5rem;
    }
    .h-35 {
        height: 8.75rem;
    }
    .h-22 {
        height: 5.5rem;
    }
    .h-19 {
        height: 4.75rem;
    }
    .min-w-8{
        min-width: 2rem;
    }
</style>
