<template>
    <textarea
        v-if="!field.options || (['full', 'short', true].indexOf(field.options.toolbar) === -1)"
        v-model="value"
        :placeholder="field?.options?.placeholder ?? null"
        class="block w-full transition dark:text-dark-color dark:border-gray-600 dark:bg-dark rounded shadow-sm text-form-input border-form-border placeholder-form-input h-9 sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300"
        :style="field.options && field.options.height ? 'min-height:'+field.options.height+'px' : 'min-height: 100px'"
        @change="onchange"
    />
    <EditorComponent
        v-else
        :data="value"
        :field="field"
        :type="1"
        @update:data="$emit('update:data', $event)"
    />
</template>

<script lang="ts">
    import EditorComponent from '../../components/editor/Editor.vue';
    export default {
        name: 'EditorField',
        components: {
            EditorComponent
        },
        props: {
            data: {
                type: String,
                default: ''
            },
            field: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data() {
            return {
                value: ''
            };
        },
        watch: {
            value(val) {
                if (this.field?.options?.update != 'onchange' || this.field.options.toolbar) {
                    this.$emit('update:data', val);
                }
            },
            data(val) {
                this.value = val;
            }
        },
        mounted() {
            this.value = this.data != '__empty__' ? this.data : '';
        },
        methods: {
            onchange() {
                if (this.field?.options?.update == 'onchange') {
                    this.$emit('update:data', this.value);
                }
            }
        }
    };
</script>
