<template>
    <div
        class="relative flex flex-col items-center justify-center p-3 border-2 border-dashed rounded group bg-gray-50 dark:bg-gray-700 dark:text-dark-color"
        @drop="dragFile"
    >
        <ecom-icon
            v-if="!file"
            :icon="faFileUpload"
            class="w-8 h-8 text-gray-500 dark:text-dark-color  transition group-hover:text-gray-600 dark:group-hover:text-white"
        />
        <p
            v-else
            class="h-8 font-medium text-gray-500 dark:text-dark-color dark:group-hover:text-white"
        >
            {{ file.name }}
        </p>
        <p class="mt-1 text-sm text-gray-500 dark:text-dark-color transition group-hover:text-gray-600 dark:group-hover:text-white"><span class="text-primary-600">Upload a file </span>or drag and drop</p>
        <input
            type="file"
            class="absolute inset-0 w-full h-full opacity-0 cursor-pointer dark:bg-gray-700"
            :accept="field.options?.accept ?? null"
            @change="uploadFile"
        >
    </div>
</template>

<script lang="ts">
    import { faFileUpload } from '@fortawesome/pro-light-svg-icons';
    export default {
        name: 'UploadField',
        props: {
            field: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data() {
            return {
                file: null,
                faFileUpload
            };
        },
        watch: {
            file(val) {
                this.$emit('update:data', val);
            }
        },
        methods: {
            uploadFile(e) {
                this.addFile(e.target.files);
            },
            dragFile(e) {
                this.addFile(e.dataTransfer.files);
            },
            addFile(files) {
                if(files.length) {
                    this.file = files[0];
                }
                else {
                    this.file = null;
                }
            }
        }
    };
</script>
