<template>
    <div class="relative flex" :class="id">
        <button type="button"
            class="w-7 h-7 rounded border border-gray-100 dark:border-gray-600"
            :class="{ 'ecom-bg__none': isNone }"
            :style="preview"
            @click="open"
        />
        <transition
            enter-active-class="transition duration-200 ease-out"
            enter-from-class="transform scale-90 opacity-0"
            enter-to-class="transform scale-100 opacity-100"
            leave-active-class="transition duration-150 ease-in"
            leave-from-class="transform scale-100 opacity-100"
            leave-to-class="transform scale-90 opacity-0"
        >
            <div
                v-if="isopen"
                style="z-index:23"
                class="absolute pb-10 mt-3 top-full -left-4 ecom-fields__background"
            >
                <div class="text-white inline-block bg-white dark:bg-gray-700 dark:text-gray-700 rounded w-68 shadow-popup">
                    <svg
                        viewBox="0 0 213.3 213.3"
                        class="absolute top-0 w-4 h-4 mr-1 -mt-3 transform rotate-180 left-8 fill-current"
                    >
                        <polygon
                            class="shadow-xxl"
                            points="0,53.333 106.667,160 213.333,53.333"
                        />
                    </svg>
                    <ul
                        v-if="types && types.length > 1"
                        class="flex flex-wrap gap-2 px-4 pt-2 pb-0 border-b border-form-border-input dark:border-transparent"
                    >
                        <li
                            v-for="(item, index) in types"
                            :key="index"
                            class="flex-1 ecom-item-tab-bg-hover"
                        >
                            <button
                                type="button"
                                class="flex items-center justify-center w-full px-3 py-2 text-sm font-normal text-center capitalize align-middle transition rounded h-7-5 whitespace-nowrap"
                                :class="
                                    type === item ? 'text-form-title dark:text-primary-600' : 'text-form-secondary dark:text-dark-color'
                                "
                                @click="changeType(item)"
                            >
                                {{ item }}
                            </button>
                            <span
                                :class="
                                    type === item ? 'opacity-100 bg-primary-600' : 'bg-gray-400'
                                "
                                class="flex w-full rounded-t opacity-0"
                                style="height: 3px"
                            />
                        </li>
                    </ul>
                    <div v-else class="w-full mt-5" />
                    <Fields
                        v-if="type === 'classic'"
                        :key="refresh"
                        :fields="fields"
                        :deep="1"
                        :ispop="true"
                        :data="curValue"
                        class="p-3 mt-3 -mx-2"
                        @update:data="updateData"
                    />
                    <GradientComponent v-else :data="curValue" @update:data="updateData" />
                </div>
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
import { Popmenu } from "@/mixins";
import GradientComponent from "./Gradient.vue"
import { convertBg } from "@/components/Builder/Helpers/Css";
export default {
    name: "Background",
    components: {
        GradientComponent,
    },
    mixins: [ Popmenu ],
    provide: {
        teleport_id: ".ecom-fields__background"
    },
    props: {
        field: {
            type: Object,
            default() {
                return {};
            }
        },
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    emits: ["update:data"],
    data() {

        return {
            type: this.field.options?.types ? this.field.options?.types[0] : 'classic',
            values: this.$helpers.copy(this.data),
            types: this.field.options?.types ?? ["classic", "gradient"],
            refresh: 0,
            image: this.field.options?.image ?? true,
            classicColor: this.field.options?.classicColor ?? false,
            status: true,
            emit: false
        };
    },
    computed: {
        fields() {
            if (this.type === "classic") {
                return !this.classicColor
                    ? [
                          {
                              name: "background-color",
                              label: "Color",
                              type: "color",
                              options: {
                                  oneline: true,
                                  global: {
                                      type: "colors"
                                  }
                              }
                          },
                          this.image
                              ? {
                                    name: "background-image",
                                    label: "Image",
                                    type: "picker",
                                    options: {
                                        type: "image",
                                        multiple: false,
                                        output: "value",
                                        position: "fixed",
                                        formatValue: 'url("%value%")'
                                    }
                                }
                              : {},
                          {
                              type: 'size',
                              label: 'Image size',
                              name: 'image-size',
                              options: {
                                  reset: true,
                                  visible: {
                                    keep_data: true,
                                        condition: (data) => {
                                            return data["background-image"];
                                        }
                                  }
                              }
                          },
                          this.image
                              ? {
                                    name: "background-position",
                                    label: "Position",
                                    type: "popup",
                                    value: "center center",
                                    options: {
                                        default: false,
                                        values: {
                                            "center center": "Center center",
                                            "center right": "Center right",
                                            "center left": "Center left",
                                            "top left": "Top left",
                                            "top center": "Top center",
                                            "top right": "Top right",
                                            "bottom right": "Bottom right",
                                            "bottom left": "Bottom left",
                                            "bottom center": "Bottom center",
                                            custom: "Custom"
                                        },
                                        type: "dropdown",
                                        oneline: true,
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["background-image"];
                                            }
                                        }
                                    }
                                }
                              : {
                                    name: "background-position",
                                    label: "Position",
                                    type: "popup",
                                    value: "center center",
                                    options: {
                                        default: false,
                                        values: {
                                            "center center": "Center center",
                                            "center right": "Center right",
                                            "center left": "Center left",
                                            "top left": "Top left",
                                            "top center": "Top center",
                                            "top right": "Top right",
                                            "bottom right": "Bottom right",
                                            "bottom left": "Bottom left",
                                            "bottom center": "Bottom center",
                                            custom: "Custom"
                                        },
                                        type: "dropdown",
                                        oneline: true
                                    }
                                },
                          {
                              name: "background-position-x",
                              label: "X Position",
                              type: "number",
                              options: {
                                  units: {
                                      px: {
                                          min: -800,
                                          max: 800,
                                          step: 1
                                      },
                                      em: {
                                          min: -100,
                                          max: 100,
                                          step: 0.1
                                      },
                                      "%": {
                                          min: -100,
                                          max: 100,
                                          step: 1
                                      },
                                      vw: {
                                          min: -100,
                                          max: 100,
                                          step: 1
                                      }
                                  },
                                  visible: {
                                      keep_data: true,
                                      condition: (data) => {
                                          return data["background-position"] === "custom";
                                      }
                                  }
                              }
                          },
                          {
                              name: "background-position-y",
                              label: "Y Position",
                              type: "number",
                              options: {
                                  units: {
                                      px: {
                                          min: -800,
                                          max: 800,
                                          step: 1
                                      },
                                      em: {
                                          min: -100,
                                          max: 100,
                                          step: 0.1
                                      },
                                      "%": {
                                          min: -100,
                                          max: 100,
                                          step: 1
                                      },
                                      vw: {
                                          min: -100,
                                          max: 100,
                                          step: 1
                                      }
                                  },
                                  visible: {
                                      keep_data: true,
                                      condition: (data) => {
                                          return data["background-position"] === "custom";
                                      }
                                  }
                              }
                          },
                          this.image
                              ? {
                                    name: "background-attachment",
                                    label: "Attachment",
                                    type: "popup",
                                    options: {
                                        default: false,
                                        values: {
                                            fixed: "Fixed",
                                            scroll: "Scroll",
                                            local: "Local",
                                            inherit: "Inherit"
                                        },
                                        type: "dropdown",
                                        oneline: true,
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["background-image"];
                                            }
                                        }
                                    }
                                }
                              : {
                                    name: "background-attachment",
                                    label: "Attachment",
                                    type: "popup",
                                    options: {
                                        default: false,
                                        values: {
                                            fixed: "Fixed",
                                            scroll: "Scroll",
                                            local: "Local",
                                            inherit: "Inherit"
                                        },
                                        type: "dropdown",
                                        oneline: true
                                    }
                                },
                          this.image
                              ? {
                                    name: "background-repeat",
                                    label: "Repeat",
                                    type: "popup",
                                    options: {
                                        default: false,
                                        values: {
                                            "no-repeat": "No-repeat",
                                            repeat: "Repeat",
                                            "repeat-x": "Horizontally",
                                            "repeat-y": "Verically"
                                        },
                                        type: "dropdown",
                                        oneline: true,
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["background-image"];
                                            }
                                        }
                                    }
                                }
                              : {
                                    name: "background-repeat",
                                    label: "Repeat",
                                    type: "popup",
                                    options: {
                                        default: false,
                                        type: "dropdown",
                                        values: {
                                            "no-repeat": "No-repeat",
                                            repeat: "Repeat",
                                            "repeat-x": "Horizontally",
                                            "repeat-y": "Verically"
                                        },
                                        oneline: true
                                    }
                                },
                          this.image
                              ? {
                                    name: "background-size",
                                    label: "Size",
                                    type: "popup",
                                    options: {
                                        type: "dropdown",
                                        default: false,
                                        values: {
                                            cover: "Cover",
                                            auto: "Auto",
                                            contain: "Contain",
                                            inherit: "Inherit"
                                        },
                                        oneline: true,
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["background-image"];
                                            }
                                        }
                                    }
                                }
                              : {
                                    name: "background-size",
                                    label: "Size",
                                    type: "popup",
                                    options: {
                                        type: "dropdown",
                                        default: false,
                                        values: {
                                            cover: "Cover",
                                            auto: "Auto",
                                            contain: "Contain",
                                            inherit: "Inherit"
                                        },
                                        oneline: true
                                    }
                                }
                      ]
                    : [
                          {
                              name: "background-color",
                              label: "Color",
                              type: "color",
                              options: {
                                  oneline: true,
                                  global: {
                                      type: "colors"
                                  }
                              }
                          }
                      ];
            } else if (this.type === "gradient") {
                return [
                    {
                        name: "first-color",
                        label: "Color",
                        type: "color",
                        options: {
                            oneline: true,
                            global: {
                                type: "colors"
                            }
                        }
                    },
                    {
                        name: "first-location",
                        label: "Location",
                        type: "number",
                        value: 0,
                        options: {
                            min: 0,
                            max: 100
                        }
                    },
                    {
                        name: "second-color",
                        label: "Second Color",
                        type: "color",
                        options: {
                            oneline: true,
                            global: {
                                type: "colors"
                            }
                        }
                    },
                    {
                        name: "second-location",
                        label: "Location",
                        type: "number",
                        value: 100,
                        options: {
                            min: 0,
                            max: 100
                        }
                    },
                    {
                        name: "type",
                        label: "Type",
                        type: "popup",
                        value: "linear",
                        options: {
                            type: "dropdown",
                            default: false,
                            values: {
                                linear: "Linear",
                                radial: "Radial"
                            },
                            oneline: true
                        }
                    },
                    {
                        name: "angle",
                        label: "Angle",
                        type: "number",
                        value: 180,
                        options: {
                            min: 0,
                            max: 360,
                            visible: {
                                keep_data: true,
                                condition: (data) => {
                                    return data["type"] == "linear";
                                }
                            }
                        }
                    },
                    {
                        name: "position",
                        label: "Position",
                        type: "popup",
                        value: "center center",
                        options: {
                            type: "dropdown",
                            default: false,
                            values: {
                                "center center": "Center center",
                                "center right": "Center right",
                                "center left": "Center left",
                                "top left": "Top left",
                                "top center": "Top center",
                                "top right": "Top right",
                                "bottom right": "Bottom right",
                                "bottom left": "Bottom left",
                                "bottom center": "Bottom center"
                            },
                            oneline: true,
                            visible: {
                                keep_data: true,
                                condition: (data) => {
                                    return data["type"] == "radial";
                                }
                            }
                        }
                    }
                ];
            } else {
                return [];
            }
        },
        isNone() {
            return this.preview.length === 0 || !Object.keys(this.data).length ? true : false;
        },
        curValue() {

            let tmp =  this.values?.[this.type] ?? {};
            if(!tmp || tmp.constructor  === Array)
            {
                return {};
            }
            return tmp;
        },
        preview() {
            const bg = Object.keys(this.curValue).length
                ? convertBg({
                      [this.type]: this.curValue
                  })
                : "";
            return (bg && bg?.length) ? [bg[0].replace('background-color', 'background')] : "";
        }
    },

    watch: {
        data: {
            deep: true,
            immediate: true,
            handler: function (val) {
                if (this.status) {
                    this.emit = false;
                    this.values = this.$helpers.copy(val ?? {});
                    if (val && !this.type) {
                        this.changeType("gradient" in val ? "gradient" : "classic");
                    }
                }
                this.status = true;
            }
        },
        "values.classic": {
            deep: true,
            handler: function (val) {
                if (val?.["background-image"] && this.type == "classic") {
                    if (!val["background-size"]) {
                        this.values.classic["background-size"] = "cover";
                    }
                    if (!val["background-repeat"]) {
                        this.values.classic["background-repeat"] = "no-repeat";
                    }
                    if (!val["background-position"]) {
                        this.values.classic["background-position"] = "center center";
                    }
                    if (!val["background-attachment"]) {
                        this.values.classic["background-attachment"] = "scroll";
                    }
                }
            }
        },
        values: {
            deep: true,
            immediate: true,
            handler: function (val) {
                if (this.emit) {
                    this.status = false;
                    if (this.type.length > 0) {
                        let dt = this.$helpers.copy({
                                [this.type]: val[this.type]
                            });
                        if(this.field?.options?.toNative)
                        {
                            dt.native =  typeof this.preview === 'object' ? this.$helpers.copy(this.preview) : this.preview;
                        }
                        this.$emit(
                            "update:data",
                            dt
                        );
                    } else {
                        this.$emit("update:data", {});
                    }
                }
                this.emit = true;
            }
        },
        fields: {
            deep: true,
            handler: function () {
                this.refresh = this.$helpers.makeId(8);
            }
        },
        type(val) {
            if (val.length > 0) {
                this.$emit(
                    "update:data",
                    this.$helpers.copy({
                        [val]: this.values[val]
                    })
                );
            } else {
                this.$emit("update:data", {});
            }
            this.status = false;
        }
    },
    created () {
        if(this?.values?.gradient && !this?.values?.classic) {
            this.type = 'gradient';
        }
    },
    methods: {
        backToDefault() {
            this.values = {};
        },
        changeType(type) {
            this.type = type;
            if (this.values[type] === undefined) {
                this.values[type] = {};
            }
        },
        updateData(val) {
            if (val) {
                if (this.type) {
                    this.values[this.type] = val;
                }
            }
        }
    }

};
</script>

<style>
.ecom-bg__none {
    background-position: 0 0, 0.375rem 0.375rem;
    background-size: 0.75rem 0.75rem;
    background-image: linear-gradient(
            45deg,
            #dfe3e8 25%,
            transparent 0,
            transparent 75%,
            #dfe3e8 0,
            #dfe3e8
        ),
        linear-gradient(45deg, #dfe3e8 25%, #fff 0, #fff 75%, #dfe3e8 0, #dfe3e8);
}
.ecom-item-tab-bg-hover:hover span {
    opacity: 1;
}
.ecom-fields__background div.ecom-picker__wrp {
    background: #fff;
    padding: 0px;
    border-radius: 05px;
}
</style>
