<template>
    <div class="mt-2">
        <div
            v-if="!columns || columns.length === 0"
            class="mx-auto text-sm text-center text-gray-400"
        >
            Empty
        </div>
        <div
            v-for="(str, i) in structure"
            :key="i"
            class="inline-flex mb-3 cursor-pointer group"
            :class="i % 2 === 0 ? 'mr-3' : ' mr-0'"
            style="min-width: 47.5%"
            @click="setStructure(str)"
        >
            <div
                v-for="(st, j) in str"
                :key="j"
                class="inline-flex items-center justify-center float-left py-3 mr-px transition-all text-tiny group-hover:bg-gray-400 group-hover:text-white"
                :class="isActive(str) ? 'bg-gray-400 text-white' : 'bg-gray-200 text-gray-400'"
                :style="'width:' + st + '%'"
            >
                <span>{{ st.toFixed(1).replace(".0", "") }}</span>
            </div>
        </div>
        <div
            v-if="columns.length < 10"
            class="inline-flex mb-3 text-sm cursor-pointer group"
            style="min-width: 47.5%"
        >
            <div
                class="relative inline-flex items-center justify-center w-full py-3 text-gray-500 dark:text-dark-color transition-all bg-gray-200 text-xss -bottom-1 group-hover:bg-gray-400 group-hover:text-white gap-1"
                @click="insertCol"
            >
                <ecom-icon :icon="icons.faPlusCircle" class="h-4 w-4 my-0.5" /> Add column
            </div>
        </div>
        <div v-if="hasChanged" class="mt-2">
            <Button type="secondary" class="flex items-center text-sm text-gray-500 dark:text-dark-color" @click="reset">
                <ecom-icon :icon="icons.faArrowsRotate" class="w-4 h-4 p-px mr-1" />
                <span>Reset structure</span>
            </Button>
        </div>
    </div>
</template>

<script lang="ts">
import { faArrowsRotate, faPlusCircle} from '@fortawesome/pro-light-svg-icons';
import { mapGetters } from "vuex";

export default {
    name: "Structure",
    props: {
        field: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            curStr: "",
            icons: { faArrowsRotate, faPlusCircle}
        };
    },
    computed: {
        columns() {
            return this.field.structure;
        },
        structures() {
            return {
                "2": [
                    [50, 50],
                    [30, 70],
                    [70, 30]
                ],
                "3": [
                    [33.33, 33.33, 33.33],
                    [20, 60, 20],
                    [30, 40, 30],
                    [20, 20, 60],
                    [60, 20, 20],
                    [40, 20, 40],
                    [40, 40, 20],
                    [20, 40, 40]
                ],
                "4": [
                    [25, 25, 25, 25],
                    [20, 30, 30, 20],
                    [30, 20, 20, 30],
                    [40, 20, 20, 20],
                    [20, 20, 20, 40],
                    [15, 15, 15, 55],
                    [55, 15, 15, 15]
                ],
                "5": [
                    [20, 20, 20, 20, 20],
                    [30, 17.5, 17.5, 17.5, 17.5],
                    [17.5, 17.5, 17.5, 17.5, 30]
                ]
            };
        },
        structure() {
            let colstr = this.columns.length.toString(),
                strs = this.structures[colstr];
            if (!strs) {
                let str = [];
                this.columns.forEach(() => {
                    str.push(100 / this.columns.length);
                });
                strs = [str];
            }
            return strs;
        },
        currentStructure() {
            let re = [];
            this.columns.forEach((col) => {
                re.push(col.width);
            });
            return re;
        },
        hasChanged() {
            return this.curStr != JSON.stringify(this.currentStructure);
        },
        ...mapGetters("builder", ["editingElement"])
    },
    mounted() {
        this.curStr = JSON.stringify(this.currentStructure);
    },
    methods: {
        isActive(s) {
            return (
                s.length === this.columns.length &&
                s.filter((_s, i) => {
                    return parseInt(_s) != parseInt(this.currentStructure[i]);
                }).length === 0
            );
        },
        setStructure(str) {
            str.forEach((st, i) => {
                this.columns[i].width = st;
            });
            if (typeof Event === "function") {
                setTimeout(() => {
                    window.EComposerBuilder.contentWindow.dispatchEvent(new Event("resize"));
                }, 1);
            }
        },
        insertCol() {
            if (typeof this.editingElement?.insertCol == "function") {
                this.editingElement.insertCol();
            } else if (typeof this.editingElement?.$refs?.row?.insertCol == "function") {
                this.editingElement.$refs.row.insertCol();
            }
        },
        reset() {
            this.setStructure(JSON.parse(this.curStr));
        }
    }
};
</script>
