<template>
    <div
        :class="{'pt-4 mt-4 border-t border-form-border' : type !== 'time'}"
    >

        <div class="flex items-center -mx-2">
            <div class="flex-1 px-2">
                <p class="mb-2 text-sm text-form-input dark:text-dark-color"> Time </p>
                <div class="flex-1">
                    <Dropdown
                        :cl="'w-full'"
                        :data="time"
                        :field="{options: {values: times, default: false}}"
                        @update:data="updateTime"
                    />
                </div>
            </div>
            <div class="flex-1 px-2">
                <p class="mb-2 text-sm text-form-input dark:text-dark-color"> GMT </p>
                <Dropdown
                    :data="_data.gmt ?? '+07.00'"
                    :field="{options: {values: gmts, default: false}}"
                    @update:data="_data.gmt = $event"
                />
            </div>
        </div>
    </div>
</template>

<script lang="ts">
    import Dropdown from '../../dropdown/Dropdown.vue';
    import { EComDate } from '../../../utils';
    import mixin from '../mixin';
    export default {
        name: 'ContentTime',
        components: {
            Dropdown
        },
        mixins: [mixin],
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: {
                type: Object,
                default() {
                    return {};
                }
            },
            field: {
                type: Object,
                default() {
                    return {};
                }
            },
            type: {
                type: String,
                default: 'datetime'
            }
        },
        emits: ['update:time'],
        computed: {
            _data() {
                return this.data;
            },
            time() {
                if(this.data.hour !== undefined) {
                    let { hour, minute } = this.data;
                    const format = (num) => {
                        return num < 10 ? ('0' + num) : num;
                    };
                    return format(hour) + ':' + format(minute);
                }
                return '12:00';
            },
            times() {
                let times = EComDate.names.times;
                let { month, year, day } = this.data;
                let isTimeMin = year <= this.minDate.getFullYear() &&
                        month <= this.minDate.getMonth() + 1 &&
                        day <= this.minDate.getDate(),
                    isTimeMax = year >= this.maxDate.getFullYear() &&
                        month >= this.maxDate.getMonth() + 1 &&
                        day >= this.maxDate.getDate();
                let minHour = this.minDate.getHours(),
                    maxHour = this.maxDate.getHours(),
                    minMinute = this.maxDate.getMinutes(),
                    maxMinute = this.maxDate.getMinutes();

                times = times.map(time => {
                    let hour = parseInt(time.split(':')[0]),
                        minute = parseInt(time.split(':')[1])
                    return {
                        name: time,
                        title: time,
                        disabled: (isTimeMin && (minHour > hour || (minHour === hour && minMinute > minute))) ||
                                  (isTimeMax && (maxHour < hour || (maxHour === hour && maxMinute < minute)))
                    };
                });
                return times;
            },
            gmts() {
                return EComDate.names.gmts;
            }
        },
        methods: {
            now() {
                let now = new Date();
                return {
                    date: now.getDate(),
                    month: now.getMonth() + 1,
                    year: now.getFullYear()
                };
            },
            updateTime(val) {
                if(!val) {
                    return;
                }
                let r = /(\d{1,2}):(\d{1,2})/g;
                let m = r.exec(val);
                if(m) {
                    let [, hour, minute] = m;
                    this._data.hour = parseInt(hour);
                    this._data.minute = parseInt(minute);
                    this._data.day = this._data.day ?? this.now().date;
                    this._data.month = this._data.month ?? this.now().month;
                    this._data.year = this._data.year ?? this.now().year;
                }

            }
        }
    };
</script>
