<template>
    <div ref="vslider" :class="['color-slider', sliderClass]" @mousedown="onDragStart">
        <div
            class="
                ecom-picker-slider
                h-3
                w-3
                rounded-lg
                absolute
                pointer-events-none
                left-0.5
                mt-px
            "
            :style="pointStyle"
        />
    </div>
</template>

<script lang="ts">
import { getElSizePosition, getValInRange } from './utility';

export default {
    props: {
        step: {
            type: Number,
            default: 0
        },
        value: {
            type: Number,
            default: 0
        }
    },
    emits: ['change'],
    data() {
        return {
            val: getValInRange(this.value, 0, 100),
            elSizePostion: {}
        };
    },
    computed: {
        pointStyle() {
            let cls = {};
            let key = 'top';
            cls[key] = 'calc(' + (144 * this.val) / 100 + 'px)';
            return cls;
        },
        sliderClass() {
            return 'color-slider-horizontal';
        }
    },
    watch: {
        value(val) {
            this.val = getValInRange(val, 0, 100);
        }
    },
    methods: {
        resetOffset() {
            let $el = this.$refs.vslider;
            let { width, height, left, top } = getElSizePosition($el);
            this.elSizePostion = {
                width,
                height,
                left,
                top
            };
        },
        onDragStart(e) {
            this.isDragging = true;
            this.resetOffset();
            this.onDragging(e);
            this.bindGlobalEvent();
        },
        onDragging(e) {
            let { pageY } = e;
            let { height, top } = this.elSizePostion;
            top = pageY - top;
            let percent = (top / height) * 100;
            percent = percent < 0 ? 0 : percent > 100 ? 100 : parseFloat(percent.toFixed(4));
            percent = this.handleStep(percent);
            this.val = percent;
            this.$emit('change', this.val);
        },
        handleStep(percent) {
            let value = percent;
            let step = this.step;
            if (step) {
                value = Math.floor(percent / step) * step;
            }
            return value;
        },
        onDragEnd() {
            this.isDragging = false;
            this.unbindGlobalEvent();
        },
        change(e) {
        },
        bindGlobalEvent() {
            window.addEventListener('mousemove', this.onDragging);
            window.addEventListener('mouseup', this.onDragEnd);
            // window.addEventListener('contextmenu', this.onDragEnd)
        },
        unbindGlobalEvent() {
            window.removeEventListener('mousemove', this.onDragging);
            window.removeEventListener('mouseup', this.onDragEnd);
            // window.removeEventListener('contextmenu', this.onDragEnd)
        }
    }
};
</script>

<style>
.color-slider {
    position: relative;
}
.color-slider-slot {
    position: absolute;
    width: 20px;
}
.color-slider-pointer {
    position: absolute;
    top: 0;
    width: 12px;
    height: 12px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 12px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.4);
    cursor: pointer;
}
.color-slider-pointer:hover {
    box-shadow: 0 0 5px #3449d7;
}
.color-slider-horizontal {
    height: 100%;
}
.color-slider-horizontal .color-slider-slot {
    height: 12px;
    width: 100%;
}
.color-slider-horizontal .color-slider-pointer {
    left: 0;
    top: calc((12px - 12px) / 2);
    transform: translateX(-6px);
}
.ecom-picker-slider {
    left: 1px !important;
    box-shadow: inset 0 0 0 3px #fff, 1px 1px 6px #000;
}
</style>
